import { beregnBrukteUttaksdager, getAllePerioderMedUttaksinfoFraUttaksplan } from 'uttaksplan/utils/uttaksPlanStatus';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { isUttaksperiode, Periode, Uttaksperiode } from 'uttaksplan/types/Periode';
import { Forelder } from 'app/types/Forelder';
import { StnadskontoUttak } from 'uttaksplan/types/StnadskontoUttak';
import { Periodene } from 'app/steps/uttaksplan-info/utils/Periodene';

interface ForeldersBrukteDager {
    frTermin: StnadskontoUttak[];
    etterTermin: StnadskontoUttak[];
    alle: StnadskontoUttak[];
    dagerTotalt: number;
    dagerEgneKvoter: number;
    dagerAnnenForeldersKvote: number;
    dagerFellesperiode: number;
}

export interface BrukteDager {
    mor: ForeldersBrukteDager;
    farMedmor: ForeldersBrukteDager;
    alle: StnadskontoUttak[];
}

const isMorsPeriode = (periode: Uttaksperiode): boolean => {
    return periode.forelder === Forelder.mor;
};
const isFarsPeriode = (periode: Uttaksperiode): boolean => {
    return periode.forelder === Forelder.farMedmor;
};
const isFellesperiodeKvote = (uttak: StnadskontoUttak): boolean => uttak.konto === StnadskontoType.Fellesperiode;

const isMorsKvote = (uttak: StnadskontoUttak): boolean => {
    switch (uttak.konto) {
        case StnadskontoType.ForeldrepengerFrFdsel:
        case StnadskontoType.Mdrekvote:
            return true;
        default:
            return false;
    }
};

const isFarMedmorsKvote = (uttak: StnadskontoUttak): boolean => {
    return uttak.konto === StnadskontoType.Fedrekvote;
};

const summerBrukteUttaksdager = (uttak: StnadskontoUttak[]) => {
    return uttak.reduce((dager, u) => dager + u.dager, 0);
};

const getBrukteDagerForForelder = (
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    perioder: Uttaksperiode[],
    familiehendelsesdato: Date,
    forelder: Forelder
): ForeldersBrukteDager => {
    const perioderFrTermin = Periodene(perioder)
        .getPerioderFrFamiliehendelsesdato(familiehendelsesdato)
        .filter(isUttaksperiode);
    const perioderEtterTermin = Periodene(perioder)
        .getPerioderEtterFamiliehendelsesdato(familiehendelsesdato)
        .filter(isUttaksperiode);
    const frTermin = beregnBrukteUttaksdager(tilgjengeligeStnadskontoer, perioderFrTermin);
    const etterTermin = beregnBrukteUttaksdager(tilgjengeligeStnadskontoer, perioderEtterTermin);
    const alle = beregnBrukteUttaksdager(tilgjengeligeStnadskontoer, perioder);
    const dagerTotalt = summerBrukteUttaksdager(alle);

    const isMor = forelder === Forelder.mor;
    const dagerEgneKvoter = summerBrukteUttaksdager(alle.filter(isMor ? isMorsKvote : isFarMedmorsKvote));
    const dagerOverfrt = summerBrukteUttaksdager(alle.filter(isMor ? isFarMedmorsKvote : isMorsKvote));
    const dagerFellesperiode = summerBrukteUttaksdager(alle.filter(isFellesperiodeKvote));

    return {
        frTermin,
        etterTermin,
        alle,
        dagerTotalt,
        dagerEgneKvoter,
        dagerFellesperiode,
        dagerAnnenForeldersKvote: dagerOverfrt,
    };
};

export const getBrukteDager = (
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    perioder: Periode[],
    familiehendelsesdato: Date
): BrukteDager => {
    const perioderMedUttak = getAllePerioderMedUttaksinfoFraUttaksplan(perioder);
    return {
        mor: getBrukteDagerForForelder(
            tilgjengeligeStnadskontoer,
            perioderMedUttak.filter(isMorsPeriode),
            familiehendelsesdato,
            Forelder.mor
        ),
        farMedmor: getBrukteDagerForForelder(
            tilgjengeligeStnadskontoer,
            perioderMedUttak.filter(isFarsPeriode),
            familiehendelsesdato,
            Forelder.farMedmor
        ),
        alle: beregnBrukteUttaksdager(tilgjengeligeStnadskontoer, perioder),
    };
};
