import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { RegelTest, RegelTestresultat } from 'uttaksplan/validering/utils/types/regelTypes';
import { laTilPeriodeEtterFrsteStnadsdagPflgendeBarn } from './periodevalideringstester/forSenUttakVedPflgendeBarn';

export const harPerioderEtterFrsteStnadsdagNesteBarnDerToTette: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    if (grunnlag.minsterettUkerToTette === undefined || grunnlag.minsterettUkerToTette === 0) {
        return {
            passerer: true,
        };
    }
    const perioderEtterFrsteStnadsperiodeNyttBarn = grunnlag.perioder.filter((periode) =>
        laTilPeriodeEtterFrsteStnadsdagPflgendeBarn(periode, grunnlag.frsteUttaksdagNesteBarnsSak)
    );

    const tekstKey = grunnlag.skerErFarEllerMedmor
        ? 'uttaksplan.validering.info.harPerioderEtterFrsteStnadsdagNesteBarnDerToTette.far'
        : 'uttaksplan.validering.info.harPerioderEtterFrsteStnadsdagNesteBarnDerToTette.mor';
    return {
        passerer: perioderEtterFrsteStnadsperiodeNyttBarn.length === 0,
        info: {
            intlKey: tekstKey,
        },
    };
};
