import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { RegelTestresultat } from '../utils/types/regelTypes';
import { isUttaksperiode, Periode, Uttaksperiode } from 'uttaksplan/types/Periode';
import { nskerFlerbarnsdagerSkalBesvares } from 'uttaksplan/utils/uttaksskjema/nskerFlerbarnsdagerSkalBesvares';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { UttakRundtFdselrsak } from 'app/types/UttakRundtFdselrsak';

export const inneholderUttaksperiodeMedUbservartSpmOmFlerbarnsdagerTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    const uttaksperioder = grunnlag.perioder.filter((p: Periode) => isUttaksperiode(p)) as Uttaksperiode[];
    const erFlerbarnssknad = grunnlag.antallBarn > 1;
    const bareFarMedmorHarRett = grunnlag.skerErFarEllerMedmor && !grunnlag.erDeltUttak;
    const erDeltUttakINorge =
        grunnlag.erDeltUttak &&
        isAnnenForelderOppgitt(grunnlag.annenForelder) &&
        grunnlag.annenForelder.harRettPForeldrepengerINorge;
    const perioderMedUbesvartSpmOmFlerbarnsdager = uttaksperioder
        .filter((p) =>
            nskerFlerbarnsdagerSkalBesvares(
                p.type,
                erFlerbarnssknad,
                grunnlag.skerErFarEllerMedmor,
                grunnlag.familiehendelsesdato,
                p.tidsperiode,
                p.konto,
                bareFarMedmorHarRett,
                grunnlag.antallBarn,
                grunnlag.skerErAleneOmOmsorg,
                !!erDeltUttakINorge,
                p.erMorForSyk ? UttakRundtFdselrsak.morErForSyk : undefined
            )
        )
        .filter((p) => p.gradert === undefined);

    return {
        passerer: perioderMedUbesvartSpmOmFlerbarnsdager.length === 0,
        info: perioderMedUbesvartSpmOmFlerbarnsdager.map((periode) => ({
            intlKey: 'uttaksplan.validering.feil.inneholderUttaksperiodeMedUbservartSpmOmFlerbarnsdager',
            periodeId: periode.id,
        })),
    };
};
