import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { StnadskontoUttak } from 'uttaksplan/types/StnadskontoUttak';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { beregnGjenstendeUttaksdager } from 'uttaksplan/utils/uttaksPlanStatus';
import { Periode } from 'uttaksplan/types/Periode';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';

const erUttaksmengdeForFarMedmorForHy = (
    uttaksplan: Periode[],
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    farEllerMedmor: boolean
): boolean => {
    if (farEllerMedmor === true) {
        const kontoUttak = beregnGjenstendeUttaksdager(tilgjengeligeStnadskontoer, uttaksplan, false);
        return kontoUttak.some((konto: StnadskontoUttak) => konto.dager < 0);
    } else {
        return false;
    }
};

export const erUttaksmengdeForFarMedmorForHyTest: RegelTest = (grunnlag: Sknadsinfo): RegelTestresultat => {
    return {
        passerer:
            erUttaksmengdeForFarMedmorForHy(
                grunnlag.perioder,
                grunnlag.stnadskontoer,
                grunnlag.skerErFarEllerMedmor
            ) === false,
    };
};
