import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { isUttaksperiode, Periode } from 'uttaksplan/types/Periode';
import { getFloatFromString } from 'app/utils/numberUtils';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';

const uttaksplanGraderingStrreEnnSamtidigUttak = (uttaksplan: Periode[]) => {
    const result = uttaksplan
        .slice()
        .filter(
            (periode) => isUttaksperiode(periode) && periode.nskerSamtidigUttak === true && periode.gradert === true
        )
        .filter(
            (periode) =>
                isUttaksperiode(periode) &&
                getFloatFromString(periode.stillingsprosent)! + getFloatFromString(periode.samtidigUttakProsent)! >
                    100.0
        );

    return result.length > 0;
};

export const erUttaksplanGraderingStrreEnnSamtidigUttakTest: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    return { passerer: uttaksplanGraderingStrreEnnSamtidigUttak(grunnlag.perioder) === false };
};
