import { Locale } from '@navikt/fp-common';
import NavFrontendSpinner from 'nav-frontend-spinner';
import React, { useEffect } from 'react';
import { BrowserRouter } from 'react-router-dom';
import Api from './api/api';
import { sendErrorMessageToSentry } from './api/apiUtils';
import actionCreator from './context/action/actionCreator';
import { useForeldrepengesknadContext } from './context/hooks/useForeldrepengesknadContext';
import ForeldrepengesknadRoutes from './routes/ForeldrepengesknadRoutes';
import SknadRoutes from './routes/routes';
import mapSkerinfoDTOToSkerinfo from './utils/mapSkerinfoDTO';
import { shouldApplyStorage } from './utils/mellomlagringUtils';

interface Props {
    locale: Locale;
    onChangeLocale: any;
}

const renderSpinner = () => (
    <div style={{ textAlign: 'center', padding: '12rem 0' }}>
        <NavFrontendSpinner type="XXL" />
    </div>
);

const Foreldrepengesknad: React.FunctionComponent<Props> = ({ locale, onChangeLocale }) => {
    const { skerinfoData, skerinfoError } = Api.useSkerinfo();
    const { sakerData, sakerError } = Api.useGetSaker();
    const { storageData } = Api.useStoredAppState();
    const { dispatch, state } = useForeldrepengesknadContext();

    useEffect(() => {
        if (storageData) {
            if (shouldApplyStorage(storageData)) {
                dispatch(actionCreator.applyStoredState(storageData));
            }
        }
        if (skerinfoData) {
            dispatch(actionCreator.setSkerinfo(mapSkerinfoDTOToSkerinfo(skerinfoData)));
        }

        if (sakerData) {
            dispatch(actionCreator.setSaker(sakerData.foreldrepenger));
        }
    }, [dispatch, storageData, skerinfoData, sakerData]);

    useEffect(() => {
        if (skerinfoError) {
            sendErrorMessageToSentry(skerinfoError);
            throw new Error(
                `Vi klarte ikke  hente informasjon om deg. Prv igjen om noen minutter og hvis problemet vedvarer kontakt brukersttte.`
            );
        }
        if (sakerError) {
            sendErrorMessageToSentry(sakerError);
            throw new Error(
                `Vi klarte ikke  hente informasjon om sakene dine. Prv igjen om noen minutter og hvis problemet vedvarer kontakt brukersttte.`
            );
        }
    }, [skerinfoError, sakerError]);

    if (!state.skerinfo || !sakerData) {
        return renderSpinner();
    }

    return (
        <BrowserRouter>
            <ForeldrepengesknadRoutes
                fornavn={state.skerinfo.person.fornavn}
                locale={locale}
                onChangeLocale={onChangeLocale}
                currentRoute={storageData ? storageData.currentRoute : SknadRoutes.VELKOMMEN}
            />
        </BrowserRouter>
    );
};

export default Foreldrepengesknad;
