import { Sknadsinfo } from '../../utils/types/Sknadsinfo';
import { RegelTest, RegelTestresultat } from 'uttaksplan/validering/utils/types/regelTypes';
import { starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel } from 'app/utils/wlbUtils';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';
import { isUttaksperiode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';

export const periodeDeFrsteSeksUkeneHarUlovligUttakBFHR: RegelTest = (grunnlag: Sknadsinfo): RegelTestresultat => {
    if (
        !andreAugust2022ReglerGjelder(grunnlag.familiehendelsesdato) ||
        !grunnlag.skerErFarEllerMedmor ||
        grunnlag.morHarRett ||
        grunnlag.skersituasjon.situasjon !== 'fdsel'
    ) {
        return {
            passerer: true,
        };
    }

    const perioderDeFrsteSeksUkeneMedUlovligUttakBFHR = grunnlag.perioder.filter(
        (periode) =>
            isUttaksperiode(periode) &&
            starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel(
                periode.tidsperiode,
                grunnlag.familiehendelsesdato,
                grunnlag.termindato
            ) &&
            periode.konto === StnadskontoType.Foreldrepenger &&
            periode.erMorForSyk === false
    );

    return {
        passerer: perioderDeFrsteSeksUkeneMedUlovligUttakBFHR.length === 0,
        info: perioderDeFrsteSeksUkeneMedUlovligUttakBFHR.map((periode) => ({
            intlKey: 'uttaksplan.validering.feil.perioderDeFrsteSeksUkeneMedUlovligUttakBFHR',
            periodeId: periode.id,
        })),
    };
};
