import { RegelTest, RegelTestresultat } from 'uttaksplan/validering/utils/types/regelTypes';
import { isUttaksperiode } from 'uttaksplan/types/Periode';
import { Sknadsinfo } from 'uttaksplan/validering/utils/types/Sknadsinfo';

export const harUttaksperiodeStnadskonto: RegelTest = (grunnlag: Sknadsinfo): RegelTestresultat => {
    const uttaksperioderUtenStnadskonto = grunnlag.perioder.filter(
        (periode) => isUttaksperiode(periode) && periode.konto === undefined
    );
    return {
        passerer: uttaksperioderUtenStnadskonto.length === 0,
        info: uttaksperioderUtenStnadskonto.map((periode) => ({
            periodeId: periode.id,
        })),
    };
};
