import { TidsperiodeDate } from '@navikt/fp-common';
import AnnenForelder from 'app/context/types/AnnenForelder';
import { Tidsperioden } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Situasjon } from 'app/types/Situasjon';
import { convertYesOrNoOrUndefinedToBoolean } from 'app/utils/formUtils';
import { PeriodeUttakFormData } from 'uttaksplan/components/uttaks-forms/periode-uttak-form/periodeUttakFormConfig';
import { Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { getUttaksdatoer } from '../uttaksdatoerUtils';
import { aktivitetskravMorSkalBesvares } from './aktivitetskravMorSkalBesvares';
import erMorForForSykSkalBesvares from './erMorForSykSkalBesvares';
import { graderingSkalBesvares } from './graderingSkalBesvares';
import { graderingSkalBesvaresPgaWLBUttakRundtFdsel } from './graderingSkalBesvaresPgaWLBUttakRundtFdsel';
import samtidigUttakSkalBesvares from './samtidigUttakSkalBesvares';
import { nskerFlerbarnsdagerSkalBesvares } from './nskerFlerbarnsdagerSkalBesvares';
import uttakRundtFdselrsakSprsmlSkalBesvares from './uttakRundtFdselrsakSprsmlSkalBesvares';
import kontoSkalBesvares from './kontoSkalBesvarer';
import { StnadskontoUttak } from 'uttaksplan/types/StnadskontoUttak';
import hvemSkalTaUttakSkalBesvares from './hvemSkalTaUttakSkalBesvares';
import overfringsrsakSkalBesvares from './overfringsrsakSkalBesvares';
export interface UttakSkjemaregler {
    aktivitetskravMorSkalBesvares: () => boolean;
    erMorForSykSkalBesvares: () => boolean;
    uttakRundtFdselrsakSprsmlSkalBesvares: () => boolean;
    samtidigUttakSkalBesvares: () => boolean;
    kontoSkalBesvares: () => boolean;
    hvemSkalTaUttakSkalBesvares: () => boolean;
    overfringsrsakSkalBesvares: () => boolean;
    nskerFlerbarnsdagerSkalBesvares: () => boolean;
    graderingSkalBesvares: () => boolean;
    graderingSkalBesvaresPgaWLBUttakRundtFdsel: () => boolean;
}

export interface UttakSkjemaReglerProps {
    erFarEllerMedmor: boolean;
    erAleneOmOmsorg: boolean;
    annenForelder: AnnenForelder;
    situasjon: Situasjon;
    erFlerbarnssknad: boolean;
    erDeltUttakINorge: boolean;
    familiehendelsesdato: Date;
    periodetype: Periodetype;
    termindato: Date | undefined;
    morHarRett: boolean;
    stnadskontoer: StnadskontoUttak[];
    antallBarn: number;
}

export const getUttakSkjemaregler = (
    formValues: PeriodeUttakFormData,
    regelProps: UttakSkjemaReglerProps
): UttakSkjemaregler => {
    const {
        erFarEllerMedmor,
        erAleneOmOmsorg,
        annenForelder,
        situasjon,
        erFlerbarnssknad,
        erDeltUttakINorge,
        familiehendelsesdato,
        periodetype,
        termindato,
        stnadskontoer,
        morHarRett,
        antallBarn,
    } = regelProps;

    const { konto } = formValues;

    const uttaksdatoer = getUttaksdatoer(familiehendelsesdato, erFarEllerMedmor, termindato);
    const tidsperiode: TidsperiodeDate = { fom: formValues.fom!, tom: formValues.tom! };

    return {
        aktivitetskravMorSkalBesvares: () =>
            aktivitetskravMorSkalBesvares(
                convertYesOrNoOrUndefinedToBoolean(formValues.nskerFlerbarnsdager),
                convertYesOrNoOrUndefinedToBoolean(formValues.samtidigUttak),
                convertYesOrNoOrUndefinedToBoolean(formValues.erMorForSyk),
                periodetype,
                konto as StnadskontoType,
                !erFarEllerMedmor,
                erAleneOmOmsorg,
                annenForelder.kanIkkeOppgis,
                false, // TODO Midlertidig omsorg,
                tidsperiode,
                familiehendelsesdato,
                termindato,
                situasjon,
                stnadskontoer,
                !morHarRett
            ),
        erMorForSykSkalBesvares: (): boolean =>
            erMorForForSykSkalBesvares(
                periodetype,
                konto as StnadskontoType,
                tidsperiode,
                situasjon,
                erFarEllerMedmor,
                uttaksdatoer,
                erFlerbarnssknad,
                erAleneOmOmsorg,
                annenForelder.kanIkkeOppgis,
                convertYesOrNoOrUndefinedToBoolean(formValues.nskerFlerbarnsdager),
                false, // TODO Midlertidig omsorg,
                familiehendelsesdato,
                termindato,
                !morHarRett
            ),
        samtidigUttakSkalBesvares: (): boolean =>
            samtidigUttakSkalBesvares(
                periodetype,
                konto as StnadskontoType,
                Tidsperioden(tidsperiode).erInnenforFrsteSeksUker(familiehendelsesdato) && situasjon === 'fdsel',
                periodetype === Periodetype.Uttak && konto === StnadskontoType.ForeldrepengerFrFdsel,
                erAleneOmOmsorg,
                erDeltUttakINorge,
                false, // TODO Midlertidig omsorg,
                convertYesOrNoOrUndefinedToBoolean(formValues.erMorForSyk),
                convertYesOrNoOrUndefinedToBoolean(formValues.nskerFlerbarnsdager),
                tidsperiode,
                erFarEllerMedmor,
                annenForelder.kanIkkeOppgis,
                familiehendelsesdato,
                termindato,
                situasjon,
                !morHarRett
            ),
        kontoSkalBesvares: (): boolean =>
            kontoSkalBesvares(periodetype, tidsperiode, stnadskontoer, familiehendelsesdato, erFarEllerMedmor),
        nskerFlerbarnsdagerSkalBesvares: (): boolean => {
            return nskerFlerbarnsdagerSkalBesvares(
                periodetype,
                erFlerbarnssknad,
                erFarEllerMedmor,
                familiehendelsesdato,
                tidsperiode,
                konto as StnadskontoType,
                !morHarRett,
                antallBarn,
                erAleneOmOmsorg,
                erDeltUttakINorge,
                formValues.uttakRundtFdselrsak === '' ? undefined : formValues.uttakRundtFdselrsak
            );
        },
        hvemSkalTaUttakSkalBesvares: (): boolean =>
            hvemSkalTaUttakSkalBesvares(
                tidsperiode,
                erDeltUttakINorge,
                familiehendelsesdato,
                erFarEllerMedmor,
                situasjon
            ),
        graderingSkalBesvares: (): boolean => {
            return graderingSkalBesvares(
                periodetype,
                konto as StnadskontoType,
                familiehendelsesdato,
                erFarEllerMedmor,
                convertYesOrNoOrUndefinedToBoolean(formValues.erMorForSyk),
                tidsperiode
            );
        },
        graderingSkalBesvaresPgaWLBUttakRundtFdsel: (): boolean => {
            return graderingSkalBesvaresPgaWLBUttakRundtFdsel(
                tidsperiode,
                periodetype,
                konto as StnadskontoType,
                erFarEllerMedmor,
                familiehendelsesdato,
                termindato,
                situasjon
            );
        },
        overfringsrsakSkalBesvares: (): boolean => {
            return overfringsrsakSkalBesvares(
                periodetype,
                erFarEllerMedmor,
                konto as StnadskontoType,
                annenForelder
            );
        },
        uttakRundtFdselrsakSprsmlSkalBesvares: () => {
            return uttakRundtFdselrsakSprsmlSkalBesvares(
                periodetype,
                konto as StnadskontoType,
                tidsperiode,
                erFarEllerMedmor,
                erAleneOmOmsorg,
                annenForelder.kanIkkeOppgis,
                false, //TODO: midlertidig omsorg
                familiehendelsesdato,
                termindato,
                situasjon,
                !morHarRett
            );
        },
    };
};

export default getUttakSkjemaregler;
