import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import SknadRoutes from 'app/routes/routes';

const isEndringssknadRoute = (route: SknadRoutes): boolean => {
    switch (route) {
        case SknadRoutes.UTTAKSPLAN:
        case SknadRoutes.OPPSUMMERING:
        case SknadRoutes.SKNAD_SENDT:
            return true;
        default:
            return false;
    }
};

export const shouldApplyStorage = (storedState: ForeldrepengesknadContextState): boolean => {
    if (storedState.sknad && storedState.sknad.erEndringssknad) {
        if (!isEndringssknadRoute(storedState.currentRoute)) {
            return false;
        }
    }

    return storedState.version === 5;
};
