import { Block, intlUtils } from '@navikt/fp-common';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import { isValidTidsperiode } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Situasjon } from 'app/types/Situasjon';
import { ISOStringToDate } from 'app/utils/dateUtils';
import React, { FunctionComponent, useState } from 'react';
import { useIntl } from 'react-intl';
import TidsperiodeDisplay from 'uttaksplan/components/tidsperiode-display/TidsperiodeDisplay';
import UttakEndreTidsperiodeSprsml from 'uttaksplan/components/uttak-endre-tidsperiode-sprsml/UttakEndreTidsperiodeSprsml';
import { ForeldrepengerFrFdselUttaksperiode, Periode, Utsettelsesperiode } from 'uttaksplan/types/Periode';
import { SubmitListener } from '../submit-listener/SubmitListener';
import TidsperiodeForm from '../tidsperiode-form/TidsperiodeForm';
import {
    PeriodeFrFdselFormComponents,
    PeriodeFrFdselFormData,
    PeriodeFrFdselFormField,
} from './periodeFrFdselFormConfig';
import { periodeFrFdselFormQuestionsConfig } from './periodeFrFdselFormQuestionsConfig';
import { getPeriodeFrFdselFormInitialValues, mapPeriodeFrFdselFormToPeriode } from './periodeFrFdselFormUtils';

interface Props {
    periode: ForeldrepengerFrFdselUttaksperiode;
    handleUpdatePeriode: (periode: Periode, familiehendelsedato: Date) => void;
    familiehendelsesdato: Date;
    erFarEllerMedmor: boolean;
    morHarRett: boolean;
    situasjon: Situasjon;
    utsettelserIPlan: Utsettelsesperiode[];
}

const PeriodeFrFdselForm: FunctionComponent<Props> = ({
    periode,
    familiehendelsesdato,
    handleUpdatePeriode,
    erFarEllerMedmor,
    morHarRett,
    situasjon,
    utsettelserIPlan,
}) => {
    const { tidsperiode } = periode;
    const [tidsperiodeIsOpen, setTidsperiodeIsOpen] = useState(false);
    const intl = useIntl();

    const toggleVisTidsperiode = () => {
        setTidsperiodeIsOpen(!tidsperiodeIsOpen);
    };

    const handleCleanup = (
        values: PeriodeFrFdselFormData,
        _visibility: QuestionVisibility<PeriodeFrFdselFormField, undefined>
    ): PeriodeFrFdselFormData => {
        return values;
    };

    return (
        <PeriodeFrFdselFormComponents.FormikWrapper
            initialValues={getPeriodeFrFdselFormInitialValues(periode, familiehendelsesdato)}
            onSubmit={(values) =>
                handleUpdatePeriode(mapPeriodeFrFdselFormToPeriode(values, periode), familiehendelsesdato)
            }
            renderForm={({ setFieldValue, values }) => {
                const visibility = periodeFrFdselFormQuestionsConfig.getVisbility(values);

                return (
                    <>
                        <Block
                            visible={
                                !isValidTidsperiode(tidsperiode) && visibility.isVisible(PeriodeFrFdselFormField.fom)
                            }
                            padBottom="l"
                        >
                            <TidsperiodeForm
                                tidsperiode={tidsperiode}
                                familiehendelsesdato={familiehendelsesdato}
                                onBekreft={(values) => {
                                    setFieldValue(PeriodeFrFdselFormField.fom, ISOStringToDate(values.fom));
                                    setFieldValue(PeriodeFrFdselFormField.tom, ISOStringToDate(values.tom));
                                }}
                                ugyldigeTidsperioder={undefined}
                                utsettelserIPlan={utsettelserIPlan}
                                erFarEllerMedmor={erFarEllerMedmor}
                                morHarRett={morHarRett}
                                situasjon={situasjon}
                                erFarMedmorOgHarAleneomsorg={false}
                            />
                        </Block>
                        <PeriodeFrFdselFormComponents.Form includeButtons={false}>
                            <SubmitListener cleanup={() => handleCleanup(values, visibility)} />

                            <Block
                                visible={
                                    isValidTidsperiode(tidsperiode) &&
                                    visibility.isVisible(PeriodeFrFdselFormField.fom)
                                }
                                padBottom="l"
                            >
                                <TidsperiodeDisplay
                                    tidsperiode={tidsperiode}
                                    toggleVisTidsperiode={toggleVisTidsperiode}
                                />
                                <UttakEndreTidsperiodeSprsml
                                    periode={periode}
                                    familiehendelsesdato={familiehendelsesdato}
                                    ugyldigeTidsperioder={undefined}
                                    utsettelserIPlan={utsettelserIPlan}
                                    onBekreft={(values) => {
                                        toggleVisTidsperiode();
                                        setFieldValue(PeriodeFrFdselFormField.fom, values.fom);
                                        setFieldValue(PeriodeFrFdselFormField.tom, values.tom);
                                    }}
                                    changeTidsperiode={(values) => {
                                        setFieldValue(PeriodeFrFdselFormField.fom, values.fom);
                                        setFieldValue(PeriodeFrFdselFormField.tom, values.tom);
                                    }}
                                    tidsperiode={tidsperiode}
                                    onAvbryt={() => toggleVisTidsperiode()}
                                    visible={tidsperiodeIsOpen}
                                    erFarEllerMedmor={erFarEllerMedmor}
                                    morHarRett={morHarRett}
                                    situasjon={situasjon}
                                    erFarMedmorOgHarAleneomsorg={false}
                                />
                            </Block>

                            <Block
                                visible={visibility.isVisible(PeriodeFrFdselFormField.skalIkkeHaUttakFrTermin)}
                                padBottom="l"
                            >
                                <PeriodeFrFdselFormComponents.Checkbox
                                    name={PeriodeFrFdselFormField.skalIkkeHaUttakFrTermin}
                                    label={intlUtils(intl, 'uttaksplan.periodeFrFdselForm.skalIkkeHaUttakFrTermin')}
                                />
                            </Block>
                        </PeriodeFrFdselFormComponents.Form>
                    </>
                );
            }}
        />
    );
};

export default PeriodeFrFdselForm;
