import Barn, { BarnType, isUfdtBarn } from 'app/context/types/Barn';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { Forelder } from 'app/types/Forelder';
import { Situasjon } from 'app/types/Situasjon';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';
import dayjs from 'dayjs';
import { isUttaksperiode, Periode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';

export const getKanPeriodenRundtFdselJusteres = (
    periodeRundtFdsel: Periode,
    termindato: Date | undefined
): boolean => {
    return (
        termindato !== undefined &&
        isUttaksperiode(periodeRundtFdsel) &&
        dayjs(periodeRundtFdsel.tidsperiode.fom).isSame(Uttaksdagen(termindato).denneEllerNeste(), 'day') &&
        periodeRundtFdsel.forelder === Forelder.farMedmor &&
        periodeRundtFdsel.konto === StnadskontoType.Fedrekvote &&
        periodeRundtFdsel.nskerSamtidigUttak === true &&
        periodeRundtFdsel.nskerFlerbarnsdager !== true
    );
};

export const getKanJustereAutomatiskVedFdsel = (
    perioderMedUttakRundtFdsel: Periode[],
    termindato: Date | undefined,
    erFarEllerMedmor: boolean,
    barn: Barn
): boolean => {
    return (
        barn.type === BarnType.UFDT &&
        erFarEllerMedmor &&
        perioderMedUttakRundtFdsel.length === 1 &&
        getKanPeriodenRundtFdselJusteres(perioderMedUttakRundtFdsel[0], termindato)
    );
};

export const getVisAutomatiskJusteringForm = (
    erFarEllerMedmor: boolean,
    familiehendelsesdato: Date,
    situasjon: Situasjon,
    perioderMedUttakRundtFdsel: Periode[],
    barn: Barn,
    termindato: Date | undefined,
    bareFarHarRett: boolean
): boolean => {
    return (
        erFarEllerMedmor &&
        andreAugust2022ReglerGjelder(familiehendelsesdato) &&
        situasjon === 'fdsel' &&
        perioderMedUttakRundtFdsel.length !== 0 &&
        isUfdtBarn(barn) &&
        termindato !== undefined &&
        !bareFarHarRett
    );
};
