import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionConfig, Questions } from '@navikt/sif-common-question-config/lib';
import { getKanPeriodenRundtFdselJusteres } from 'uttaksplan/components/automatisk-justering-form/automatiskJusteringUtils';
import { Periode } from 'uttaksplan/types/Periode';
import { UttaksplanFormData, UttaksplanFormField } from './UttaksplanFormConfig';

export interface UttaksplanQuestionPayload extends UttaksplanFormData {
    termindato: Date | undefined;
    perioderMedUttakRundtFdsel: Periode[];
}
const includeAutomatiskJusteringSprsml = (
    perioderMedUttakRundtFdsel: Periode[],
    termindato: Date | undefined
): boolean => {
    if (perioderMedUttakRundtFdsel.length !== 1 || termindato === undefined) {
        return false;
    }

    return getKanPeriodenRundtFdselJusteres(perioderMedUttakRundtFdsel[0], termindato);
};

const UttaksplanFormConfig: QuestionConfig<UttaksplanQuestionPayload, UttaksplanFormField> = {
    [UttaksplanFormField.nskerAutomatiskJustering]: {
        isIncluded: ({ perioderMedUttakRundtFdsel, termindato }) =>
            includeAutomatiskJusteringSprsml(perioderMedUttakRundtFdsel, termindato),
        isAnswered: ({ nskerAutomatiskJustering }) => nskerAutomatiskJustering !== YesOrNo.UNANSWERED,
    },
};

const uttaksplanQuestionsConfig = Questions<UttaksplanQuestionPayload, UttaksplanFormField>(UttaksplanFormConfig);

export default uttaksplanQuestionsConfig;
