import { Perioden } from 'app/steps/uttaksplan-info/utils/Perioden';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { getFloatFromString } from 'app/utils/numberUtils';
import {
    AvslttPeriode,
    InfoPeriode,
    isAvslttPeriode,
    isInfoPeriode,
    isOppholdsperiode,
    isOverfringsperiode,
    isUttaksperiode,
    Oppholdsperiode,
    Overfringsperiode,
    Periode,
    Periodetype,
    UttakAnnenPartInfoPeriode,
    Uttaksperiode,
} from 'uttaksplan/types/Periode';
import { PeriodeInfoType } from 'uttaksplan/types/PeriodeInfoType';
import { StnadskontoUttak } from 'uttaksplan/types/StnadskontoUttak';
import { getStnadskontoFromOppholdsrsak } from './periodeUtils';

export const finnAntallDagerTrekke = (periode: Periode): number => {
    const dager = Perioden(periode).getAntallUttaksdager();
    if (isUttaksperiode(periode)) {
        const periodeErGradert = periode.stillingsprosent !== undefined;
        const periodeErSamtidigUttak = periode.samtidigUttakProsent !== undefined;

        if (periodeErGradert && periodeErSamtidigUttak) {
            const graderingsProsent = (100 - getFloatFromString(periode.stillingsprosent)!) / 100;

            return dager * graderingsProsent;
        } else if (periodeErSamtidigUttak) {
            return dager * (getFloatFromString(periode.samtidigUttakProsent)! / 100);
        } else if (periodeErGradert) {
            const graderingsProsent = (100 - getFloatFromString(periode.stillingsprosent)!) / 100;

            return dager * graderingsProsent;
        } else {
            return dager;
        }
    }
    return dager;
};

export const getAllePerioderMedUttaksinfoFraUttaksplan = (perioder: Periode[]): Uttaksperiode[] => {
    return [
        ...perioder.filter(isUttaksperiode),
        ...getUttakFraOppholdsperioder(perioder.filter(isOppholdsperiode)),
        ...getUttakFraOverfringsperioder(perioder.filter(isOverfringsperiode)),
        ...getUttakFraInfoperioder(perioder.filter(isInfoPeriode)),
        ...getUttakFraAvslttePerioder(perioder.filter(isAvslttPeriode)),
    ];
};

export const beregnGjenstendeUttaksdager = (
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    uttaksplan: Periode[],
    beregnDagerBrukt: boolean
): StnadskontoUttak[] => {
    const alleUttakIUttaksplan = getAllePerioderMedUttaksinfoFraUttaksplan(uttaksplan);
    return tilgjengeligeStnadskontoer.map((konto): StnadskontoUttak => {
        let antallDager = beregnDagerBrukt ? 0 : konto.dager;
        const uttaksplanPerioder = alleUttakIUttaksplan.filter((p) => p.konto === konto.konto);
        if (uttaksplanPerioder) {
            uttaksplanPerioder.forEach((p: Periode) => {
                if (p.type === Periodetype.Uttak || p.type === Periodetype.Overfring || isAvslttPeriode(p)) {
                    antallDager = beregnDagerBrukt
                        ? antallDager + finnAntallDagerTrekke(p)
                        : antallDager - finnAntallDagerTrekke(p);
                }
            });

            antallDager = beregnDagerBrukt ? Math.floor(antallDager) : Math.ceil(antallDager);
        }

        return {
            konto: konto.konto,
            dager: antallDager,
        };
    });
};

export const beregnBrukteUttaksdager = (
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[],
    uttaksplan: Periode[]
): StnadskontoUttak[] => {
    return beregnGjenstendeUttaksdager(tilgjengeligeStnadskontoer, uttaksplan, true);
};

const getUttakFraOppholdsperioder = (oppholdsperioder: Oppholdsperiode[]): Uttaksperiode[] => {
    if (oppholdsperioder.length === 0) {
        return [];
    }
    return oppholdsperioder.map(
        (opphold: Oppholdsperiode): Uttaksperiode => ({
            id: opphold.id,
            tidsperiode: opphold.tidsperiode,
            type: Periodetype.Uttak,
            konto: getStnadskontoFromOppholdsrsak(opphold.rsak)!,
            forelder: opphold.forelder,
        })
    );
};
const getUttakFraOverfringsperioder = (overfringer: Overfringsperiode[]): Uttaksperiode[] => {
    if (overfringer.length === 0) {
        return [];
    }
    return overfringer.map(
        (overfring): Uttaksperiode => ({
            id: overfring.id,
            tidsperiode: overfring.tidsperiode,
            type: Periodetype.Uttak,
            konto: overfring.konto,
            forelder: overfring.forelder,
        })
    );
};

const getUttakFraInfoperioder = (perioder: InfoPeriode[]): Uttaksperiode[] => {
    if (perioder.length === 0) {
        return [];
    }
    const oppholdAnnenPart: UttakAnnenPartInfoPeriode[] = [];
    perioder
        .filter((periode) => isAvslttPeriode(periode) === false)
        .forEach((periode) => {
            if (periode.infotype === PeriodeInfoType.uttakAnnenPart) {
                oppholdAnnenPart.push(periode);
            }
        });
    return oppholdAnnenPart.map((periode): Uttaksperiode => {
        const { type, rsak, ...rest } = periode;
        return {
            type: Periodetype.Uttak,
            konto: getStnadskontoFromOppholdsrsak(periode.rsak),
            ...rest,
        };
    });
};

const getUttakFraAvslttePerioder = (perioder: AvslttPeriode[]): Uttaksperiode[] => {
    if (perioder.length === 0) {
        return [];
    }

    return perioder
        .filter((p) => p.avslttPeriodeType === Periodetype.Uttak)
        .map(
            (periode): Uttaksperiode => ({
                type: Periodetype.Uttak,
                konto: periode.kontoType!,
                tidsperiode: periode.tidsperiode,
                id: periode.id,
                forelder: periode.forelder,
            })
        );
};
