import { TidsperiodeDate } from '@navikt/fp-common';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';
import { getSisteUttaksdag6UkerEtterFdsel } from 'app/utils/wlbUtils';
import dayjs from 'dayjs';
import { Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';

export const graderingSkalBesvares = (
    periodetype: Periodetype,
    konto: StnadskontoType,
    familiehendelsesdato: Date,
    erFarMedmor: boolean,
    morErForSyk: boolean | undefined,
    tidsperiode: TidsperiodeDate
): boolean => {
    const farMedmorSkerDeFrsteSeksUkeneFrWLBOgMorErIkkeSyk =
        !andreAugust2022ReglerGjelder(familiehendelsesdato) && erFarMedmor && morErForSyk === false;
    const sisteUttaksdagInnenSeksUkerEtterFdsel = getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato);
    const bareFarHarRettSkerAktivitetsKravKontoDeFrsteSeksUkeneOgMorErIkkeSyk =
        erFarMedmor &&
        konto === StnadskontoType.Foreldrepenger &&
        morErForSyk === false &&
        dayjs(tidsperiode.fom).isSameOrBefore(sisteUttaksdagInnenSeksUkerEtterFdsel, 'day');
    if (
        periodetype === Periodetype.Uttak &&
        konto !== StnadskontoType.ForeldrepengerFrFdsel &&
        !farMedmorSkerDeFrsteSeksUkeneFrWLBOgMorErIkkeSyk &&
        !bareFarHarRettSkerAktivitetsKravKontoDeFrsteSeksUkeneOgMorErIkkeSyk
    ) {
        return true;
    }
    return false;
};
