import { Block, intlUtils } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import actionCreator from 'app/context/action/actionCreator';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { UttaksplanFormComponents, UttaksplanFormField } from 'app/steps/uttaksplan/UttaksplanFormConfig';
import { mapUttaksplanFormValueToState } from 'app/steps/uttaksplan/UttaksplanFormUtils';
import dayjs from 'dayjs';
import AlertStripe from 'nav-frontend-alertstriper';
import React, { FunctionComponent } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { isOverfringsperiode, isUttaksperiode, Periode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';

interface Props {
    termindato: Date;
    perioderMedUttakRundtFdsel: Periode[];
    antallBarn: number;
    visibility: QuestionVisibility<UttaksplanFormField, undefined>;
}
const AutomatiskJusteringForm: FunctionComponent<Props> = ({
    termindato,
    perioderMedUttakRundtFdsel,
    antallBarn,
    visibility,
}) => {
    const intl = useIntl();
    const uttaksdagPEllerEtterTermin = Uttaksdagen(termindato).denneEllerNeste();
    const { dispatch, state } = useForeldrepengesknadContext();
    const svarteJaMenFlerePerioderInnen6Uker =
        state.brukerSvarteJaPAutoJustering && perioderMedUttakRundtFdsel.length > 1;
    const svarteJaMenStarterIkkeLengerPTermin =
        state.brukerSvarteJaPAutoJustering &&
        perioderMedUttakRundtFdsel.length === 1 &&
        !dayjs(perioderMedUttakRundtFdsel[0].tidsperiode.fom).isSame(uttaksdagPEllerEtterTermin, 'day');
    const svarteJaMenEndretPeriodenPTermin =
        state.brukerSvarteJaPAutoJustering &&
        perioderMedUttakRundtFdsel.length === 1 &&
        dayjs(perioderMedUttakRundtFdsel[0].tidsperiode.fom).isSame(uttaksdagPEllerEtterTermin, 'day') &&
        ((isUttaksperiode(perioderMedUttakRundtFdsel[0]) &&
            (perioderMedUttakRundtFdsel[0].konto !== StnadskontoType.Fedrekvote ||
                !perioderMedUttakRundtFdsel[0].nskerSamtidigUttak)) ||
            isOverfringsperiode(perioderMedUttakRundtFdsel[0]));
    const svarteJaMenEndretPeriodenTilnskerFlerbarnsdager =
        state.brukerSvarteJaPAutoJustering &&
        perioderMedUttakRundtFdsel.length === 1 &&
        dayjs(perioderMedUttakRundtFdsel[0].tidsperiode.fom).isSame(uttaksdagPEllerEtterTermin, 'day') &&
        isUttaksperiode(perioderMedUttakRundtFdsel[0]) &&
        perioderMedUttakRundtFdsel[0].konto === StnadskontoType.Fedrekvote &&
        perioderMedUttakRundtFdsel[0].nskerFlerbarnsdager === true;
    let infoTekstId = '';
    if (svarteJaMenFlerePerioderInnen6Uker) {
        infoTekstId = 'uttaksplan.automatiskJustering.info.hvisFlerePerioder';
    }
    if (svarteJaMenStarterIkkeLengerPTermin) {
        infoTekstId = 'uttaksplan.automatiskJustering.info.hvisIkkeLengerStarterPTermin';
    }

    if (svarteJaMenEndretPeriodenPTermin) {
        infoTekstId = 'uttaksplan.automatiskJustering.info.hvisEndretPeriodePTermin';
    }
    if (svarteJaMenEndretPeriodenTilnskerFlerbarnsdager) {
        infoTekstId = 'uttaksplan.automatiskJustering.info.hvisEndretPeriodeTilnskerFlerbarnsdager';
    }

    const handleOnChange = (value: string) => {
        const nskerJustertUttakVedFdsel = mapUttaksplanFormValueToState(value as YesOrNo);
        dispatch(actionCreator.setnskerJustertUttakVedFdsel(nskerJustertUttakVedFdsel));
        dispatch(actionCreator.setBrukerSvarteJaPAutoJustering(nskerJustertUttakVedFdsel));
    };

    return (
        <UttaksplanFormComponents.Form includeButtons={false}>
            <div style={{ paddingTop: '1rem', paddingBottom: '1rem' }}>
                {infoTekstId !== '' && (
                    <Block padBottom="l">
                        <AlertStripe type="info">
                            <FormattedMessage id={infoTekstId} />
                        </AlertStripe>
                    </Block>
                )}
                <Block visible={visibility.isVisible(UttaksplanFormField.nskerAutomatiskJustering)} padBottom="l">
                    <UttaksplanFormComponents.YesOrNoQuestion
                        name={UttaksplanFormField.nskerAutomatiskJustering}
                        legend={intlUtils(intl, 'uttaksplan.automatiskJustering.sprsml', {
                            antallBarn,
                        })}
                        validate={(value: YesOrNo) => {
                            if (value === YesOrNo.UNANSWERED) {
                                return intlUtils(intl, 'uttaksplan.automatiskJustering.svar.pkrevd');
                            }
                        }}
                        afterOnChange={(value) => handleOnChange(value)}
                    />
                </Block>
            </div>
        </UttaksplanFormComponents.Form>
    );
};

export default AutomatiskJusteringForm;
