import { Block } from '@navikt/fp-common';
import AnnenForelder from 'app/context/types/AnnenForelder';
import Arbeidsforhold from 'app/types/Arbeidsforhold';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { Undertittel } from 'nav-frontend-typografi';
import React, { Dispatch, FunctionComponent, SetStateAction, useState } from 'react';
import { Periode, Periodetype, Utsettelsesperiode } from 'uttaksplan/types/Periode';
import PeriodeUttakForm from '../periode-uttak-form/PeriodeUttakForm';
import PeriodeUtsettelseForm from '../periode-utsettelse-form/PeriodeUtsettelseForm';
import { FormattedMessage, IntlShape } from 'react-intl';
import { Situasjon } from 'app/types/Situasjon';

interface Props {
    familiehendelsesdato: Date;
    stnadskontoer: TilgjengeligStnadskonto[];
    navnPForeldre: NavnPForeldre;
    annenForelder: AnnenForelder;
    setNyPeriodeFormIsVisible: Dispatch<SetStateAction<boolean>>;
    arbeidsforhold: Arbeidsforhold[];
    isUtsettelse: boolean;
    handleAddPeriode: (nyPeriode: Periode, familiehendelsedato: Date) => void;
    erFarEllerMedmor: boolean;
    erFlerbarnssknad: boolean;
    erAleneOmOmsorg: boolean;
    erDeltUttak: boolean;
    situasjon: Situasjon;
    erMorUfr: boolean;
    setPeriodeErGyldig: Dispatch<SetStateAction<boolean>>;
    nesteLedigeUttaksdato: Date | undefined;
    skerErFarEllerMedmorOgKunDeHarRett: boolean;
    erEndringssknad: boolean;
    termindato: Date | undefined;
    antallBarn: number;
    utsettelserIPlan: Utsettelsesperiode[];
    intl: IntlShape;
}

const NyPeriode: FunctionComponent<Props> = ({
    setNyPeriodeFormIsVisible,
    isUtsettelse,
    annenForelder,
    arbeidsforhold,
    navnPForeldre,
    stnadskontoer,
    familiehendelsesdato,
    handleAddPeriode,
    erFarEllerMedmor,
    erFlerbarnssknad,
    erAleneOmOmsorg,
    erDeltUttak,
    situasjon,
    erMorUfr,
    setPeriodeErGyldig,
    nesteLedigeUttaksdato,
    skerErFarEllerMedmorOgKunDeHarRett,
    erEndringssknad,
    termindato,
    antallBarn,
    utsettelserIPlan,
    intl,
}) => {
    const [periode, setPeriode] = useState<Periode>({
        type: isUtsettelse ? Periodetype.Utsettelse : Periodetype.Uttak,
        tidsperiode: { fom: isUtsettelse ? undefined : nesteLedigeUttaksdato },
    } as Periode);

    return !isUtsettelse ? (
        <>
            <Block padBottom="l">
                <Undertittel>
                    <FormattedMessage id="uttaksplan.nyPeriode.tittel" />
                </Undertittel>
            </Block>
            <PeriodeUttakForm
                periode={periode}
                handleUpdatePeriode={setPeriode}
                handleAddPeriode={handleAddPeriode}
                annenForelder={annenForelder}
                arbeidsforhold={arbeidsforhold}
                familiehendelsesdato={familiehendelsesdato}
                navnPForeldre={navnPForeldre}
                stnadskontoer={stnadskontoer}
                setNyPeriodeFormIsVisible={setNyPeriodeFormIsVisible}
                isNyPeriode={true}
                erFarEllerMedmor={erFarEllerMedmor}
                erFlerbarnssknad={erFlerbarnssknad}
                erAleneOmOmsorg={erAleneOmOmsorg}
                erDeltUttak={erDeltUttak}
                situasjon={situasjon}
                erMorUfr={erMorUfr}
                setPeriodeErGyldig={setPeriodeErGyldig}
                erEndringssknad={erEndringssknad}
                termindato={termindato}
                morHarRett={!skerErFarEllerMedmorOgKunDeHarRett}
                antallBarn={antallBarn}
                utsettelserIPlan={utsettelserIPlan}
                intl={intl}
            />
        </>
    ) : (
        <PeriodeUtsettelseForm
            periode={periode}
            familiehendelsesdato={familiehendelsesdato}
            handleUpdatePeriode={setPeriode}
            erFarEllerMedmor={erFarEllerMedmor}
            erAleneOmOmsorg={erAleneOmOmsorg}
            handleAddPeriode={handleAddPeriode}
            setNyPeriodeFormIsVisible={setNyPeriodeFormIsVisible}
            isNyPeriode={true}
            navnPForeldre={navnPForeldre}
            erMorUfr={erMorUfr}
            skerErFarEllerMedmorOgKunDeHarRett={skerErFarEllerMedmorOgKunDeHarRett}
            arbeidsforhold={arbeidsforhold}
            situasjon={situasjon}
            utsettelserIPlan={utsettelserIPlan}
        />
    );
};

export default NyPeriode;
