import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { Periode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { StnadskontoUttak } from 'uttaksplan/types/StnadskontoUttak';
import { beregnGjenstendeUttaksdager } from './uttaksPlanStatus';

export interface Uttaksstatus {
    gjelderDagerBrukt: boolean;
    uttak: StnadskontoUttak[];
}

export const getUttaksstatus = ({
    erDeltUttak,
    erEndringssknad,
    harKomplettUttaksplan,
    erFarEllerMedmor,
    tilgjengeligeStnadskontoer,
    uttaksplan,
}: UttaksstatusFuncParams): Uttaksstatus => {
    const gjelderDagerBrukt =
        (erEndringssknad && harKomplettUttaksplan !== true) ||
        (erDeltUttak && erFarEllerMedmor && harKomplettUttaksplan !== true);

    const uttak: StnadskontoUttak[] = beregnGjenstendeUttaksdager(
        tilgjengeligeStnadskontoer,
        uttaksplan,
        gjelderDagerBrukt
    );
    return {
        gjelderDagerBrukt,
        uttak: erFarEllerMedmor
            ? uttak.filter((kontouttak) => kontouttak.konto !== StnadskontoType.ForeldrepengerFrFdsel)
            : uttak,
    };
};

export interface UttaksstatusFuncParams {
    erDeltUttak: boolean;
    erEndringssknad: boolean;
    harKomplettUttaksplan: boolean;
    erFarEllerMedmor: boolean;
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[];
    uttaksplan: Periode[];
}

export const getUttaksstatusFunc = ({
    erDeltUttak,
    erEndringssknad,
    harKomplettUttaksplan,
    erFarEllerMedmor,
    tilgjengeligeStnadskontoer,
    uttaksplan,
}: UttaksstatusFuncParams): (() => Uttaksstatus) => {
    return () =>
        getUttaksstatus({
            erDeltUttak,
            erEndringssknad,
            harKomplettUttaksplan,
            erFarEllerMedmor,
            tilgjengeligeStnadskontoer,
            uttaksplan,
        });
};
