import { intlUtils, Block, hasValue } from '@navikt/fp-common';
import VeilederNormal from 'app/assets/VeilederNormal';
import FormikFileUploader from 'app/components/formik-file-uploader/FormikFileUploader';
import { Attachment } from 'app/types/Attachment';
import { AttachmentType } from 'app/types/AttachmentType';
import { Skjemanummer } from 'app/types/Skjemanummer';
import Veilederpanel from 'nav-frontend-veilederpanel';
import React, { FunctionComponent } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { OverfringrsakType } from 'uttaksplan/types/OverfringrsakType';
import { getNavnGenitivEierform } from 'uttaksplan/utils/stnadskontoerUtils';
import { PeriodeUttakFormComponents, PeriodeUttakFormField } from '../../periode-uttak-form/periodeUttakFormConfig';
import { capitalizeFirstLetter } from 'app/utils/stringUtils';
interface Props {
    vedlegg: Attachment[];
    navnAnnenForelder: string;
    erEndringssknad: boolean;
    valgtOverfringsrsak: OverfringrsakType | '';
}

const OverfringsrsakSprsml: FunctionComponent<Props> = ({
    vedlegg,
    navnAnnenForelder,
    erEndringssknad,
    valgtOverfringsrsak,
}) => {
    const intl = useIntl();
    const navn = capitalizeFirstLetter(navnAnnenForelder);
    const radios = [
        {
            label: intlUtils(intl, 'uttaksplan.overfringsrsaktype.INSTITUSJONSOPPHOLD_ANNEN_FORELDER', {
                navnAnnenForelder: navn,
            }),
            value: OverfringrsakType.institusjonsoppholdAnnenForelder,
        },
        {
            label: intlUtils(intl, 'uttaksplan.overfringsrsaktype.SYKDOM_ANNEN_FORELDER', {
                navnAnnenForelder: navn,
            }),
            value: OverfringrsakType.sykdomAnnenForelder,
        },
    ];

    if (erEndringssknad) {
        radios.push({
            label: intlUtils(intl, 'uttaksplan.overfringsrsaktype.ALENEOMSORG'),
            value: OverfringrsakType.aleneomsorg,
        });
        radios.push({
            label: intlUtils(intl, 'uttaksplan.overfringsrsaktype.IKKE_RETT_ANNEN_FORELDER', {
                navnAnnenForelder: navn,
            }),
            value: OverfringrsakType.ikkeRettAnnenForelder,
        });
    }

    const beOmDokumentasjon =
        valgtOverfringsrsak !== '' &&
        valgtOverfringsrsak !== OverfringrsakType.aleneomsorg &&
        valgtOverfringsrsak !== OverfringrsakType.ikkeRettAnnenForelder;

    return (
        <>
            <Block padBottom="l">
                <PeriodeUttakFormComponents.RadioPanelGroup
                    name={PeriodeUttakFormField.overfringsrsak}
                    legend={intlUtils(intl, 'uttaksplan.overfringsrsak', {
                        navnAnnenForelder: getNavnGenitivEierform(navnAnnenForelder, intl.locale),
                    })}
                    radios={radios}
                    useTwoColumns={true}
                    validate={(value) => {
                        if (!hasValue(value)) {
                            return intlUtils(intl, 'uttaksplan.validering.overfringsrsak');
                        }
                    }}
                />
            </Block>
            {beOmDokumentasjon && (
                <Block padBottom="l">
                    <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                        <FormattedMessage
                            id="uttaksplan.overfringsrsak.informasjonVedSykdomAnnenForelder"
                            values={{ navnAnnenForelder }}
                        />
                    </Veilederpanel>
                </Block>
            )}
            {beOmDokumentasjon && (
                <Block padBottom="l">
                    <FormikFileUploader
                        label={intlUtils(intl, 'uttaksplan.overfringsrsak.dokumentasjon')}
                        name={PeriodeUttakFormField.overfringsdokumentasjon}
                        attachments={vedlegg || []}
                        attachmentType={AttachmentType.OVERFRING_KVOTE}
                        skjemanummer={Skjemanummer.DOK_OVERFRING_FOR_SYK}
                    />
                </Block>
            )}
        </>
    );
};

export default OverfringsrsakSprsml;
