import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';
import { formaterDatoKompakt } from 'app/utils/dateUtils';
import { Periode } from 'uttaksplan/types/Periode';
import {
    getFarMedmorUttakRundtFdsel,
    getFrsteUttaksdag2UkerFrFdsel,
    getSisteUttaksdag6UkerEtterFdsel,
    gjelderWLBReglerFarMedmorRundtFdsel,
    slutterTidsperiodeInnen6UkerEtterFdsel,
} from 'app/utils/wlbUtils';
import { getSumUttaksdagerTrekkeIPeriodene } from 'app/steps/uttaksplan-info/utils/Periodene';
import { ANTALL_UTTAKSDAGER_FAR_MEDMOR_RUNDT_FDSEL } from 'app/utils/wlbUtils';
import dayjs from 'dayjs';

const overskridelseUttakRundtFdselAntallDager = (
    perioderRundtFdsel: Periode[],
    familiehendelsesdato: Date
): number => {
    const sisteUttak6UkerEtterFdsel = getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato);
    const perioderAvkortetTilSlutte6UkerEtterFdsel = perioderRundtFdsel.map((p) =>
        dayjs(p.tidsperiode.tom).isAfter(dayjs(sisteUttak6UkerEtterFdsel), 'day')
            ? { ...p, tidsperiode: { fom: p.tidsperiode.fom, tom: sisteUttak6UkerEtterFdsel } }
            : p
    );
    const antallDager = getSumUttaksdagerTrekkeIPeriodene(perioderAvkortetTilSlutte6UkerEtterFdsel);
    return ANTALL_UTTAKSDAGER_FAR_MEDMOR_RUNDT_FDSEL - antallDager;
};

export const farMedmorHarSktUgyldigAntallDagerUttakRundtFdselTest: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    if (
        gjelderWLBReglerFarMedmorRundtFdsel(
            grunnlag.familiehendelsesdato,
            grunnlag.skerErFarEllerMedmor,
            grunnlag.morHarRett,
            grunnlag.skersituasjon.situasjon
        )
    ) {
        const perioderUttakRundtFdsel = getFarMedmorUttakRundtFdsel(
            grunnlag.perioder,
            grunnlag.familiehendelsesdato,
            grunnlag.termindato
        );
        const antallDagerForMye = overskridelseUttakRundtFdselAntallDager(
            perioderUttakRundtFdsel,
            grunnlag.familiehendelsesdato
        );

        const slutterEnAvPeriodeneForSent = perioderUttakRundtFdsel.some(
            (p) => !slutterTidsperiodeInnen6UkerEtterFdsel(p.tidsperiode, grunnlag.familiehendelsesdato)
        );
        let info;
        const fraDato = formaterDatoKompakt(
            getFrsteUttaksdag2UkerFrFdsel(grunnlag.familiehendelsesdato, grunnlag.termindato)
        );
        const tilDato = formaterDatoKompakt(getSisteUttaksdag6UkerEtterFdsel(grunnlag.familiehendelsesdato));
        if (slutterEnAvPeriodeneForSent) {
            info = {
                intlKey:
                    'uttaksplan.validering.feil.farMedmorHarSktUgyldigAntallDagerUttakRundtFdsel.periodeSlutterForSent',
                values: {
                    fraDato: fraDato,
                    tilDato: tilDato,
                },
            };
        } else {
            info = {
                intlKey: 'uttaksplan.validering.feil.farMedmorHarSktUgyldigAntallDagerUttakRundtFdsel',
                values: {
                    fraDato: fraDato,
                    tilDato: tilDato,
                    dagerForMye: Math.abs(antallDagerForMye),
                },
            };
        }
        return {
            passerer: antallDagerForMye >= 0,
            info: info,
        };
    }

    return {
        passerer: true,
    };
};
