import { formatDate } from '@navikt/fp-common';
import { isUfdtBarn } from 'app/context/types/Barn';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { getUgyldigUttakMor } from '../utils/uttakValideringUtils';

export const ferieEllerArbeidInnenforDeFrstetteUkeneEtterTermindatoTest: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    const skerErMor = !grunnlag.skerErFarEllerMedmor;
    if (skerErMor && isUfdtBarn(grunnlag.barn)) {
        const ugyldigePerioder = getUgyldigUttakMor(
            grunnlag.perioder,
            grunnlag.familiehendelsesdato,
            grunnlag.skersituasjon.situasjon,
            grunnlag.skerErFarEllerMedmor,
            grunnlag.termindato,
            grunnlag.erFlerbarnssknad,
            'mellomSyvOgtteUkerForMor'
        );

        const passerer = ugyldigePerioder.length === 0;
        return {
            passerer,
            info: ugyldigePerioder.map((periode) => ({
                intlKey: 'uttaksplan.validering.advarsel.ferieEllerArbeidInnenforDeFrstetteUkeneEtterTermindato',
                values: {
                    fraDato: formatDate(periode.tidsperiode.fom),
                    tilDato: formatDate(periode.tidsperiode.tom),
                },
                periodeId: periode.id,
            })),
        };
    }

    return { passerer: true };
};
