import { TidsperiodeDate } from '@navikt/fp-common';
import { Situasjon } from 'app/types/Situasjon';
import dayjs from 'dayjs';
import { Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { erInnenFrsteSeksUkerFdselFarMedmor, Uttaksdatoer } from '../uttaksdatoerUtils';
import uttakRundtFdselrsakSprsmlSkalBesvares from './uttakRundtFdselrsakSprsmlSkalBesvares';

const erMorForForSykSkalBesvares = (
    periodetype: Periodetype,
    konto: StnadskontoType,
    tidsperiode: TidsperiodeDate,
    situasjon: Situasjon,
    skerErFarEllerMedmor: boolean,
    uttaksdatoer: Uttaksdatoer,
    erFlerbarnssknad: boolean,
    erAleneOmOmsorg: boolean,
    annenForelderKanIkkeOppgis: boolean,
    nskerFlerbarnsdager: boolean | undefined,
    skerHarMidlertidigOmsorg: boolean,
    familiehendelsesdato: Date,
    termindato: Date | undefined,
    bareFarMedmorHarRett: boolean
): boolean => {
    const rsakTilUttakRundtFdselSkalBesvares = uttakRundtFdselrsakSprsmlSkalBesvares(
        periodetype,
        konto as StnadskontoType,
        tidsperiode,
        skerErFarEllerMedmor,
        erAleneOmOmsorg,
        annenForelderKanIkkeOppgis,
        skerHarMidlertidigOmsorg,
        familiehendelsesdato,
        termindato,
        situasjon,
        bareFarMedmorHarRett
    );

    if (
        erAleneOmOmsorg ||
        annenForelderKanIkkeOppgis ||
        skerHarMidlertidigOmsorg ||
        rsakTilUttakRundtFdselSkalBesvares ||
        dayjs(tidsperiode.fom).isBefore(familiehendelsesdato, 'day') ||
        konto === StnadskontoType.AktivitetsfriKvote
    ) {
        return false;
    }

    if (periodetype === Periodetype.Uttak && skerErFarEllerMedmor) {
        if (
            (konto === StnadskontoType.Fedrekvote || konto === StnadskontoType.Foreldrepenger) &&
            erInnenFrsteSeksUkerFdselFarMedmor(
                tidsperiode,
                situasjon,
                skerErFarEllerMedmor,
                uttaksdatoer.etterFdsel.frsteUttaksdagEtterSeksUker
            )
        ) {
            if (erFlerbarnssknad) {
                return nskerFlerbarnsdager !== undefined && nskerFlerbarnsdager === false;
            }

            return true;
        }
        return false;
    }

    return false;
};

export default erMorForForSykSkalBesvares;
