import uttaksConstants from 'app/constants';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import dayjs from 'dayjs';
import { ForeldrepengerFrFdselUttaksperiode, Periode, Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { PeriodeFrFdselFormData, PeriodeFrFdselFormField } from './periodeFrFdselFormConfig';

export const initialValues: PeriodeFrFdselFormData = {
    [PeriodeFrFdselFormField.fom]: undefined,
    [PeriodeFrFdselFormField.tom]: undefined,
    [PeriodeFrFdselFormField.skalIkkeHaUttakFrTermin]: false,
};

export const getPeriodeFrFdselFormInitialValues = (
    periode: ForeldrepengerFrFdselUttaksperiode,
    familiehendelsesdato: Date
): PeriodeFrFdselFormData => {
    const sisteUttaksagFrFdsel = Uttaksdagen(dayjs(familiehendelsesdato).toDate()).forrige();
    const frsteUttaksdag3UkerFrFdsel = Uttaksdagen(sisteUttaksagFrFdsel).trekkFra(
        uttaksConstants.ANTALL_UKER_FORELDREPENGER_FR_FDSEL * 5 - 1
    );
    const fom = periode.tidsperiode.fom !== undefined ? periode.tidsperiode.fom : frsteUttaksdag3UkerFrFdsel;
    const tom = periode.tidsperiode.tom !== undefined ? periode.tidsperiode.tom : sisteUttaksagFrFdsel;
    return {
        ...initialValues,
        fom,
        tom,
        skalIkkeHaUttakFrTermin: periode.skalIkkeHaUttakFrTermin,
    };
};

export const mapPeriodeFrFdselFormToPeriode = (
    values: Partial<PeriodeFrFdselFormData>,
    periode: ForeldrepengerFrFdselUttaksperiode
): Periode => {
    return {
        type: Periodetype.Uttak,
        id: periode.id,
        forelder: periode.forelder,
        konto: StnadskontoType.ForeldrepengerFrFdsel,
        tidsperiode: {
            fom: values.fom!,
            tom: values.tom!,
        },
        skalIkkeHaUttakFrTermin: !!values.skalIkkeHaUttakFrTermin,
    };
};
