import { Forelder } from 'app/types/Forelder';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { Situasjon } from 'app/types/Situasjon';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { getStnadskontoFarge } from 'uttaksplan/utils/styleUtils';
import { getStnadskontoNavn } from 'uttaksplan/utils/stnadskontoerUtils';
import IconBox from '../icon-box/IconBox';
import UttaksplanIkon, { UttaksplanIkonKeys } from '../uttaksplan-ikon/UttaksplanIkon';

export interface Props {
    konto: StnadskontoType;
    forelder?: Forelder;
    gradert?: boolean;
    navnPForeldre: NavnPForeldre;
    erFarEllerMedmor?: boolean;
    situasjon?: Situasjon;
    erAleneOmOmsorg?: boolean;
    harMidlertidigOmsorg?: boolean;
}

const StnadskontoIkon: FunctionComponent<Props> = ({
    konto,
    forelder,
    gradert,
    navnPForeldre,
    harMidlertidigOmsorg,
    erFarEllerMedmor,
    erAleneOmOmsorg,
}) => {
    const intl = useIntl();

    return (
        <IconBox color={getStnadskontoFarge(konto, forelder, true, harMidlertidigOmsorg)} stripes={gradert}>
            <UttaksplanIkon
                ikon={UttaksplanIkonKeys.uttak}
                title={getStnadskontoNavn(intl, konto, navnPForeldre, erFarEllerMedmor, erAleneOmOmsorg)}
            />
        </IconBox>
    );
};

export default StnadskontoIkon;
