import { hasValue, intlUtils } from '@navikt/fp-common';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { Situasjon } from 'app/types/Situasjon';
import { RadioPanelProps } from 'nav-frontend-skjema';
import React, { FunctionComponent } from 'react';
import { IntlShape, useIntl } from 'react-intl';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { getStnadskontoNavn } from 'uttaksplan/utils/stnadskontoerUtils';
import { PeriodeUttakFormComponents, PeriodeUttakFormField } from '../../periode-uttak-form/periodeUttakFormConfig';

interface Props {
    velgbareStnadskontoer: StnadskontoType[];
    erOppholdsperiode: boolean;
    navnPForeldre: NavnPForeldre;
    navnAnnenForelder?: string;
    fieldName: PeriodeUttakFormField;
    erFarEllerMedmor: boolean;
    situasjon: Situasjon;
    erAleneOmOmsorg: boolean;
}

const getSprsmlsTekst = (erOppholdsperiode: boolean, intl: IntlShape, navnAnnenForelder?: string): string => {
    if (erOppholdsperiode) {
        return intlUtils(intl, 'uttaksplan.hvilkenKvote.annenForelder', {
            navnAnnenForelder,
        });
    } else {
        return intlUtils(intl, 'uttaksplan.hvilkenKvote');
    }
};

const HvilkenKontoSprsml: FunctionComponent<Props> = ({
    velgbareStnadskontoer,
    erOppholdsperiode,
    navnAnnenForelder,
    navnPForeldre,
    fieldName,
    erFarEllerMedmor,
    erAleneOmOmsorg,
}) => {
    const intl = useIntl();
    const legend = getSprsmlsTekst(erOppholdsperiode, intl, navnAnnenForelder);

    const radios = velgbareStnadskontoer.map(
        (konto): RadioPanelProps => ({
            label: getStnadskontoNavn(intl, konto, navnPForeldre, erFarEllerMedmor, erAleneOmOmsorg),
            value: `${konto}`,
        })
    );

    return (
        <PeriodeUttakFormComponents.RadioPanelGroup
            name={fieldName}
            radios={radios}
            legend={legend}
            useTwoColumns={true}
            validate={(value) => {
                if (!hasValue(value)) {
                    return intlUtils(intl, 'uttaksplan.validering.hvilkenKonto');
                }
            }}
        />
    );
};

export default HvilkenKontoSprsml;
