import { bemUtils, Block, intlUtils, LanguageToggle, Locale, Sidebanner } from '@navikt/fp-common';
import actionCreator, { ForeldrepengesknadContextAction } from 'app/context/action/actionCreator';
import React, { useEffect, useState } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useSknad from 'app/utils/hooks/useSknad';
import {
    getInitialVelkommenValues,
    VelkommenFormComponents,
    VelkommenFormData,
    VelkommenFormField,
    velkommenFormQuestions,
} from './velkommenFormConfig';
import DinePlikter from 'app/components/dine-plikter/DinePlikter';
import { Hovedknapp } from 'nav-frontend-knapper';
import { Innholdstittel, Normaltekst } from 'nav-frontend-typografi';
import DinePersonopplysningerModal from '../modaler/DinePersonopplysningerModal';

import './velkommen.less';
import { validateHarForstttRettigheterOgPlikter } from './validation/velkommenValidation';
import SknadRoutes from 'app/routes/routes';
import { storeAppState } from 'app/utils/submitUtils';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import {
    mapSkerensEksisterendeSakFromDTO,
    opprettSknadFraEksisterendeSak,
    opprettSknadFraValgteBarn,
    opprettSknadFraValgteBarnMedSak,
} from 'app/utils/eksisterendeSakUtils';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';
import { Sknad } from 'app/context/types/Sknad';

import BarnVelger, { SelectableBarnOptions } from './components/barnVelger/BarnVelger';
import { getBarnFraNesteSak, getSelectableBarnOptions, sorterSelectableBarnEtterYngst } from './velkommenUtils';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import useSaveLoadedRoute from 'app/utils/hooks/useSaveLoadedRoute';
import { Sak } from 'app/types/Sak';

interface Props {
    fornavn: string;
    onChangeLocale: (locale: Locale) => void;
    locale: Locale;
    saker: Sak[];
    fnr: string;
}

const Velkommen: React.FunctionComponent<Props> = ({ fornavn, locale, saker, onChangeLocale }) => {
    const intl = useIntl();
    const sknad = useSknad();
    const { dispatch, state } = useForeldrepengesknadContext();
    const [isDinePersonopplysningerModalOpen, setDinePersonopplysningerModalOpen] = useState(false);
    const bem = bemUtils('velkommen');
    const skerinfo = useSkerinfo();
    const { registrerteBarn } = skerinfo;
    const selectableBarn = getSelectableBarnOptions(saker, registrerteBarn);
    const sortedSelectableBarn = selectableBarn.sort(sorterSelectableBarnEtterYngst);

    useSaveLoadedRoute(SknadRoutes.VELKOMMEN);

    useEffect(() => {
        if (state.sknad.sker.sprkkode !== locale) {
            dispatch(actionCreator.setSprkkode(locale));
        }
    }, [dispatch, locale, state.sknad.sker.sprkkode]);

    const onValidSubmitHandler = (values: Partial<VelkommenFormData>) => {
        const valgteBarn =
            values.valgteBarn === SelectableBarnOptions.SKNAD_GJELDER_NYTT_BARN
                ? undefined
                : selectableBarn.find((sb) => sb.id === values.valgteBarn);
        const vilSkeOmEndring = valgteBarn !== undefined && !!valgteBarn.kanSkeOmEndring;
        const valgtEksisterendeSak =
            vilSkeOmEndring && valgteBarn.sak !== undefined
                ? saker.find((sak) => sak.saksnummer === valgteBarn.sak?.saksnummer)
                : undefined;

        const actionsToDispatch: ForeldrepengesknadContextAction[] = [
            actionCreator.setVelkommen(values.harForstttRettigheterOgPlikter!),
            actionCreator.setErEndringssknad(vilSkeOmEndring),
        ];
        let barnFraNesteSak = undefined;
        if (valgteBarn !== undefined) {
            barnFraNesteSak = getBarnFraNesteSak(valgteBarn, sortedSelectableBarn);
            actionsToDispatch.push(actionCreator.setBarnFraNesteSak(barnFraNesteSak));
        }
        const frsteUttaksdagNesteBarnsSak =
            barnFraNesteSak !== undefined ? barnFraNesteSak.startdatoFrsteStnadsperiode : undefined;

        const endringssknad = vilSkeOmEndring && valgtEksisterendeSak;
        const nySknadPAlleredeSktBarn =
            valgteBarn !== undefined && valgteBarn.sak !== undefined && valgteBarn.kanSkeOmEndring === false;
        const nySknadPValgteRegistrerteBarn =
            !endringssknad && !nySknadPAlleredeSktBarn && valgteBarn !== undefined;

        if (endringssknad) {
            const eksisterendeSak = mapSkerensEksisterendeSakFromDTO(
                valgtEksisterendeSak,
                frsteUttaksdagNesteBarnsSak
            );

            const sknad = opprettSknadFraEksisterendeSak(
                state.skerinfo,
                eksisterendeSak!,
                intl,
                valgtEksisterendeSak.annenPart,
                valgteBarn.fnr
            ) as Sknad;

            actionsToDispatch.push(actionCreator.updateCurrentRoute(SknadRoutes.UTTAKSPLAN));
            actionsToDispatch.push(actionCreator.setSknad(sknad));
            actionsToDispatch.push(actionCreator.setEksisterendeSak(eksisterendeSak));
            actionsToDispatch.push(
                actionCreator.setBrukerSvarteJaPAutoJustering(eksisterendeSak?.grunnlag.nskerJustertUttakVedFdsel)
            );
            actionsToDispatch.push(actionCreator.setSknadGjelderEtNyttBarn(false));
        } else if (nySknadPAlleredeSktBarn) {
            const sknad = opprettSknadFraValgteBarnMedSak(valgteBarn, intl, skerinfo) as Sknad;
            actionsToDispatch.push(actionCreator.setSknad(sknad));
            actionsToDispatch.push(actionCreator.setSknadGjelderEtNyttBarn(false));
        } else if (nySknadPValgteRegistrerteBarn) {
            const sknad = opprettSknadFraValgteBarn(valgteBarn) as Sknad;
            actionsToDispatch.push(actionCreator.setSknad(sknad));
            actionsToDispatch.push(actionCreator.setSknadGjelderEtNyttBarn(false));
        } else {
            actionsToDispatch.push(actionCreator.setSknadGjelderEtNyttBarn(true));
        }

        return actionsToDispatch;
    };

    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        SknadRoutes.SKERSITUASJON,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );

    return (
        <VelkommenFormComponents.FormikWrapper
            initialValues={getInitialVelkommenValues(sknad.harGodkjentVilkr)}
            onSubmit={handleSubmit}
            renderForm={({ values, setFieldValue }) => {
                const visibility = velkommenFormQuestions.getVisbility({
                    ...values,
                    selectableBarn,
                });
                const valgtBarnId = values.valgteBarn;
                const valgtBarn =
                    valgtBarnId === SelectableBarnOptions.SKNAD_GJELDER_NYTT_BARN
                        ? undefined
                        : selectableBarn.find((barn) => barn.id === valgtBarnId);
                const knapptekst =
                    valgtBarn !== undefined && valgtBarn.kanSkeOmEndring === true
                        ? intlUtils(intl, 'velkommen.endreSknad')
                        : intlUtils(intl, 'velkommen.begynnMedSknad');
                return (
                    <VelkommenFormComponents.Form includeButtons={false}>
                        <LanguageToggle
                            locale={locale}
                            availableLocales={['nb', 'nn']}
                            toggle={(l: Locale) => onChangeLocale(l)}
                        />
                        <Sidebanner
                            dialog={{
                                title: intlUtils(intl, 'velkommen.bobletittel', { name: fornavn }),
                                text: (
                                    <>
                                        <Block padBottom="m">
                                            <FormattedMessage id={'velkommen.bobletekst'} />
                                        </Block>
                                    </>
                                ),
                            }}
                        />

                        <div className={bem.block}>
                            <Innholdstittel className={`${bem.element('tittel')} blokk-s`}>
                                {intlUtils(intl, 'velkommen.tittel')}
                            </Innholdstittel>
                            <Block padBottom="l" visible={visibility.isVisible(VelkommenFormField.valgteBarn)}>
                                <BarnVelger
                                    selectableBarn={sortedSelectableBarn}
                                    visibility={visibility}
                                    formValues={values}
                                    setFieldValue={setFieldValue}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(VelkommenFormField.harForstttRettigheterOgPlikter)}
                            >
                                <VelkommenFormComponents.ConfirmationCheckbox
                                    name={VelkommenFormField.harForstttRettigheterOgPlikter}
                                    label={intlUtils(intl, 'velkommen.samtykke')}
                                    validate={validateHarForstttRettigheterOgPlikter(intl)}
                                >
                                    <>
                                        <Block padBottom="l">
                                            <FormattedMessage id="velkommen.samtykkeIntro.del1" />
                                        </Block>
                                        <Block padBottom="m">
                                            <DinePlikter />
                                        </Block>
                                        <Block padBottom="l">
                                            <FormattedMessage id="velkommen.samtykkeIntro.del2" />
                                        </Block>
                                        <Block padBottom="l">
                                            <FormattedMessage id="velkommen.samtykkeIntro.del3" />
                                        </Block>
                                    </>
                                </VelkommenFormComponents.ConfirmationCheckbox>
                            </Block>
                            <Block padBottom="l">
                                <div style={{ textAlign: 'center' }}>
                                    <Hovedknapp disabled={isSubmitting} spinner={isSubmitting}>
                                        {knapptekst}
                                    </Hovedknapp>
                                </div>
                            </Block>
                            <Normaltekst className={bem.element('personopplysningerLink')}>
                                <a
                                    className="lenke"
                                    href="#"
                                    onClick={(e) => {
                                        e.preventDefault();
                                        setDinePersonopplysningerModalOpen(true);
                                    }}
                                >
                                    <FormattedMessage id="velkommen.lesMerOmPersonopplysninger" />
                                </a>
                            </Normaltekst>
                            <DinePersonopplysningerModal
                                isOpen={isDinePersonopplysningerModalOpen}
                                onRequestClose={() => setDinePersonopplysningerModalOpen(false)}
                            />
                        </div>
                    </VelkommenFormComponents.Form>
                );
            }}
        />
    );
};

export default Velkommen;
