import React, { Dispatch, FunctionComponent, SetStateAction, useState } from 'react';
import { bemUtils, Block, formatDate } from '@navikt/fp-common';
import PeriodelisteItem from './../periodeliste-item/PeriodelisteItem';
import { isInfoPeriode, Periode, Utsettelsesperiode } from 'uttaksplan/types/Periode';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';

import './periodeliste.less';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import AnnenForelder from 'app/context/types/AnnenForelder';
import Arbeidsforhold from 'app/types/Arbeidsforhold';
import { Situasjon } from 'app/types/Situasjon';
import { VeiledermeldingerPerPeriode } from 'uttaksplan/validering/veilederInfo/types';
import { getAnnenForelderSamtidigUttakPeriode } from 'uttaksplan/utils/periodeUtils';
import dayjs from 'dayjs';
import FamiliehendelsedatoDisplay from '../familiehendelsedato-display/FamiliehendelsedatoDisplay';
import Barn, { BarnFraNesteSak } from 'app/context/types/Barn';
import AlertStripe from 'nav-frontend-alertstriper';
import { FormattedMessage, IntlShape } from 'react-intl';
import { isValidTidsperiode } from 'app/steps/uttaksplan-info/utils/Tidsperioden';

interface Props {
    uttaksplan: Periode[];
    familiehendelsesdato: Date;
    handleUpdatePeriode: (periode: Periode, familiehendelsedato: Date) => void;
    stnadskontoer: TilgjengeligStnadskonto[];
    navnPForeldre: NavnPForeldre;
    annenForelder: AnnenForelder;
    arbeidsforhold: Arbeidsforhold[];
    handleDeletePeriode: (periodeId: string) => void;
    erFarEllerMedmor: boolean;
    erFlerbarnssknad: boolean;
    erAleneOmOmsorg: boolean;
    erDeltUttak: boolean;
    situasjon: Situasjon;
    meldingerPerPeriode: VeiledermeldingerPerPeriode;
    erMorUfr: boolean;
    skerErFarEllerMedmorOgKunDeHarRett: boolean;
    setPeriodeErGyldig: Dispatch<SetStateAction<boolean>>;
    erEndringssknad: boolean;
    termindato: Date | undefined;
    antallBarn: number;
    utsettelserIPlan: Utsettelsesperiode[];
    barn: Barn;
    barnFraNesteSak: BarnFraNesteSak | undefined;
    intl: IntlShape;
}

const getIndexOfFrstePeriodeEtterFdsel = (uttaksplan: Periode[], familiehendelsesdato: Date) => {
    return uttaksplan.findIndex(
        (p) => isValidTidsperiode(p.tidsperiode) && dayjs(p.tidsperiode.fom).isSameOrAfter(familiehendelsesdato)
    );
};

const getIndexOfSistePeriodeFrDato = (uttaksplan: Periode[], dato: Date | undefined) => {
    if (dato !== undefined) {
        return Math.max(0, uttaksplan.filter((p) => dayjs(p.tidsperiode.tom).isBefore(dato, 'day')).length);
    }
    return undefined;
};

const Periodeliste: FunctionComponent<Props> = ({
    uttaksplan,
    familiehendelsesdato,
    handleUpdatePeriode,
    stnadskontoer,
    navnPForeldre,
    annenForelder,
    arbeidsforhold,
    handleDeletePeriode,
    erFarEllerMedmor,
    erFlerbarnssknad,
    erAleneOmOmsorg,
    erDeltUttak,
    situasjon,
    meldingerPerPeriode,
    erMorUfr,
    skerErFarEllerMedmorOgKunDeHarRett,
    setPeriodeErGyldig,
    erEndringssknad,
    termindato,
    antallBarn,
    utsettelserIPlan,
    barn,
    barnFraNesteSak,
    intl,
}) => {
    const [openPeriodeId, setOpenPeriodeId] = useState<string>(null!);
    const bem = bemUtils('periodeliste');

    const toggleIsOpen = (id: string) => {
        if (openPeriodeId === id) {
            setOpenPeriodeId(null!);
        } else {
            setOpenPeriodeId(id);
        }
    };

    const indexOfFrstePeriodeEtterFdsel = getIndexOfFrstePeriodeEtterFdsel(uttaksplan, familiehendelsesdato);
    const erAllePerioderIPlanenFrFdsel = indexOfFrstePeriodeEtterFdsel === -1;
    const indexOfSistePeriodeFrNyStndasperiodeNyttBarn =
        barnFraNesteSak !== undefined
            ? getIndexOfSistePeriodeFrDato(uttaksplan, barnFraNesteSak.startdatoFrsteStnadsperiode)
            : undefined;
    return (
        <div className={bem.block}>
            {uttaksplan.map((p, index) => {
                return (
                    <>
                        {indexOfFrstePeriodeEtterFdsel === index ? (
                            <FamiliehendelsedatoDisplay barn={barn} familiehendelsedato={familiehendelsesdato} />
                        ) : null}
                        {barnFraNesteSak !== undefined &&
                        indexOfSistePeriodeFrNyStndasperiodeNyttBarn !== undefined &&
                        indexOfSistePeriodeFrNyStndasperiodeNyttBarn === index ? (
                            <Block padBottom="s">
                                <AlertStripe className="nyStnadsperiodeNesteSak" type="info">
                                    <FormattedMessage
                                        id="uttaksplan.periodeliste.info.nyStnadsperiodeNesteSak"
                                        values={{
                                            datoStnadsperiodeNyttBarn: formatDate(
                                                barnFraNesteSak.startdatoFrsteStnadsperiode
                                            ),
                                        }}
                                    />
                                </AlertStripe>
                            </Block>
                        ) : null}
                        <PeriodelisteItem
                            key={p.id}
                            egenPeriode={!isInfoPeriode(p)}
                            periode={p}
                            isOpen={openPeriodeId === p.id}
                            toggleIsOpen={toggleIsOpen}
                            familiehendelsesdato={familiehendelsesdato}
                            handleUpdatePeriode={handleUpdatePeriode}
                            stnadskontoer={stnadskontoer}
                            navnPForeldre={navnPForeldre}
                            annenForelder={annenForelder}
                            arbeidsforhold={arbeidsforhold}
                            handleDeletePeriode={handleDeletePeriode}
                            erFarEllerMedmor={erFarEllerMedmor}
                            erFlerbarnssknad={erFlerbarnssknad}
                            erAleneOmOmsorg={erAleneOmOmsorg}
                            erDeltUttak={erDeltUttak}
                            situasjon={situasjon}
                            meldinger={meldingerPerPeriode[p.id]}
                            erMorUfr={erMorUfr}
                            annenForelderSamtidigUttakPeriode={getAnnenForelderSamtidigUttakPeriode(p, uttaksplan)}
                            skerErFarEllerMedmorOgKunDeHarRett={skerErFarEllerMedmorOgKunDeHarRett}
                            setPeriodeErGyldig={setPeriodeErGyldig}
                            erEndringssknad={erEndringssknad}
                            termindato={termindato}
                            antallBarn={antallBarn}
                            utsettelserIPlan={utsettelserIPlan}
                            intl={intl}
                        />
                        {erAllePerioderIPlanenFrFdsel && index === uttaksplan.length - 1 ? (
                            <FamiliehendelsedatoDisplay barn={barn} familiehendelsedato={familiehendelsesdato} />
                        ) : null}
                        {barnFraNesteSak !== undefined &&
                        index === uttaksplan.length - 1 &&
                        indexOfSistePeriodeFrNyStndasperiodeNyttBarn === uttaksplan.length ? (
                            <Block padBottom="s">
                                <AlertStripe className="nyStnadsperiodeNesteSak" type="info">
                                    <FormattedMessage
                                        id="uttaksplan.periodeliste.info.nyStnadsperiodeNesteSak"
                                        values={{
                                            datoStnadsperiodeNyttBarn: formatDate(
                                                barnFraNesteSak.startdatoFrsteStnadsperiode
                                            ),
                                        }}
                                    />
                                </AlertStripe>
                            </Block>
                        ) : null}
                    </>
                );
            })}
        </div>
    );
};

export default Periodeliste;
