import {
    ANTALL_UTTAKSDAGER_FAR_MEDMOR_RUNDT_FDSEL,
    getFarMedmorUttakRundtFdsel,
    getFrsteUttaksdag2UkerFrFdsel,
    getSisteUttaksdag6UkerEtterFdsel,
    gjelderWLBReglerFarMedmorRundtFdsel,
} from 'app/utils/wlbUtils';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { formaterDatoKompakt } from 'app/utils/dateUtils';
import { getSumUttaksdagerTrekkeIPeriodene } from 'app/steps/uttaksplan-info/utils/Periodene';
import { erUttaksmengdeForFarMedmorForHyTest } from './erUttaksmengdeForFarMedmorForHyTest';
import dayjs from 'dayjs';

export const farMedMorHarRettPUttakRundtFdselTest: RegelTest = (grunnlag: Sknadsinfo): RegelTestresultat => {
    const tattUtForMangeDagerIPlanen = erUttaksmengdeForFarMedmorForHyTest(grunnlag).passerer === false;
    if (
        !gjelderWLBReglerFarMedmorRundtFdsel(
            grunnlag.familiehendelsesdato,
            grunnlag.skerErFarEllerMedmor,
            grunnlag.morHarRett,
            grunnlag.skersituasjon.situasjon
        ) ||
        tattUtForMangeDagerIPlanen
    ) {
        return {
            passerer: true,
        };
    }
    const perioderRundtFdsel = getFarMedmorUttakRundtFdsel(
        grunnlag.perioder,
        grunnlag.familiehendelsesdato,
        grunnlag.termindato
    );

    let dagerIgjenRundtFdsel;
    const frsteUttaksdag2UkerFrFdsel = getFrsteUttaksdag2UkerFrFdsel(
        grunnlag.familiehendelsesdato,
        grunnlag.termindato
    );
    const sisteUttak6UkerEtterFdsel = getSisteUttaksdag6UkerEtterFdsel(grunnlag.familiehendelsesdato);

    if (perioderRundtFdsel.length > 0) {
        const antallUttaksdagerRundtFdsel = getSumUttaksdagerTrekkeIPeriodene(perioderRundtFdsel);
        dagerIgjenRundtFdsel = ANTALL_UTTAKSDAGER_FAR_MEDMOR_RUNDT_FDSEL - antallUttaksdagerRundtFdsel;
    } else {
        dagerIgjenRundtFdsel = ANTALL_UTTAKSDAGER_FAR_MEDMOR_RUNDT_FDSEL;
    }
    const erMerEnn3mnederSidenFamiliehendelse = dayjs(new Date())
        .subtract(3, 'month')
        .isSameOrAfter(grunnlag.familiehendelsesdato, 'day');

    return {
        passerer: dagerIgjenRundtFdsel <= 0 || erMerEnn3mnederSidenFamiliehendelse,
        info: {
            intlKey: 'uttaksplan.validering.info.rettTilUttakRundtFdsel',
            values: {
                fraDato: formaterDatoKompakt(frsteUttaksdag2UkerFrFdsel),
                tilDato: formaterDatoKompakt(sisteUttak6UkerEtterFdsel),
                antallDager: Math.round(dagerIgjenRundtFdsel * 10) / 10,
            },
        },
    };
};
