import { TidsperiodeDate } from '@navikt/fp-common';
import { Situasjon } from 'app/types/Situasjon';
import dayjs from 'dayjs';
import { Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { uttakRundtFdselrsakSprsmlSkalBesvares } from './uttakRundtFdselrsakSprsmlSkalBesvares';

const samtidigUttakSkalBesvares = (
    periodetype: Periodetype,
    konto: StnadskontoType,
    erUttakInnenFrsteSeksUkerFdselFarMedmor: boolean,
    erUttakFrFdsel: boolean,
    erAleneOmsorg: boolean,
    erDeltUttakINorge: boolean,
    skerHarMidlertidigOmsorg: boolean,
    erMorForSyk: boolean | undefined,
    nskerFlerbarnsdager: boolean | undefined,
    tidsperiode: TidsperiodeDate,
    skerErFarEllerMedmor: boolean,
    annenforelderKanIkkeOppgis: boolean,
    familiehendelsesdato: Date,
    termindato: Date | undefined,
    situasjon: Situasjon,
    bareFarMedmorHarRett: boolean
): boolean => {
    if (erAleneOmsorg || !erDeltUttakINorge || skerHarMidlertidigOmsorg) {
        return false;
    }
    if (skerErFarEllerMedmor && dayjs(tidsperiode.fom).isBefore(familiehendelsesdato, 'd')) {
        return false;
    }
    if (
        uttakRundtFdselrsakSprsmlSkalBesvares(
            periodetype,
            konto,
            tidsperiode,
            skerErFarEllerMedmor,
            erAleneOmsorg,
            annenforelderKanIkkeOppgis,
            skerHarMidlertidigOmsorg,
            familiehendelsesdato,
            termindato,
            situasjon,
            bareFarMedmorHarRett
        )
    ) {
        return false;
    }

    if (periodetype === Periodetype.Uttak) {
        if (erMorForSyk) {
            return false;
        }

        const erUttakEgenKvoteFarMedmorFrsteSeksUkerUtenFlerbarnsdager: boolean =
            erUttakInnenFrsteSeksUkerFdselFarMedmor &&
            (konto === StnadskontoType.Fedrekvote || konto === StnadskontoType.Foreldrepenger) &&
            nskerFlerbarnsdager !== true;

        return !(erUttakFrFdsel || erUttakEgenKvoteFarMedmorFrsteSeksUkerUtenFlerbarnsdager);
    }

    return false;
};

export default samtidigUttakSkalBesvares;
