import { intlUtils } from '@navikt/fp-common';
import { Forelder } from 'app/types/Forelder';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { formaterDatoKompakt } from 'app/utils/dateUtils';
import { Normaltekst } from 'nav-frontend-typografi';
import React, { FunctionComponent } from 'react';
import { IntlShape, useIntl } from 'react-intl';
import { isUttakAnnenPart, UtsettelseAnnenPartInfoPeriode, UttakAnnenPartInfoPeriode } from 'uttaksplan/types/Periode';
import { UtsettelsersakType } from 'uttaksplan/types/UtsettelsersakType';
import { getForelderNavn, getOppholdskontoNavn } from 'uttaksplan/utils/periodeUtils';

export interface Props {
    periode: UttakAnnenPartInfoPeriode | UtsettelseAnnenPartInfoPeriode;
    navnPForeldre: NavnPForeldre;
}

const getUtsettelseTekst = (intl: IntlShape, rsak: UtsettelsersakType, foreldernavn: string, erMor: boolean) => {
    return erMor
        ? intlUtils(intl, `uttaksplan.utsettelsersaktype.foreldernavn.far.${rsak}`, { foreldernavn })
        : intlUtils(intl, `uttaksplan.utsettelsersaktype.foreldernavn.mor.${rsak}`, { foreldernavn });
};

const PeriodeInfo: FunctionComponent<Props> = ({ periode, navnPForeldre }) => {
    const intl = useIntl();

    return (
        <Normaltekst>
            <strong>
                <span>{formaterDatoKompakt(periode.tidsperiode.fom)}</span>
                <span>&mdash;</span>
                <span>{formaterDatoKompakt(periode.tidsperiode.tom)}:</span>
            </strong>{' '}
            {isUttakAnnenPart(periode)
                ? getOppholdskontoNavn(
                      intl,
                      periode.rsak,
                      getForelderNavn(periode.forelder, navnPForeldre),
                      periode.forelder === Forelder.mor
                  )
                : getUtsettelseTekst(
                      intl,
                      periode.rsak,
                      getForelderNavn(periode.forelder, navnPForeldre),
                      periode.forelder === Forelder.mor
                  )}
        </Normaltekst>
    );
};

export default PeriodeInfo;
