import { Periode } from 'uttaksplan/types/Periode';
import { leggTilPeriode } from './leggTilPeriode';
import { slettPeriode } from './slettPeriode';
import { slSammenLikePerioder } from './uttaksplanbuilderUtils';

interface OppdaterPeriodeParams {
    perioder: Periode[];
    endretPeriode: Periode;
    originalPeriode: Periode;
    familiehendelsesdato: Date;
    harAktivitetskravIPeriodeUtenUttak: boolean;
    erAdopsjon: boolean;
    bareFarHarRett: boolean;
    erFarEllerMedmor: boolean;
    annenPartsUttak: Periode[] | undefined;
    frsteUttaksdagNesteBarnsSak: Date | undefined;
}

export const oppdaterPeriode = ({
    perioder,
    endretPeriode,
    originalPeriode,
    familiehendelsesdato,
    harAktivitetskravIPeriodeUtenUttak,
    erAdopsjon,
    bareFarHarRett,
    erFarEllerMedmor,
    annenPartsUttak,
    frsteUttaksdagNesteBarnsSak,
}: OppdaterPeriodeParams): Periode[] => {
    const perioderSlettetEndretPeriode = slSammenLikePerioder(
        slettPeriode({
            perioder,
            slettetPeriode: originalPeriode,
            familiehendelsesdato,
            harAktivitetskravIPeriodeUtenUttak,
            erAdopsjon,
            bareFarHarRett,
            erFarEllerMedmor,
            frsteUttaksdagNesteBarnsSak,
        }),
        familiehendelsesdato,
        frsteUttaksdagNesteBarnsSak,
        annenPartsUttak
    );

    return slSammenLikePerioder(
        leggTilPeriode({
            perioder: perioderSlettetEndretPeriode,
            nyPeriode: endretPeriode,
            familiehendelsesdato,
            harAktivitetskravIPeriodeUtenUttak,
            erAdopsjon,
            bareFarHarRett,
            erFarEllerMedmor,
            frsteUttaksdagNesteBarnsSak,
        }),
        familiehendelsesdato,
        frsteUttaksdagNesteBarnsSak,
        annenPartsUttak
    );
};
