import React from 'react';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';
import { IntlShape } from 'react-intl';
import {
    isInfoPeriode,
    isPeriodeUtenUttak,
    isUtsettelsePgaArbeid,
    isUttaksperiode,
    Periode,
} from 'uttaksplan/types/Periode';
import links from 'app/links/links';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';

const vekslendeArbeidOgUttakTest = (kunSkersPerioder: Periode[]): boolean => {
    const firstArbeidsperiodeIndex = kunSkersPerioder.findIndex((p) => isUtsettelsePgaArbeid(p));

    if (firstArbeidsperiodeIndex > -1) {
        const antallPerioderEtterFrsteUtsettelseGrunnetArbeid =
            kunSkersPerioder.length - 1 - firstArbeidsperiodeIndex;

        if (antallPerioderEtterFrsteUtsettelseGrunnetArbeid >= 5) {
            // Hvis vi har en serie perioder av formatet:
            // Arbeid -> Uttak -> Arbeid -> Uttak -> Arbeid -> Uttak => foresl gradering
            if (
                isUtsettelsePgaArbeid(kunSkersPerioder[firstArbeidsperiodeIndex]) &&
                isUttaksperiode(kunSkersPerioder[firstArbeidsperiodeIndex + 1]) &&
                isUtsettelsePgaArbeid(kunSkersPerioder[firstArbeidsperiodeIndex + 2]) &&
                isUttaksperiode(kunSkersPerioder[firstArbeidsperiodeIndex + 3]) &&
                isUtsettelsePgaArbeid(kunSkersPerioder[firstArbeidsperiodeIndex + 4]) &&
                isUttaksperiode(kunSkersPerioder[firstArbeidsperiodeIndex + 5])
            ) {
                return false;
            }
        }
        if (antallPerioderEtterFrsteUtsettelseGrunnetArbeid === 4 && firstArbeidsperiodeIndex > 0) {
            // Hvis vi har en serie perioder av formatet:
            // Uttak -> Arbeid -> Uttak -> Arbeid -> Uttak -> Arbeid => foresl gradering
            if (
                firstArbeidsperiodeIndex > 0 &&
                isUttaksperiode(kunSkersPerioder[firstArbeidsperiodeIndex - 1]) &&
                isUtsettelsePgaArbeid(kunSkersPerioder[firstArbeidsperiodeIndex]) &&
                isUttaksperiode(kunSkersPerioder[firstArbeidsperiodeIndex + 1]) &&
                isUtsettelsePgaArbeid(kunSkersPerioder[firstArbeidsperiodeIndex + 2]) &&
                isUttaksperiode(kunSkersPerioder[firstArbeidsperiodeIndex + 3]) &&
                isUtsettelsePgaArbeid(kunSkersPerioder[firstArbeidsperiodeIndex + 4])
            ) {
                return false;
            }
        }
    }

    return true;
};

const vekslendePeriodeUtenUttakOgUttakTest = (kunSkersPerioder: Periode[]): boolean => {
    const firstPeriodeUtenUttakIndex = kunSkersPerioder.findIndex((p) => isPeriodeUtenUttak(p));

    if (firstPeriodeUtenUttakIndex > -1) {
        const antallPerioderEtterFrstePeriodeUtenUttak = kunSkersPerioder.length - 1 - firstPeriodeUtenUttakIndex;

        if (antallPerioderEtterFrstePeriodeUtenUttak >= 5) {
            // Hvis vi har en serie perioder av formatet:
            // Uten Uttak -> Uttak ->  Uten Uttak -> Uttak ->  Uten Uttak -> Uttak => foresl gradering
            if (
                isPeriodeUtenUttak(kunSkersPerioder[firstPeriodeUtenUttakIndex]) &&
                isUttaksperiode(kunSkersPerioder[firstPeriodeUtenUttakIndex + 1]) &&
                isPeriodeUtenUttak(kunSkersPerioder[firstPeriodeUtenUttakIndex + 2]) &&
                isUttaksperiode(kunSkersPerioder[firstPeriodeUtenUttakIndex + 3]) &&
                isPeriodeUtenUttak(kunSkersPerioder[firstPeriodeUtenUttakIndex + 4]) &&
                isUttaksperiode(kunSkersPerioder[firstPeriodeUtenUttakIndex + 5])
            ) {
                return false;
            }
        }
        if (antallPerioderEtterFrstePeriodeUtenUttak === 4 && firstPeriodeUtenUttakIndex > 0) {
            // Hvis vi har en serie perioder av formatet:
            // Uttak ->  Uten Uttak -> Uttak ->  Uten Uttak -> Uttak ->  Uten Uttak => foresl gradering
            if (
                firstPeriodeUtenUttakIndex > 0 &&
                isUttaksperiode(kunSkersPerioder[firstPeriodeUtenUttakIndex - 1]) &&
                isPeriodeUtenUttak(kunSkersPerioder[firstPeriodeUtenUttakIndex]) &&
                isUttaksperiode(kunSkersPerioder[firstPeriodeUtenUttakIndex + 1]) &&
                isPeriodeUtenUttak(kunSkersPerioder[firstPeriodeUtenUttakIndex + 2]) &&
                isUttaksperiode(kunSkersPerioder[firstPeriodeUtenUttakIndex + 3]) &&
                isPeriodeUtenUttak(kunSkersPerioder[firstPeriodeUtenUttakIndex + 4])
            ) {
                return false;
            }
        }
    }

    return true;
};

export const burdeKanskjeSkeGraderingTest: RegelTest = (grunnlag: Sknadsinfo): RegelTestresultat => {
    const { perioder } = grunnlag;
    const kunSkersPerioder = perioder.filter((p: Periode) => !isInfoPeriode(p));

    if (kunSkersPerioder.length >= 6) {
        const vekslerArbeidOgUttakTest = vekslendeArbeidOgUttakTest(kunSkersPerioder);
        const veklserPerioderUtenUttakOgUttaktest = vekslendePeriodeUtenUttakOgUttakTest(kunSkersPerioder);
        const passerer = vekslerArbeidOgUttakTest && veklserPerioderUtenUttakOgUttaktest;
        let infoTekstKey = undefined;
        if (!vekslerArbeidOgUttakTest) {
            infoTekstKey =
                'uttaksplan.validering.info.burdeKanskjeSkeGradering.PgaVekslendeUtsettelserPgaArbeidOgUttak';
        }

        if (!veklserPerioderUtenUttakOgUttaktest) {
            infoTekstKey = 'uttaksplan.validering.info.burdeKanskjeSkeGradering.PgaVekslendePerioderUtenUttakOgUttak';
        }

        return {
            passerer: passerer,
            info: {
                intlKey: infoTekstKey,
                renderAsHtml: true,
                values: {
                    a: (_intl: IntlShape) => (msg: any) =>
                        (
                            <a href={links.graderingInfo} className="lenke" rel="noreferrer" target="_blank">
                                {msg}
                            </a>
                        ),
                },
            },
        };
    }

    return {
        passerer: true,
    };
};
