import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { RegelTestresultat } from '../utils/types/regelTypes';
import { isUtsettelsesperiode } from 'uttaksplan/types/Periode';
import { Periodene } from 'app/steps/uttaksplan-info/utils/Periodene';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { formaterDato } from 'app/utils/dateUtils';

export function inneholderBareUtsettelserTest(grunnlag: Sknadsinfo): RegelTestresultat {
    const bareUtsettelser = !grunnlag.perioder.some((p) => !isUtsettelsesperiode(p)) && grunnlag.perioder.length > 0;
    const passerer = bareUtsettelser === false;
    const intlKey = grunnlag.erEndringssknad
        ? 'uttaksplan.veileder.planenInneholderKunUtsettelser.endringssknad'
        : 'uttaksplan.veileder.planenInneholderKunUtsettelser';

    return {
        passerer,
        info: {
            intlKey,
            values: {
                sisteDag: formaterDato(
                    Uttaksdagen(Periodene(grunnlag.perioder).getFrsteUttaksdagEtterSistePeriode()!).forrige(),
                    'D. MMMM YYYY'
                ),
            },
        },
    };
}
