import { Sknadsinfo } from '../../utils/types/Sknadsinfo';
import { RegelTest, RegelTestresultat } from 'uttaksplan/validering/utils/types/regelTypes';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import dayjs from 'dayjs';
import { isUttakAnnenPart, Periode, Periodetype } from 'uttaksplan/types/Periode';
import { formatDate } from '@navikt/fp-common';

export const laTilPeriodeEtterFrsteStnadsdagPflgendeBarn = (
    periode: Periode,
    frsteUttaksdagForPflgendeBarn: Date | undefined
) => {
    if (
        frsteUttaksdagForPflgendeBarn !== undefined &&
        (periode.type === Periodetype.Uttak ||
            periode.type === Periodetype.Overfring ||
            periode.type === Periodetype.Utsettelse ||
            periode.type === Periodetype.Opphold ||
            isUttakAnnenPart(periode))
    ) {
        return (
            dayjs(periode.tidsperiode.fom).isSameOrAfter(frsteUttaksdagForPflgendeBarn, 'd') ||
            dayjs(periode.tidsperiode.tom).isSameOrAfter(frsteUttaksdagForPflgendeBarn, 'd')
        );
    }
    return false;
};

export const forSenUttakVedPflgendeBarn: RegelTest = (grunnlag: Sknadsinfo): RegelTestresultat => {
    if (
        grunnlag.familiehendelsesdatoNesteSak === undefined ||
        grunnlag.frsteUttaksdagNesteBarnsSak === undefined ||
        (grunnlag.minsterettUkerToTette && grunnlag.minsterettUkerToTette > 0)
    ) {
        return {
            passerer: true,
        };
    }
    const perioderMedUgyldigTidsperiode = grunnlag.perioder.filter((periode) =>
        laTilPeriodeEtterFrsteStnadsdagPflgendeBarn(periode, grunnlag.frsteUttaksdagNesteBarnsSak)
    );
    const sisteMuligeUttaksdag = Uttaksdagen(grunnlag.frsteUttaksdagNesteBarnsSak).trekkFra(1);
    return {
        passerer: perioderMedUgyldigTidsperiode.length === 0,
        info: perioderMedUgyldigTidsperiode.map((periode) => ({
            intlKey: 'uttaksplan.validering.info.forSenUttakVedPflgendeBarn',
            values: {
                fom: formatDate(periode.tidsperiode.fom),
                tom: formatDate(periode.tidsperiode.tom),
                sisteMuligeUttaksdag: formatDate(sisteMuligeUttaksdag),
            },
            periodeId: periode.id,
        })),
    };
};
