import { IntlShape } from 'react-intl';
import { bemUtils, Block } from '@navikt/fp-common';
import AnnenForelder from 'app/context/types/AnnenForelder';
import Arbeidsforhold from 'app/types/Arbeidsforhold';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { Situasjon } from 'app/types/Situasjon';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import classNames from 'classnames';
import { EkspanderbartpanelBase } from 'nav-frontend-ekspanderbartpanel';
import React, { Dispatch, FunctionComponent, SetStateAction } from 'react';
import {
    isAvslttPeriode,
    isForeldrepengerFrFdselUttaksperiode,
    isInfoPeriode,
    Periode,
    Periodetype,
    Utsettelsesperiode,
} from 'uttaksplan/types/Periode';
import { VeilederMessage } from 'uttaksplan/validering/veilederInfo/types';
import VeilederMeldinger from 'uttaksplan/validering/veilederInfo/VeilederMeldinger';
import PeriodelisteItemHeader from '../periodeliste-item-header/PeriodelisteItemHeader';
import PeriodeHull from '../perioder/PeriodeHull';
import PeriodeInfo from '../perioder/PeriodeInfo';
import PeriodeUtenUttak from '../perioder/PeriodeUtenUttak';
import PeriodeFrFdselForm from '../uttaks-forms/periode-fr-fdsel-form/PeriodeFrFdselForm';
import PeriodeUtsettelseForm from '../uttaks-forms/periode-utsettelse-form/PeriodeUtsettelseForm';
import PeriodeUttakForm from '../uttaks-forms/periode-uttak-form/PeriodeUttakForm';

import './periodelisteItem.less';

interface Props {
    egenPeriode: boolean;
    periode: Periode;
    isOpen: boolean;
    toggleIsOpen: (id: string) => void;
    familiehendelsesdato: Date;
    handleUpdatePeriode: (periode: Periode, familiehendelsedato: Date) => void;
    stnadskontoer: TilgjengeligStnadskonto[];
    navnPForeldre: NavnPForeldre;
    annenForelder: AnnenForelder;
    arbeidsforhold: Arbeidsforhold[];
    handleDeletePeriode: (periodeId: string) => void;
    erFarEllerMedmor: boolean;
    erFlerbarnssknad: boolean;
    erAleneOmOmsorg: boolean;
    erDeltUttak: boolean;
    situasjon: Situasjon;
    meldinger?: VeilederMessage[];
    erMorUfr: boolean;
    annenForelderSamtidigUttakPeriode: Periode | undefined;
    skerErFarEllerMedmorOgKunDeHarRett: boolean;
    setPeriodeErGyldig: Dispatch<SetStateAction<boolean>>;
    erEndringssknad: boolean;
    termindato: Date | undefined;
    antallBarn: number;
    utsettelserIPlan: Utsettelsesperiode[];
    intl: IntlShape;
}

const renderPeriodeListeInnhold = (
    periode: Periode,
    familiehendelsesdato: Date,
    handleUpdatePeriode: (periode: Periode, familiehendelsedato: Date) => void,
    stnadskontoer: TilgjengeligStnadskonto[],
    navnPForeldre: NavnPForeldre,
    annenForelder: AnnenForelder,
    toggleIsOpen: (id: string) => void,
    arbeidsforhold: Arbeidsforhold[],
    handleDeletePeriode: (periodeId: string) => void,
    erFarEllerMedmor: boolean,
    erFlerbarnssknad: boolean,
    erAleneOmOmsorg: boolean,
    erDeltUttak: boolean,
    situasjon: Situasjon,
    erMorUfr: boolean,
    skerErFarEllerMedmorOgKunDeHarRett: boolean,
    setPeriodeErGyldig: Dispatch<SetStateAction<boolean>>,
    erEndringssknad: boolean,
    termindato: Date | undefined,
    antallBarn: number,
    utsettelserIPlan: Utsettelsesperiode[],
    intl: IntlShape
) => {
    switch (periode.type) {
        case Periodetype.Uttak:
        case Periodetype.Overfring:
        case Periodetype.Opphold:
            if (isForeldrepengerFrFdselUttaksperiode(periode)) {
                return (
                    <PeriodeFrFdselForm
                        periode={periode}
                        familiehendelsesdato={familiehendelsesdato}
                        handleUpdatePeriode={handleUpdatePeriode}
                        erFarEllerMedmor={erFarEllerMedmor}
                        morHarRett={!skerErFarEllerMedmorOgKunDeHarRett}
                        situasjon={situasjon}
                        utsettelserIPlan={utsettelserIPlan}
                    />
                );
            }

            return (
                <PeriodeUttakForm
                    periode={periode}
                    familiehendelsesdato={familiehendelsesdato}
                    handleUpdatePeriode={handleUpdatePeriode}
                    stnadskontoer={stnadskontoer}
                    navnPForeldre={navnPForeldre}
                    annenForelder={annenForelder}
                    toggleIsOpen={toggleIsOpen}
                    arbeidsforhold={arbeidsforhold}
                    handleDeletePeriode={handleDeletePeriode}
                    erFarEllerMedmor={erFarEllerMedmor}
                    erFlerbarnssknad={erFlerbarnssknad}
                    erAleneOmOmsorg={erAleneOmOmsorg}
                    erDeltUttak={erDeltUttak}
                    situasjon={situasjon}
                    erMorUfr={erMorUfr}
                    erEndringssknad={erEndringssknad}
                    setPeriodeErGyldig={setPeriodeErGyldig}
                    termindato={termindato}
                    morHarRett={!skerErFarEllerMedmorOgKunDeHarRett}
                    antallBarn={antallBarn}
                    utsettelserIPlan={utsettelserIPlan}
                    intl={intl}
                />
            );
        case Periodetype.Utsettelse:
            return (
                <PeriodeUtsettelseForm
                    periode={periode}
                    familiehendelsesdato={familiehendelsesdato}
                    handleUpdatePeriode={handleUpdatePeriode}
                    erFarEllerMedmor={erFarEllerMedmor}
                    erAleneOmOmsorg={erAleneOmOmsorg}
                    handleDeletePeriode={handleDeletePeriode}
                    toggleIsOpen={toggleIsOpen}
                    navnPForeldre={navnPForeldre}
                    erMorUfr={erMorUfr}
                    skerErFarEllerMedmorOgKunDeHarRett={skerErFarEllerMedmorOgKunDeHarRett}
                    arbeidsforhold={arbeidsforhold}
                    situasjon={situasjon}
                    utsettelserIPlan={utsettelserIPlan}
                />
            );
        case Periodetype.Hull:
            return (
                <PeriodeHull
                    erAleneOmOmsorg={erAleneOmOmsorg}
                    erDeltUttak={erDeltUttak}
                    erFarEllerMedmor={erFarEllerMedmor}
                    periode={periode}
                    familiehendelsesdato={familiehendelsesdato}
                    navnAnnenForelder={erFarEllerMedmor ? navnPForeldre.mor : navnPForeldre.farMedmor}
                    handleUpdatePeriode={handleUpdatePeriode}
                />
            );
        case Periodetype.PeriodeUtenUttak:
            return (
                <PeriodeUtenUttak
                    periode={periode}
                    handleUpdatePeriode={handleUpdatePeriode}
                    familiehendelsesdato={familiehendelsesdato}
                />
            );
        case Periodetype.Info:
            return (
                periode.visPeriodeIPlan &&
                !isAvslttPeriode(periode) && <PeriodeInfo periode={periode} navnPForeldre={navnPForeldre} />
            );
        default:
            return <div>Ingen visning</div>;
    }
};

const PeriodelisteItem: FunctionComponent<Props> = ({
    egenPeriode,
    periode,
    isOpen,
    toggleIsOpen,
    familiehendelsesdato,
    handleUpdatePeriode,
    stnadskontoer,
    navnPForeldre,
    annenForelder,
    arbeidsforhold,
    handleDeletePeriode,
    erFarEllerMedmor,
    erFlerbarnssknad,
    erAleneOmOmsorg,
    erDeltUttak,
    situasjon,
    meldinger = [],
    erMorUfr,
    annenForelderSamtidigUttakPeriode,
    skerErFarEllerMedmorOgKunDeHarRett,
    erEndringssknad,
    setPeriodeErGyldig,
    termindato,
    antallBarn,
    utsettelserIPlan,
    intl,
}) => {
    const bem = bemUtils('periodelisteItem');
    const melding = meldinger.length > 0 ? meldinger[0] : undefined;

    if (isInfoPeriode(periode) && !periode.visPeriodeIPlan) {
        return null;
    }

    const toggleFocusAndOpen = () => {
        const headerButton = window.document.getElementById(periode.id);

        if (headerButton) {
            setTimeout(() => {
                headerButton.focus();
            }, 150);
        }

        toggleIsOpen(periode.id);
    };

    return (
        <article className={bem.block}>
            <EkspanderbartpanelBase
                className={classNames(bem.element('header'), egenPeriode ? undefined : bem.modifier('transparent'))}
                id={periode.id}
                tittel={
                    <PeriodelisteItemHeader
                        egenPeriode={egenPeriode}
                        periode={periode}
                        navnPForeldre={navnPForeldre}
                        melding={melding}
                        annenForelderSamtidigUttakPeriode={annenForelderSamtidigUttakPeriode}
                        familiehendelsesdato={familiehendelsesdato}
                        termindato={termindato}
                        situasjon={situasjon}
                        erFarEllerMedmor={erFarEllerMedmor}
                        erAleneOmOmsorg={erAleneOmOmsorg}
                    />
                }
                apen={isOpen}
                onClick={() => toggleIsOpen(periode.id)}
            >
                <Block visible={meldinger.length > 0}>
                    <VeilederMeldinger meldinger={meldinger.filter((m) => m.avvikType !== 'skjema')} />
                </Block>
                {renderPeriodeListeInnhold(
                    periode,
                    familiehendelsesdato,
                    handleUpdatePeriode,
                    stnadskontoer,
                    navnPForeldre,
                    annenForelder,
                    toggleFocusAndOpen,
                    arbeidsforhold,
                    handleDeletePeriode,
                    erFarEllerMedmor,
                    erFlerbarnssknad,
                    erAleneOmOmsorg,
                    erDeltUttak,
                    situasjon,
                    erMorUfr,
                    skerErFarEllerMedmorOgKunDeHarRett,
                    setPeriodeErGyldig,
                    erEndringssknad,
                    termindato,
                    antallBarn,
                    utsettelserIPlan,
                    intl
                )}
            </EkspanderbartpanelBase>
        </article>
    );
};

export default PeriodelisteItem;
