import { Tidsperiode, TidsperiodeDate } from '@navikt/fp-common';
import { dateToISOString } from '@navikt/sif-common-formik/lib';
import { isValidTidsperiode, Tidsperioden } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { uttaksplanDatoavgrensninger } from 'app/steps/uttaksplan-info/utils/uttaksplanDatoavgrensninger';
import { ISOStringToDate } from 'app/utils/dateUtils';
import { getFrsteUttaksdag2UkerFrFdsel, getSisteUttaksdag6UkerEtterFdsel } from 'app/utils/wlbUtils';
import { DatepickerLimitations } from 'nav-datovelger';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { getFrsteMuligeUttaksdag, getSisteMuligeUttaksdag } from './uttaksdatoerUtils';

export interface DatoAvgrensninger {
    fra: Avgrensninger;
    til: Avgrensninger;
}

export interface Avgrensninger {
    minDato: Date;
    maksDato: Date;
    ugyldigeTidsperioder?: Tidsperiode[] | undefined;
    helgedagerIkkeTillatt: boolean;
}

export const getDatoavgrensningerForFarMedmorPeriodeRundtFdselWLB = (
    familiehendelsesdato: Date,
    termindato: Date | undefined,
    ugyldigeTidsperioder: Tidsperiode[] | undefined
): DatoAvgrensninger => {
    const minDato = getFrsteUttaksdag2UkerFrFdsel(familiehendelsesdato, termindato);
    const maksDato = getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato);
    return {
        fra: {
            minDato,
            maksDato,
            helgedagerIkkeTillatt: true,
            ugyldigeTidsperioder,
        },
        til: {
            minDato,
            maksDato,
            helgedagerIkkeTillatt: true,
            ugyldigeTidsperioder,
        },
    };
};

export const getDatoavgrensningerForStnadskonto = (
    konto: StnadskontoType | undefined,
    familiehendelsesdato: Date,
    tidsperiode: Partial<TidsperiodeDate> | undefined,
    ugyldigeTidsperioder: Tidsperiode[] | undefined,
    erFarEllerMedmor: boolean,
    termindato: Date | undefined
): DatoAvgrensninger => {
    if (konto === undefined) {
        return getDatoavgrensningerForPeriodeUtenKonto(
            familiehendelsesdato,
            ugyldigeTidsperioder,
            erFarEllerMedmor,
            termindato
        );
    }
    if (konto === StnadskontoType.ForeldrepengerFrFdsel) {
        return getDatoavgrensningerForForeldrepengerFrFdsel(familiehendelsesdato);
    }
    if (isValidTidsperiode(tidsperiode) && Tidsperioden(tidsperiode).erFrDato(familiehendelsesdato)) {
        return getDatoavgrensningerForEkstrauttakFrTermin(familiehendelsesdato);
    }

    const standardAvgrensninger = standardAvgrensningerForUttakEtterFdsel(familiehendelsesdato);

    return {
        fra: {
            ...standardAvgrensninger,
            ugyldigeTidsperioder,
        },
        til: {
            ...standardAvgrensninger,
            ugyldigeTidsperioder,
        },
    };
};

const getDatoavgrensningerForPeriodeUtenKonto = (
    familiehendelsesdato: Date,
    ugyldigeTidsperioder: Tidsperiode[] | undefined,
    erFarEllerMedmor: boolean,
    termindato: Date | undefined
) => {
    const minDato = getFrsteMuligeUttaksdag(familiehendelsesdato, erFarEllerMedmor, termindato);

    return {
        fra: {
            minDato,
            maksDato: getSisteMuligeUttaksdag(familiehendelsesdato),
            ugyldigeTidsperioder,
            helgedagerIkkeTillatt: true,
        },
        til: {
            minDato,
            maksDato: getSisteMuligeUttaksdag(familiehendelsesdato),
            ugyldigeTidsperioder,
            helgedagerIkkeTillatt: true,
        },
    };
};

const standardAvgrensningerForUttakEtterFdsel = (familiehendelsesdato: Date): Avgrensninger => {
    return {
        helgedagerIkkeTillatt: true,
        minDato: Uttaksdagen(familiehendelsesdato).denneEllerNeste(),
        maksDato: getSisteMuligeUttaksdag(familiehendelsesdato),
    };
};

const getDatoavgrensningerForForeldrepengerFrFdsel = (familiehendelsesdato: Date): DatoAvgrensninger => {
    const avgrensninger: DatepickerLimitations = {
        ...standardAvgrensningerForUttakEtterFdsel,
        ...uttaksplanDatoavgrensninger.startdatoFrTerminForeldrepengerFrFdselKonto(
            dateToISOString(familiehendelsesdato)
        ),
    };

    return {
        fra: {
            helgedagerIkkeTillatt: !!avgrensninger.weekendsNotSelectable,
            minDato: ISOStringToDate(avgrensninger.minDate)!,
            maksDato: ISOStringToDate(avgrensninger.maxDate)!,
        },
        til: {
            helgedagerIkkeTillatt: !!avgrensninger.weekendsNotSelectable,
            minDato: ISOStringToDate(avgrensninger.minDate)!,
            maksDato: ISOStringToDate(avgrensninger.maxDate)!,
        },
    };
};

const getDatoavgrensningerForEkstrauttakFrTermin = (familiehendelsesdato: Date): DatoAvgrensninger => {
    const avgrensninger: DatepickerLimitations = {
        ...standardAvgrensningerForUttakEtterFdsel,
        ...uttaksplanDatoavgrensninger.ekstrauttakFrFdsel(dateToISOString(familiehendelsesdato)),
    };

    return {
        fra: {
            helgedagerIkkeTillatt: !!avgrensninger.weekendsNotSelectable,
            minDato: ISOStringToDate(avgrensninger.minDate)!,
            maksDato: ISOStringToDate(avgrensninger.maxDate)!,
        },
        til: {
            helgedagerIkkeTillatt: !!avgrensninger.weekendsNotSelectable,
            minDato: ISOStringToDate(avgrensninger.minDate)!,
            maksDato: ISOStringToDate(avgrensninger.maxDate)!,
        },
    };
};

export const getDatoavgrensningerForBareFarMedmorHarRettWLB = (
    familiehendelsesdato: Date,
    termindato: Date | undefined,
    ugyldigeTidsperioder: Tidsperiode[] | undefined
): DatoAvgrensninger => {
    const minDato = getFrsteUttaksdag2UkerFrFdsel(familiehendelsesdato, termindato);
    const maksDato = getSisteMuligeUttaksdag(familiehendelsesdato);
    return {
        fra: {
            minDato,
            maksDato,
            helgedagerIkkeTillatt: true,
            ugyldigeTidsperioder,
        },
        til: {
            minDato,
            maksDato,
            helgedagerIkkeTillatt: true,
            ugyldigeTidsperioder,
        },
    };
};
