import { Sknadsinfo } from '../../utils/types/Sknadsinfo';
import { RegelTest, RegelTestresultat } from 'uttaksplan/validering/utils/types/regelTypes';
import {
    getFrsteUttaksdag2UkerFrFdsel,
    gjelderWLBReglerFarMedmorRundtFdsel,
    isUttaksperiodeFarMedmorMedValgForUttakRundtFdsel,
    starterTidsperiodeEtter2UkerFrFdsel,
} from 'app/utils/wlbUtils';
import { formaterDatoKompakt } from 'app/utils/dateUtils';

export const starterUttaksperiodeRundtFdselEtter2UkerFrFdsel: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    if (
        !gjelderWLBReglerFarMedmorRundtFdsel(
            grunnlag.familiehendelsesdato,
            grunnlag.skerErFarEllerMedmor,
            grunnlag.morHarRett,
            grunnlag.skersituasjon.situasjon
        )
    ) {
        return {
            passerer: true,
        };
    }

    const perioderFarMedmorSomStarterFr2UkerFrFdsel = grunnlag.perioder
        .filter((p) => isUttaksperiodeFarMedmorMedValgForUttakRundtFdsel(p))
        .filter(
            (p) =>
                !starterTidsperiodeEtter2UkerFrFdsel(
                    p.tidsperiode,
                    grunnlag.familiehendelsesdato,
                    grunnlag.termindato
                )
        );

    const frsteUttaksdagToUkerFrFdsel = formaterDatoKompakt(
        getFrsteUttaksdag2UkerFrFdsel(grunnlag.familiehendelsesdato, grunnlag.termindato)
    );
    return {
        passerer: perioderFarMedmorSomStarterFr2UkerFrFdsel.length === 0,
        info: perioderFarMedmorSomStarterFr2UkerFrFdsel.map((periode) => ({
            intlKey: 'uttaksplan.validering.feil.starterUttaksperiodeRundtFdselEtter2UkerFrFdsel',
            values: { dato: frsteUttaksdagToUkerFrFdsel },
            periodeId: periode.id,
        })),
    };
};
