import React, { FunctionComponent } from 'react';
import { FormattedMessage, IntlShape, useIntl } from 'react-intl';
import { bemUtils, intlUtils } from '@navikt/fp-common';
import { Undertittel } from 'nav-frontend-typografi';
import Personkort from 'app/components/personkort/Personkort';
import ForelderIkon from 'app/components/foreldrepar/ForelderIkon';
import { getVarighetString } from 'app/utils/dateUtils';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import useSknad from 'app/utils/hooks/useSknad';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { getNavnPForeldre } from 'app/utils/personUtils';
import { getSituasjonForelderSvg } from 'app/utils/foreldreparSituasjonUtils';
import { ForeldreparSituasjon } from 'app/types/ForeldreparSituasjonTypes';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { Periode } from 'uttaksplan/types/Periode';
import { BrukteDager, getBrukteDager } from 'uttaksplan/utils/brukteDagerUtils';
import { getUttaksstatus, Uttaksstatus } from 'uttaksplan/utils/uttaksstatus';
import Kontostatus from './konto-status/Kontostatus';
import TilesList from './tilesList/TilesList';
import './oversiktKvoter.less';
import { Situasjon } from 'app/types/Situasjon';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { StnadskontoUttak } from 'uttaksplan/types/StnadskontoUttak';
import { capitalizeFirstLetter } from 'app/utils/stringUtils';

const bem = bemUtils('oversiktKvoter');

const filtrerBortAnnenPartsKonto = (
    uttakskontoer: StnadskontoUttak[],
    erFarEllerMedmor: boolean
): StnadskontoUttak[] => {
    return erFarEllerMedmor
        ? uttakskontoer.filter((uttak) => uttak.konto !== StnadskontoType.Mdrekvote)
        : uttakskontoer.filter((uttak) => uttak.konto !== StnadskontoType.Fedrekvote);
};
interface PropsPerForelder {
    brukteDagerPerForelder: BrukteDager;
    erDeltUttakINorge: boolean;
    foreldreparSituasjon: ForeldreparSituasjon;
    navnPForeldre: NavnPForeldre;
    skerErFarEllerMedmor: boolean;
}

const OversiktPerForelder: FunctionComponent<PropsPerForelder> = ({
    brukteDagerPerForelder,
    erDeltUttakINorge,
    foreldreparSituasjon,
    navnPForeldre,
    skerErFarEllerMedmor,
}) => {
    const intl = useIntl();
    const skerErMor = !skerErFarEllerMedmor;
    const svgInfo = getSituasjonForelderSvg(foreldreparSituasjon);

    return (
        <div className={bem.block}>
            <div className={bem.element('perForelder')}>
                <Undertittel tag="h2" className="blokk-xs">
                    {intlUtils(intl, 'uttaksplan.oversiktKvoter.tittel.foreldre')}
                </Undertittel>
                <TilesList columns={'flex'}>
                    {(erDeltUttakINorge || skerErMor) && (
                        <Personkort ikon={<ForelderIkon forelder={svgInfo.mor} />} tittel={navnPForeldre.mor}>
                            <strong>{getVarighetString(brukteDagerPerForelder.mor.dagerTotalt, intl)}</strong>
                        </Personkort>
                    )}
                    {(erDeltUttakINorge || skerErFarEllerMedmor) && (
                        <Personkort
                            ikon={<ForelderIkon forelder={svgInfo.farMedmor} />}
                            tittel={capitalizeFirstLetter(navnPForeldre.farMedmor)}
                        >
                            <strong>{getVarighetString(brukteDagerPerForelder.farMedmor.dagerTotalt, intl)}</strong>
                        </Personkort>
                    )}
                </TilesList>
            </div>
        </div>
    );
};

interface PropsPerKvote {
    erDeltUttakINorge: boolean;
    navnPForeldre: NavnPForeldre;
    erEndringssknad: boolean;
    uttaksstatus: Uttaksstatus;
    erFarEllerMedmor: boolean;
    situasjon: Situasjon;
    erAleneOmOmsorg: boolean;
}

const OversiktPerKvote: FunctionComponent<PropsPerKvote> = ({
    erDeltUttakINorge,
    navnPForeldre,
    erEndringssknad,
    uttaksstatus,
    erFarEllerMedmor,
    situasjon,
    erAleneOmOmsorg,
}) => {
    const uttakVise = erDeltUttakINorge
        ? uttaksstatus.uttak
        : filtrerBortAnnenPartsKonto(uttaksstatus.uttak, erFarEllerMedmor);
    return (
        <div className={bem.element('perKvote')}>
            <Undertittel tag="h2" className="blokk-xs">
                <FormattedMessage
                    id={
                        uttaksstatus.gjelderDagerBrukt
                            ? 'uttaksplan.oversiktKvoter.tittel.kontoer.brukteDager'
                            : 'uttaksplan.oversiktKvoter.tittel.kontoer.ikkeBrukteDager'
                    }
                    values={{ antall: erDeltUttakINorge ? 2 : 1 }}
                />
            </Undertittel>
            <TilesList columns={2}>
                {uttakVise.map((uttak, idx) => (
                    <Kontostatus
                        key={idx}
                        uttak={uttak}
                        navnPForeldre={navnPForeldre}
                        erEndringssknad={erEndringssknad}
                        erFarEllerMedmor={erFarEllerMedmor}
                        situasjon={situasjon}
                        erAleneOmOmsorg={erAleneOmOmsorg}
                    />
                ))}
            </TilesList>
        </div>
    );
};

interface Props {
    tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[];
    uttaksplan: Periode[];
    erDeltUttak: boolean;
    foreldreparSituasjon: ForeldreparSituasjon;
    familiehendelsesdato: Date;
    annenForelderHarRettINorge: boolean;
    toTetteReglerGjelder: boolean;
    intl: IntlShape;
}

const OversiktKvoter: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer,
    uttaksplan,
    erDeltUttak,
    foreldreparSituasjon,
    familiehendelsesdato,
    annenForelderHarRettINorge,
    intl,
}) => {
    const sker = useSkerinfo();
    const sknad = useSknad();
    const skerErFarEllerMedmor = isFarEllerMedmor(sknad.skersituasjon.rolle);
    const navnPForeldre = getNavnPForeldre(sker.person, sknad.annenForelder, skerErFarEllerMedmor, intl);
    const uttaksstatus = getUttaksstatus({
        erDeltUttak: erDeltUttak,
        erEndringssknad: sknad.erEndringssknad,
        harKomplettUttaksplan: true,
        erFarEllerMedmor: skerErFarEllerMedmor,
        tilgjengeligeStnadskontoer: tilgjengeligeStnadskontoer,
        uttaksplan,
    });
    const brukteDagerPerForelder = getBrukteDager(tilgjengeligeStnadskontoer, uttaksplan, familiehendelsesdato);
    const erDeltUttakINorge = erDeltUttak && annenForelderHarRettINorge;
    return (
        <div className={bem.block}>
            <OversiktPerForelder
                brukteDagerPerForelder={brukteDagerPerForelder}
                erDeltUttakINorge={erDeltUttakINorge}
                foreldreparSituasjon={foreldreparSituasjon}
                navnPForeldre={navnPForeldre}
                skerErFarEllerMedmor={skerErFarEllerMedmor}
            />
            <OversiktPerKvote
                erDeltUttakINorge={erDeltUttakINorge}
                navnPForeldre={navnPForeldre}
                erEndringssknad={sknad.erEndringssknad}
                uttaksstatus={uttaksstatus}
                erFarEllerMedmor={skerErFarEllerMedmor}
                situasjon={sknad.skersituasjon.situasjon}
                erAleneOmOmsorg={sknad.sker.erAleneOmOmsorg}
            />
        </div>
    );
};

export default OversiktKvoter;
