import { IntlShape } from 'react-intl';
import { getUttaksstatus } from '../../utils/uttaksstatus';
import { getStnadskontoNavn } from '../../utils/stnadskontoerUtils';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { RegelTest, RegelTestresultat, RegelTestresultatInfo } from '../utils/types/regelTypes';
import { getVarighetString } from '../../../app/utils/dateUtils';

export const stnadskontoInneholderForMyeUttakKunSkerTest: RegelTest = (grunnlag: Sknadsinfo): RegelTestresultat => {
    const {
        navnPForeldre,
        perioder,
        stnadskontoer,
        erDeltUttak,
        erEndringssknad,
        harKomplettUttaksplan,
        skerErFarEllerMedmor,
        skerErAleneOmOmsorg,
    } = grunnlag;
    const stnadskontoerMedForMyeUttak = getUttaksstatus({
        erDeltUttak,
        erEndringssknad,
        harKomplettUttaksplan,
        erFarEllerMedmor: skerErFarEllerMedmor,
        tilgjengeligeStnadskontoer: stnadskontoer,
        uttaksplan: perioder,
    }).uttak.filter((u) => u.dager < 0);
    return {
        passerer: stnadskontoerMedForMyeUttak.length === 0,
        info: stnadskontoerMedForMyeUttak.map(
            (uttak): RegelTestresultatInfo => ({
                intlKey: 'uttaksplan.validering.feil.forMyeUttak',
                values: {
                    dager: (intl: IntlShape) => getVarighetString(Math.abs(uttak.dager), intl),
                    konto: (intl: IntlShape) =>
                        getStnadskontoNavn(
                            intl,
                            uttak.konto,
                            navnPForeldre,
                            skerErFarEllerMedmor,
                            skerErAleneOmOmsorg
                        ),
                },
            })
        ),
    };
};
