import { RegelTestresultat } from '../utils/types/regelTypes';
import { beregnGjenstendeUttaksdager } from 'uttaksplan/utils/uttaksPlanStatus';
import { isUtsettelsesperiode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';

// Denne regelen sparker inn dersom forelders kvote + fellesperiode er tmt
// Dersom noe er igjen av andre forelders kvote er det OK
export function kanIkkeSlutteMedUtsettelseDersomStnadsdagerErTomme(grunnlag: Sknadsinfo): RegelTestresultat {
    const { perioder, skerErFarEllerMedmor, stnadskontoer } = grunnlag;
    const gjenstendeDager = beregnGjenstendeUttaksdager(stnadskontoer, perioder, false);
    const sistePeriodeErIkkeUtsettelse =
        perioder.length > 0 ? !isUtsettelsesperiode(perioder[perioder.length - 1]) : false;

    const resterendeFellesperiode = gjenstendeDager.find((konto) => konto.konto === StnadskontoType.Fellesperiode);
    const resterendeMdrekvote = gjenstendeDager.find((konto) => konto.konto === StnadskontoType.Mdrekvote);
    const resterendeFedrekvote = gjenstendeDager.find((konto) => konto.konto === StnadskontoType.Fedrekvote);
    const resterendeForeldrepenger = gjenstendeDager.find((konto) => konto.konto === StnadskontoType.Foreldrepenger);

    if (sistePeriodeErIkkeUtsettelse || perioder.length === 0) {
        return {
            passerer: true,
        };
    }

    if (skerErFarEllerMedmor) {
        if (resterendeFedrekvote !== undefined && resterendeFellesperiode !== undefined) {
            if (resterendeFedrekvote.dager > 0 || resterendeFellesperiode.dager > 0) {
                return {
                    passerer: true,
                };
            }
        }

        if (resterendeForeldrepenger !== undefined) {
            if (resterendeForeldrepenger.dager > 0) {
                return {
                    passerer: true,
                };
            }
        }

        return {
            passerer: false,
            info: {
                intlKey: 'uttaksplan.validering.feil.sluttMedUtsettelse',
            },
        };
    } else {
        if (resterendeMdrekvote !== undefined && resterendeFellesperiode !== undefined) {
            if (resterendeMdrekvote.dager > 0 || resterendeFellesperiode.dager > 0) {
                return {
                    passerer: true,
                };
            }
        }

        if (resterendeForeldrepenger !== undefined) {
            if (resterendeForeldrepenger.dager > 0) {
                return {
                    passerer: true,
                };
            }
        }

        return {
            passerer: false,
            info: {
                intlKey: 'uttaksplan.validering.feil.sluttMedUtsettelse',
            },
        };
    }
}
