import { Locale } from '@navikt/fp-common';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';
import IkkeMyndig from 'app/pages/ikkeMyndig/IkkeMyndig';
import Velkommen from 'app/pages/velkommen/Velkommen';
import AnnenForelder from 'app/steps/annen-forelder/AnnenForelder';
import Inntektsinformasjon from 'app/steps/inntektsinformasjon/Inntektsinformasjon';
import OmBarnet from 'app/steps/om-barnet/OmBarnet';
import Oppsummering from 'app/steps/oppsummering/Oppsummering';
import Skersituasjon from 'app/steps/skersituasjon/Skersituasjon';
import Utenlandsopphold from 'app/steps/utenlandsopphold/Utenlandsopphold';
import UttaksplanInfo from 'app/steps/uttaksplan-info/UttaksplanInfo';
import UttaksplanStep from 'app/steps/uttaksplan/UttaksplanStep';
import React, { FunctionComponent, useEffect, useState } from 'react';
import { Route, useNavigate, Navigate, Routes, useLocation } from 'react-router-dom';
import SknadSendt from '../pages/sknadSendt/SknadSendt';
import isAvailable from './isAvailable';
import SknadRoutes from './routes';

interface Props {
    fornavn: string;
    locale: Locale;
    onChangeLocale: (locale: Locale) => void;
    currentRoute: SknadRoutes;
}

const renderSknadRoutes = (harGodkjentVilkr: boolean, erEndringssknad: boolean, skerErMyndig: boolean) => {
    if (!harGodkjentVilkr) {
        return <Route path="*" element={<Navigate to={SknadRoutes.VELKOMMEN} />} />;
    }

    if (!skerErMyndig) {
        return <Route path="*" element={<Navigate to={SknadRoutes.IKKE_MYNDIG} />} />;
    }

    if (erEndringssknad) {
        return (
            <>
                <Route path={SknadRoutes.UTTAKSPLAN} element={<UttaksplanStep />} />
                <Route path={SknadRoutes.OPPSUMMERING} element={<Oppsummering />} />
                <Route path={SknadRoutes.SKNAD_SENDT} element={<SknadSendt />} />
            </>
        );
    }

    return (
        <>
            <Route path={SknadRoutes.SKERSITUASJON} element={<Skersituasjon />} />
            <Route path={SknadRoutes.OM_BARNET} element={<OmBarnet />} />
            <Route path={SknadRoutes.ANNEN_FORELDER} element={<AnnenForelder />} />
            <Route path={SknadRoutes.UTTAKSPLAN_INFO} element={<UttaksplanInfo />} />
            <Route path={SknadRoutes.UTTAKSPLAN} element={<UttaksplanStep />} />
            <Route path={SknadRoutes.UTENLANDSOPPHOLD} element={<Utenlandsopphold />} />
            <Route path={SknadRoutes.INNTEKTSINFORMASJON} element={<Inntektsinformasjon />} />
            <Route path={SknadRoutes.OPPSUMMERING} element={<Oppsummering />} />
            <Route path={SknadRoutes.SKNAD_SENDT} element={<SknadSendt />} />
        </>
    );
};

const ForeldrepengesknadRoutes: FunctionComponent<Props> = ({ fornavn, locale, onChangeLocale, currentRoute }) => {
    const { state } = useForeldrepengesknadContext();
    const navigate = useNavigate();
    const location = useLocation();
    const harGodkjentVilkr = state.sknad.harGodkjentVilkr;
    const erMyndig = state.skerinfo.person.erMyndig;
    const [isFirstTimeLoadingApp, setIsFirstTimeLoadingApp] = useState(true);

    useEffect(() => {
        if (currentRoute && erMyndig && harGodkjentVilkr && isFirstTimeLoadingApp) {
            setIsFirstTimeLoadingApp(false);
            if (isAvailable(currentRoute, state.sknad)) {
                navigate(currentRoute);
            } else {
                if (location.pathname === SknadRoutes.OPPSUMMERING) {
                    navigate(SknadRoutes.UTTAKSPLAN);
                }
            }
        }
    }, [currentRoute, erMyndig, harGodkjentVilkr, navigate, isFirstTimeLoadingApp, state.sknad, location.pathname]);

    return (
        <Routes>
            <Route
                path={SknadRoutes.VELKOMMEN}
                element={
                    <Velkommen
                        fornavn={fornavn}
                        locale={locale}
                        saker={state.saker}
                        onChangeLocale={onChangeLocale}
                        fnr={state.skerinfo.person.fnr}
                    />
                }
            />
            <Route path={SknadRoutes.IKKE_MYNDIG} element={<IkkeMyndig fornavn={state.skerinfo.person.fornavn} />} />

            {renderSknadRoutes(harGodkjentVilkr, state.sknad.erEndringssknad, erMyndig)}
        </Routes>
    );
};

export default ForeldrepengesknadRoutes;
