import AnnenForelder from 'app/context/types/AnnenForelder';
import { harAnnenForelderRettIES } from 'app/utils/annenForelderUtils';
import { Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';

const overfringsrsakSkalBesvares = (
    periodetype: Periodetype,
    erFarEllerMedmor: boolean,
    kontoValue: StnadskontoType,
    annenForelder: AnnenForelder
): boolean => {
    const annenForelderHarRettIES = harAnnenForelderRettIES(annenForelder);
    return (
        periodetype === Periodetype.Overfring ||
        (annenForelderHarRettIES && erFarEllerMedmor && kontoValue === StnadskontoType.Mdrekvote) ||
        (annenForelderHarRettIES && !erFarEllerMedmor && kontoValue === StnadskontoType.Fedrekvote)
    );
};

export default overfringsrsakSkalBesvares;
