import React from 'react';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { erUttaksmengdeForFarMedmorForHyTest } from './erUttaksmengdeForFarMedmorForHyTest';
import links from 'app/links/links';
import { IntlShape } from 'react-intl';
import {
    getBareFarHarRettKontoUtenAktivitetskravUker,
    getBareFarHarRettAntallUkerPTaUtDagerUtenAktivitetskravFrWLB,
} from 'app/utils/minsterettUtils';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';

export const farMedmorHarRettPForeldrepengerUtenAktivitetskravTest: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    const tattUtForMangeDagerIPlanen = erUttaksmengdeForFarMedmorForHyTest(grunnlag).passerer === false;
    if (
        !grunnlag.skerErFarEllerMedmor ||
        grunnlag.morHarRett ||
        tattUtForMangeDagerIPlanen ||
        (grunnlag.skerErFarEllerMedmor && grunnlag.skerErAleneOmOmsorg)
    ) {
        return {
            passerer: true,
        };
    }
    const kontoUtenAktivitetskravUker = getBareFarHarRettKontoUtenAktivitetskravUker(
        grunnlag.antallBarn,
        grunnlag.morErUfr,
        grunnlag.familiehendelsesdato,
        grunnlag.dekningsgrad,
        !grunnlag.morHarRett
    );

    const testPasserer = kontoUtenAktivitetskravUker === 0;
    const renderAsHtml = true;
    const link = (_intl: IntlShape) => (msg: any) =>
        (
            <a href={links.aktivitetsfriUttakInfo} className="lenke" rel="noreferrer" target="_blank">
                {msg}
            </a>
        );

    if (andreAugust2022ReglerGjelder(grunnlag.familiehendelsesdato)) {
        return {
            passerer: testPasserer,
            info: {
                intlKey: 'uttaksplan.validering.info.rettTilAktivitetsfriUttak.etterWLB',
                renderAsHtml: renderAsHtml,
                values: {
                    antallUker: kontoUtenAktivitetskravUker,
                    a: link,
                },
            },
        };
    } else {
        const bareFarHarRett = grunnlag.skerErFarEllerMedmor && !grunnlag.morHarRett;
        const antallUkerPTaUtDager = getBareFarHarRettAntallUkerPTaUtDagerUtenAktivitetskravFrWLB(
            grunnlag.antallBarn,
            grunnlag.familiehendelsesdato,
            grunnlag.dekningsgrad,
            bareFarHarRett
        );
        return {
            passerer: testPasserer,
            info: {
                intlKey: 'uttaksplan.validering.info.rettTilAktivitetsfriUttak.frWLB',
                renderAsHtml: renderAsHtml,
                values: {
                    antallUker: kontoUtenAktivitetskravUker,
                    antallUkerTaUtDagerUtenAktivitet: antallUkerPTaUtDager,
                    a: link,
                },
            },
        };
    }
};
