import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import { convertBooleanOrUndefinedToYesOrNo, convertYesOrNoOrUndefinedToBoolean } from 'app/utils/formUtils';
import { UttaksplanFormData, UttaksplanFormField } from './UttaksplanFormConfig';

export const cleanUttaksplanFormData = (
    values: UttaksplanFormData,
    visibility: QuestionVisibility<UttaksplanFormField, undefined>
): UttaksplanFormData => {
    const cleanedData: UttaksplanFormData = {
        nskerAutomatiskJustering: visibility.isVisible(UttaksplanFormField.nskerAutomatiskJustering)
            ? values.nskerAutomatiskJustering
            : YesOrNo.UNANSWERED,
    };

    return cleanedData;
};

export const mapUttaksplanFormToState = (values: Partial<UttaksplanFormData>): boolean | undefined => {
    return convertYesOrNoOrUndefinedToBoolean(values.nskerAutomatiskJustering);
};

export const mapUttaksplanFormValueToState = (value: YesOrNo): boolean | undefined => {
    return convertYesOrNoOrUndefinedToBoolean(value);
};

export const getUttaksplanFormInitialValues = (nskerAutomatiskJustering: boolean | undefined): UttaksplanFormData => {
    return {
        nskerAutomatiskJustering:
            nskerAutomatiskJustering !== undefined
                ? convertBooleanOrUndefinedToYesOrNo(nskerAutomatiskJustering)
                : YesOrNo.UNANSWERED,
    };
};
