import { Block, hasValue, intlUtils } from '@navikt/fp-common';
import { UttakRundtFdselrsak } from 'app/types/UttakRundtFdselrsak';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import { PeriodeUttakFormComponents, PeriodeUttakFormField } from '../../periode-uttak-form/periodeUttakFormConfig';

import { Attachment } from 'app/types/Attachment';
import MorErForSykDokumentasjonOpplastning from '../er-mor-for-syk-dokumentasjon/MorErForSykDokumentasjonOpplastning';

interface Props {
    fieldName: PeriodeUttakFormField;
    uttakRundtFdselrsak: UttakRundtFdselrsak | '';
    vedlegg: Attachment[];
    navnMor: string;
}

const UttakRundtFdselrsakSprsml: FunctionComponent<Props> = ({
    fieldName,
    uttakRundtFdselrsak,
    navnMor,
    vedlegg,
}) => {
    const intl = useIntl();
    const radios = [
        {
            label: intlUtils(intl, 'ja'),
            value: UttakRundtFdselrsak.samtidigUttak,
        },
        {
            label: intlUtils(intl, 'uttaksplan.uttakrundtFdselrsak.nei.morErForSyk'),
            value: UttakRundtFdselrsak.morErForSyk,
        },
    ];

    return (
        <>
            <Block padBottom="l">
                <PeriodeUttakFormComponents.RadioPanelGroup
                    legend={intlUtils(intl, 'uttaksplan.uttakrundtFdselrsak.sprsml')}
                    name={fieldName}
                    radios={radios}
                    useTwoColumns={true}
                    validate={(value) => {
                        if (!hasValue(value)) {
                            return intlUtils(intl, 'uttaksplan.validering.uttakRundtFdselFarMedmorWLB');
                        }
                    }}
                />
            </Block>
            <Block padBottom="l" visible={uttakRundtFdselrsak === UttakRundtFdselrsak.morErForSyk}>
                <MorErForSykDokumentasjonOpplastning navnMor={navnMor} vedlegg={vedlegg} />
            </Block>
        </>
    );
};

export default UttakRundtFdselrsakSprsml;
