import { getTidsperiode, Tidsperioden } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { Periodene } from 'app/steps/uttaksplan-info/utils/Periodene';
import { Forelder } from 'app/types/Forelder';
import { Situasjon } from 'app/types/Situasjon';
import { isPeriodeUtenUttak, Periode, Periodetype, Utsettelsesperiode, Uttaksperiode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { uttaksdatoer } from 'uttaksplan/utils/uttaksdatoerUtils';
import { UtsettelsersakType } from 'uttaksplan/types/UtsettelsersakType';
import { MorsAktivitet } from 'uttaksplan/types/MorsAktivitet';
import { OverfringrsakType } from 'uttaksplan/types/OverfringrsakType';
import { erPeriodeFrDato } from 'uttaksplan/utils/periodeUtils';
import AnnenForelder from 'app/context/types/AnnenForelder';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';

const ANTALL_UTTAKSDAGER_SEKS_UKER = 30;

export interface InformasjonOmTaptUttakVedUttakEtterSeksUkerFarMedmor {
    antallUttaksdagerTapt: number;
    frsteRegistrerteUttaksdag: Date;
    sisteUttaksdagInnenforSeksUker: Date;
}

export const getInformasjonOmTaptUttakVedUttakEtterSeksUkerFarMedmor = (
    uttaksplan: Periode[],
    familiehendelsesdato: Date,
    skerErFarEllerMedmor: boolean,
    bareFarMedmorHarRett: boolean,
    morErUfr: boolean,
    skerErFarEllerMedmorOgAnnenForelderKanIkkeOppgis: boolean,
    skerErFarEllerMedmorOgErAleneOmOmsorgen: boolean
): InformasjonOmTaptUttakVedUttakEtterSeksUkerFarMedmor | undefined => {
    if (skerErFarEllerMedmor === false || bareFarMedmorHarRett === false || morErUfr === true) {
        return undefined;
    }

    if (skerErFarEllerMedmorOgAnnenForelderKanIkkeOppgis || skerErFarEllerMedmorOgErAleneOmOmsorgen) {
        return undefined;
    }

    const frstePeriode = Periodene(uttaksplan).getFrstePerioderEtterFamiliehendelsesdato(familiehendelsesdato);
    if (frstePeriode === undefined) {
        return undefined;
    }

    const frsteUttaksdag = Uttaksdagen(familiehendelsesdato).denneEllerNeste();
    const frsteRegistrerteUttaksdag = frstePeriode.tidsperiode.fom;
    const sisteUttaksdagInnenforSeksUker = getTidsperiode(frsteUttaksdag, 30).tom;
    const antallUttaksdager =
        Tidsperioden({
            fom: frsteUttaksdag,
            tom: frsteRegistrerteUttaksdag,
        }).getAntallUttaksdager() - 1;

    if (antallUttaksdager === undefined || antallUttaksdager <= ANTALL_UTTAKSDAGER_SEKS_UKER) {
        return undefined;
    }

    return {
        antallUttaksdagerTapt: antallUttaksdager - ANTALL_UTTAKSDAGER_SEKS_UKER,
        frsteRegistrerteUttaksdag,
        sisteUttaksdagInnenforSeksUker,
    };
};

export const getUgyldigUttakMor = (
    perioder: Periode[],
    familiehendelsesdato: Date,
    situasjon: Situasjon,
    erFarEllerMedmor: boolean,
    termindato: Date | undefined,
    flerbarnsFdsel?: boolean,
    value?: string
): Periode[] => {
    if (situasjon == 'adopsjon') {
        return [];
    }

    const frsteUttaksdag = uttaksdatoer(
        familiehendelsesdato,
        erFarEllerMedmor,
        termindato
    ).frsteUttaksdagPEllerEtterFdsel;
    const frsteUttaksdagEtterSeksUker = Uttaksdagen(frsteUttaksdag).leggTil(30);
    const frsteUttaksdagEttertteUker = Uttaksdagen(frsteUttaksdag).leggTil(40);

    let ugyldigUttakRegelFrsteDato: Date;
    let ugyldigUttakRegelSisteDato: Date;

    if (value === 'frsteSeksUkerForMor') {
        ugyldigUttakRegelFrsteDato = frsteUttaksdag;
        ugyldigUttakRegelSisteDato = frsteUttaksdagEtterSeksUker;
    } else if (value === 'mellomSyvOgtteUkerForMor') {
        ugyldigUttakRegelFrsteDato = frsteUttaksdagEtterSeksUker;
        ugyldigUttakRegelSisteDato = frsteUttaksdagEttertteUker;
    }

    const ugyldigPeriode = Periodene(perioder)
        .getPerioderEtterFamiliehendelsesdato(ugyldigUttakRegelFrsteDato!)
        .filter((p) => erPeriodeFrDato(p, ugyldigUttakRegelSisteDato));

    let ugyldigeUtsettelser: Utsettelsesperiode[] = [];

    if (value === 'mellomSyvOgtteUkerForMor') {
        ugyldigeUtsettelser = Periodene(ugyldigPeriode)
            .getUtsettelser()
            .filter(
                (p) =>
                    p.forelder === Forelder.mor &&
                    (p.rsak === UtsettelsersakType.Ferie || p.rsak === UtsettelsersakType.Arbeid)
            );
    } else if (value === 'frsteSeksUkerForMor') {
        ugyldigeUtsettelser = Periodene(ugyldigPeriode)
            .getUtsettelser()
            .filter(
                (p) =>
                    p.forelder === Forelder.mor &&
                    p.rsak !== UtsettelsersakType.InstitusjonSker &&
                    p.rsak !== UtsettelsersakType.InstitusjonBarnet &&
                    p.rsak !== UtsettelsersakType.Sykdom
            );
    }
    const gradertePerioder = Periodene(ugyldigPeriode)
        .getUttak()
        .filter((p) => p.forelder === Forelder.mor && p.gradert === true);

    const flernbarnsPerioder = Periodene(ugyldigPeriode)
        .getUttak()
        .filter((p) => p.forelder === Forelder.mor && p.nskerFlerbarnsdager === true);

    const fellesPerioder = Periodene(ugyldigPeriode)
        .getUttak()
        .filter((p) => p.forelder === Forelder.mor && p.konto === StnadskontoType.Fellesperiode);

    let samtidigUttaksperioder: Uttaksperiode[] = [];

    if (!flerbarnsFdsel && flerbarnsFdsel !== undefined && !andreAugust2022ReglerGjelder(familiehendelsesdato)) {
        samtidigUttaksperioder = Periodene(ugyldigPeriode)
            .getUttak()
            .filter((p) => p.forelder === Forelder.mor && p.nskerSamtidigUttak);
    }

    return [
        ...flernbarnsPerioder,
        ...gradertePerioder,
        ...ugyldigeUtsettelser,
        ...fellesPerioder,
        ...samtidigUttaksperioder,
    ];
};

export const unntakFarFrsteSeksUker = (periode: Uttaksperiode, harMidlertidigOmsorg: boolean) => ({
    erMorForSykDeFrsteSeksUker: (): boolean => {
        if (harMidlertidigOmsorg) {
            return true;
        }

        if (periode.konto === StnadskontoType.Fellesperiode || periode.konto === StnadskontoType.Foreldrepenger) {
            return (
                periode.morsAktivitetIPerioden === MorsAktivitet.Innlagt ||
                periode.morsAktivitetIPerioden === MorsAktivitet.TrengerHjelp ||
                periode.erMorForSyk === true
            );
        }

        if (periode.konto === StnadskontoType.Fedrekvote || periode.konto === StnadskontoType.AktivitetsfriKvote) {
            return periode.erMorForSyk === true;
        }

        return false;
    },
    erFlerbarnsukerOgUttakAvFlerbarnsdagerEllerFedrekvote: (antallBarn: number): boolean => {
        return antallBarn > 1 && periode.nskerFlerbarnsdager === true;
    },
});

const erFarsUttakFrsteSeksUkerGyldig = (
    periode: Uttaksperiode,
    antallBarn: number,
    harMidlertidigOmsorg: boolean
): boolean => {
    const unntak = unntakFarFrsteSeksUker(periode, harMidlertidigOmsorg);
    return (
        unntak.erMorForSykDeFrsteSeksUker() || unntak.erFlerbarnsukerOgUttakAvFlerbarnsdagerEllerFedrekvote(antallBarn)
    );
};

export const getUgyldigUttakFrsteSeksUkerForFarMedmor = (
    perioder: Periode[],
    familiehendelsesdato: Date,
    antallBarn: number,
    situasjon: Situasjon,
    annenForelder: AnnenForelder,
    erAleneOmOmsorg: boolean,
    harMidlertidigOmsorg: boolean,
    erFarEllerMedmor: boolean,
    termindato: Date | undefined
): Periode[] => {
    if (situasjon === 'adopsjon' || annenForelder.kanIkkeOppgis || erAleneOmOmsorg) {
        return [];
    }

    const frsteUttaksdag = uttaksdatoer(
        familiehendelsesdato,
        erFarEllerMedmor,
        termindato
    ).frsteUttaksdagPEllerEtterFdsel;
    const frsteUttaksdagEtterSeksUker = Uttaksdagen(frsteUttaksdag).leggTil(30);

    const farsPerioderInnenforSeksFrsteUker = Periodene(perioder)
        .getPerioderEtterFamiliehendelsesdato(familiehendelsesdato)
        .filter((p) => erPeriodeFrDato(p, frsteUttaksdagEtterSeksUker))
        .filter((p) => p.type !== Periodetype.Hull && !isPeriodeUtenUttak(p) && p.forelder === Forelder.farMedmor);

    const ugyldigeUttak = Periodene(farsPerioderInnenforSeksFrsteUker)
        .getUttak()
        .filter((p) => erFarsUttakFrsteSeksUkerGyldig(p, antallBarn, harMidlertidigOmsorg) === false);

    const ugyldigeOverfringer = Periodene(farsPerioderInnenforSeksFrsteUker)
        .getOverfringer()
        .filter(
            (p) =>
                p.rsak !== OverfringrsakType.institusjonsoppholdAnnenForelder &&
                p.rsak !== OverfringrsakType.sykdomAnnenForelder
        );

    const ugyldigeUtsettelser = Periodene(farsPerioderInnenforSeksFrsteUker)
        .getUtsettelser()
        .filter((utsettelse) => utsettelse.rsak !== UtsettelsersakType.InstitusjonBarnet);

    return [...ugyldigeUttak, ...ugyldigeOverfringer, ...ugyldigeUtsettelser];
};
