import { TidsperiodeDate } from '@navikt/fp-common';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';
import dayjs from 'dayjs';
import { Periodetype } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { UttakRundtFdselrsak } from 'app/types/UttakRundtFdselrsak';

export const nskerFlerbarnsdagerSkalBesvares = (
    periodetype: Periodetype,
    erFlerbarnssknad: boolean,
    skerErFarEllerMedmor: boolean,
    familiehendelsesdato: Date,
    tidsperiode: TidsperiodeDate,
    stnadskontoType: StnadskontoType,
    bareFarHarRett: boolean,
    antallBarn: number,
    erAleneOmOmsorg: boolean,
    erDeltUttakINorge: boolean,
    uttakRundtFdselrsak: UttakRundtFdselrsak | undefined
): boolean => {
    if (dayjs(tidsperiode.fom).isBefore(familiehendelsesdato, 'day')) {
        return false;
    }

    if (
        stnadskontoType === StnadskontoType.AktivitetsfriKvote ||
        uttakRundtFdselrsak === UttakRundtFdselrsak.morErForSyk
    ) {
        return false;
    }
    if (skerErFarEllerMedmor && (bareFarHarRett || erAleneOmOmsorg) && antallBarn > 1) {
        if (andreAugust2022ReglerGjelder(familiehendelsesdato)) {
            return false;
        }
        return true;
    }
    if (!erDeltUttakINorge && stnadskontoType !== StnadskontoType.Fellesperiode) {
        return false;
    }
    return periodetype === Periodetype.Uttak && erFlerbarnssknad && skerErFarEllerMedmor;
};
