import { getSisteUttaksdag6UkerEtterFdsel } from 'app/utils/wlbUtils';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import dayjs from 'dayjs';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';
import { isUttaksperiode } from 'uttaksplan/types/Periode';

export const harMorSktMindreEnn100ProsentSamtidigUttakDeFrsteSeksUkerTest: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    const skerErMor = !grunnlag.skerErFarEllerMedmor;
    if (skerErMor && grunnlag.skersituasjon.situasjon === 'fdsel') {
        const sisteUttaksdagEtterSeksUker = getSisteUttaksdag6UkerEtterFdsel(grunnlag.familiehendelsesdato);
        const perioderMedSamtidigUttakUnder100ProsentInnenFrsteSeksUker = grunnlag.perioder.filter(
            (p) =>
                isUttaksperiode(p) &&
                dayjs(p.tidsperiode.fom).isSameOrBefore(sisteUttaksdagEtterSeksUker, 'day') &&
                p.nskerSamtidigUttak === true &&
                p.samtidigUttakProsent !== undefined &&
                parseInt(p.samtidigUttakProsent, 10) < 100
        );

        return {
            passerer: perioderMedSamtidigUttakUnder100ProsentInnenFrsteSeksUker.length <= 0,
            info: {
                intlKey: 'uttaksplan.validering.feil.morHarSktMindreEnn100ProsentSamtidigUttakDeFrsteSeksUker',
            },
        };
    }
    return { passerer: true };
};
