import { hasValue } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import { Attachment } from 'app/types/Attachment';
import { AttachmentType } from 'app/types/AttachmentType';
import { Forelder } from 'app/types/Forelder';
import { Situasjon } from 'app/types/Situasjon';
import { Skjemanummer } from 'app/types/Skjemanummer';
import { UttakRundtFdselrsak } from 'app/types/UttakRundtFdselrsak';
import { convertBooleanOrUndefinedToYesOrNo, convertYesOrNoOrUndefinedToBoolean } from 'app/utils/formUtils';
import { getMorsAktivitet, getMorsAktivitetSkjemanummer } from 'app/utils/morsAktivitetUtils';
import { trimNumberValue } from 'app/utils/numberUtils';
import { lagSendSenereDokumentNrIngenAndreFinnes } from 'app/utils/vedleggUtils';
import dayjs from 'dayjs';
import { MorsAktivitet } from 'uttaksplan/types/MorsAktivitet';
import { OverfringrsakType } from 'uttaksplan/types/OverfringrsakType';
import {
    Arbeidsform,
    isOppholdsperiode,
    isOverfringsperiode,
    isUttaksperiode,
    Oppholdsperiode,
    Overfringsperiode,
    Periode,
    Periodetype,
    Uttaksperiode,
} from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { getOppholdsrsakFromStnadskonto, getStnadskontoFromOppholdsrsak } from 'uttaksplan/utils/periodeUtils';
import { PeriodeUttakFormData, PeriodeUttakFormField } from './periodeUttakFormConfig';
import {
    erSamtidigUttakFarMedmorFrFdselWLB,
    erSamtidigUttakFarMedmorFrFrsteSeksUkerWLB,
} from './periodeUttakFormQuestionsConfig';

const getInitialKonto = (
    erDeltUttak: boolean,
    erMorUfr: boolean,
    periodenStarterFrFamdato: boolean,
    erFarEllerMedmor: boolean
) => {
    if (erDeltUttak) {
        if (periodenStarterFrFamdato && erFarEllerMedmor) {
            return StnadskontoType.Fedrekvote;
        }

        return '';
    }

    if (erFarEllerMedmor && periodenStarterFrFamdato) {
        return StnadskontoType.AktivitetsfriKvote;
    }

    if (erMorUfr) {
        return '';
    }

    return StnadskontoType.Foreldrepenger;
};

const getHvemSkalTaUttak = (
    erDeltUttak: boolean,
    forelder: Forelder,
    periodenStarterFrFamdato: boolean,
    erFarEllerMedmor: boolean,
    annenForelderHarRettIES: boolean
) => {
    if (erDeltUttak) {
        if ((periodenStarterFrFamdato || annenForelderHarRettIES) && erFarEllerMedmor) {
            return Forelder.farMedmor;
        }
        if (annenForelderHarRettIES) {
            return erFarEllerMedmor ? Forelder.farMedmor : Forelder.mor;
        }

        return '';
    }

    return forelder;
};

const getInitialValues = (
    erDeltUttak: boolean,
    forelder: Forelder,
    erMorUfr: boolean,
    familiehendelsesdato: Date,
    startdatoPeriode: Date | undefined,
    erFarEllerMedmor: boolean,
    annenForelderHarRettIES: boolean
): PeriodeUttakFormData => {
    const periodenStarterFrFamdato = startdatoPeriode
        ? dayjs(startdatoPeriode).isBefore(familiehendelsesdato, 'day')
        : false;
    const hvemSkalTaUttak = getHvemSkalTaUttak(
        erDeltUttak,
        forelder,
        periodenStarterFrFamdato,
        erFarEllerMedmor,
        annenForelderHarRettIES
    );
    const konto = getInitialKonto(erDeltUttak, erMorUfr, periodenStarterFrFamdato, erFarEllerMedmor);

    return {
        [PeriodeUttakFormField.fom]: undefined,
        [PeriodeUttakFormField.tom]: undefined,
        [PeriodeUttakFormField.konto]: konto,
        [PeriodeUttakFormField.samtidigUttak]: YesOrNo.UNANSWERED,
        [PeriodeUttakFormField.aktivitetskravMor]: '',
        [PeriodeUttakFormField.aktivitetskravMorDokumentasjon]: [],
        [PeriodeUttakFormField.overfringsrsak]: '',
        [PeriodeUttakFormField.overfringsdokumentasjon]: [],
        [PeriodeUttakFormField.skalHaGradering]: YesOrNo.UNANSWERED,
        [PeriodeUttakFormField.stillingsprosent]: '',
        [PeriodeUttakFormField.arbeidsformer]: '',
        [PeriodeUttakFormField.erMorForSyk]: YesOrNo.UNANSWERED,
        [PeriodeUttakFormField.erMorForSykDokumentasjon]: [],
        [PeriodeUttakFormField.uttakRundtFdselrsak]: '',
        [PeriodeUttakFormField.samtidigUttakProsent]: '',
        [PeriodeUttakFormField.hvemSkalTaUttak]: hvemSkalTaUttak,
        [PeriodeUttakFormField.nskerFlerbarnsdager]: YesOrNo.UNANSWERED,
    };
};

export const cleanPeriodeUttakFormData = (
    values: PeriodeUttakFormData,
    visibility: QuestionVisibility<PeriodeUttakFormField, undefined>,
    erDeltUttak: boolean,
    forelder: Forelder,
    erMorUfr: boolean,
    familiehendelsesdato: Date,
    erFarEllerMedmor: boolean,
    annenForelderHarRettIES: boolean
): PeriodeUttakFormData => {
    const initialValues = getInitialValues(
        erDeltUttak,
        forelder,
        erMorUfr,
        familiehendelsesdato,
        values.fom,
        erFarEllerMedmor,
        annenForelderHarRettIES
    );

    const cleanedData: PeriodeUttakFormData = {
        fom: values.fom,
        tom: values.tom,
        hvemSkalTaUttak: values.hvemSkalTaUttak,
        aktivitetskravMor: visibility.isVisible(PeriodeUttakFormField.aktivitetskravMor)
            ? values.aktivitetskravMor
            : '',
        aktivitetskravMorDokumentasjon: visibility.isVisible(PeriodeUttakFormField.aktivitetskravMorDokumentasjon)
            ? values.aktivitetskravMorDokumentasjon
            : [],
        erMorForSyk: visibility.isVisible(PeriodeUttakFormField.erMorForSyk)
            ? values.erMorForSyk
            : initialValues.erMorForSyk,
        erMorForSykDokumentasjon:
            visibility.isVisible(PeriodeUttakFormField.erMorForSyk) ||
            values.uttakRundtFdselrsak === UttakRundtFdselrsak.morErForSyk
                ? values.erMorForSykDokumentasjon
                : initialValues.erMorForSykDokumentasjon,
        uttakRundtFdselrsak: visibility.isVisible(PeriodeUttakFormField.uttakRundtFdselrsak)
            ? values.uttakRundtFdselrsak
            : initialValues.uttakRundtFdselrsak,
        arbeidsformer: visibility.isVisible(PeriodeUttakFormField.arbeidsformer) ? values.arbeidsformer : '',
        konto: values.konto,
        overfringsdokumentasjon: visibility.isVisible(PeriodeUttakFormField.overfringsdokumentasjon)
            ? values.overfringsdokumentasjon
            : [],
        overfringsrsak: visibility.isVisible(PeriodeUttakFormField.overfringsrsak)
            ? values.overfringsrsak
            : initialValues.overfringsrsak,
        samtidigUttak: visibility.isVisible(PeriodeUttakFormField.samtidigUttak)
            ? values.samtidigUttak
            : initialValues.samtidigUttak,
        samtidigUttakProsent: visibility.isVisible(PeriodeUttakFormField.samtidigUttakProsent)
            ? values.samtidigUttakProsent
            : initialValues.samtidigUttakProsent,
        skalHaGradering: visibility.isVisible(PeriodeUttakFormField.skalHaGradering)
            ? values.skalHaGradering
            : initialValues.skalHaGradering,
        stillingsprosent: visibility.isVisible(PeriodeUttakFormField.stillingsprosent)
            ? values.stillingsprosent
            : initialValues.stillingsprosent,
        nskerFlerbarnsdager: visibility.isVisible(PeriodeUttakFormField.nskerFlerbarnsdager)
            ? values.nskerFlerbarnsdager
            : initialValues.nskerFlerbarnsdager,
    };

    return cleanedData;
};

const getInitialrsakForUttakRundtFdsel = (
    erMorForSyk: boolean | undefined,
    nskerSamtidigUttak: boolean | undefined,
    erDeltUttak: boolean
): UttakRundtFdselrsak | undefined => {
    if (!erDeltUttak) {
        return undefined;
    }
    if (erMorForSyk) {
        return UttakRundtFdselrsak.morErForSyk;
    }
    if (nskerSamtidigUttak) {
        return UttakRundtFdselrsak.samtidigUttak;
    }
    return undefined;
};

export const getPeriodeUttakFormInitialValues = (
    periode: Periode,
    erDeltUttak: boolean,
    forelder: Forelder,
    erMorUfr: boolean,
    familiehendelsesdato: Date,
    erFarEllerMedmor: boolean,
    annenForelderHarRettIES: boolean
): PeriodeUttakFormData => {
    const initialValues = getInitialValues(
        erDeltUttak,
        forelder,
        erMorUfr,
        familiehendelsesdato,
        periode.tidsperiode.fom,
        erFarEllerMedmor,
        annenForelderHarRettIES
    );

    if (periode !== undefined) {
        if (isUttaksperiode(periode)) {
            return {
                ...initialValues,
                fom: periode.tidsperiode.fom,
                tom: periode.tidsperiode.tom,
                aktivitetskravMor: periode.morsAktivitetIPerioden || '',
                aktivitetskravMorDokumentasjon: periode.vedlegg || [],
                erMorForSyk: convertBooleanOrUndefinedToYesOrNo(periode.erMorForSyk),
                erMorForSykDokumentasjon: periode.vedlegg || [],
                hvemSkalTaUttak: periode.forelder || initialValues.hvemSkalTaUttak,
                arbeidsformer:
                    periode.arbeidsformer && periode.arbeidsformer.length > 0
                        ? getFrilansSNEllerOrgnr(periode.arbeidsformer, periode.orgnumre)
                        : '',
                konto: periode.konto || initialValues.konto,
                samtidigUttak: convertBooleanOrUndefinedToYesOrNo(periode.nskerSamtidigUttak),
                samtidigUttakProsent: periode.samtidigUttakProsent || '',
                skalHaGradering: convertBooleanOrUndefinedToYesOrNo(periode.gradert),
                stillingsprosent: periode.stillingsprosent || '',
                nskerFlerbarnsdager: convertBooleanOrUndefinedToYesOrNo(periode.nskerFlerbarnsdager),
                uttakRundtFdselrsak:
                    getInitialrsakForUttakRundtFdsel(periode.erMorForSyk, periode.nskerSamtidigUttak, erDeltUttak) ||
                    initialValues.uttakRundtFdselrsak,
            };
        }

        if (isOverfringsperiode(periode)) {
            return {
                ...initialValues,
                hvemSkalTaUttak: periode.forelder,
                konto: periode.konto,
                fom: periode.tidsperiode.fom,
                tom: periode.tidsperiode.tom,
                overfringsrsak: periode.rsak,
                overfringsdokumentasjon: periode.vedlegg || [],
            };
        }

        if (isOppholdsperiode(periode)) {
            return {
                ...initialValues,
                hvemSkalTaUttak: periode.forelder,
                konto: getStnadskontoFromOppholdsrsak(periode.rsak),
                fom: periode.tidsperiode.fom,
                tom: periode.tidsperiode.tom,
            };
        }

        return {
            ...initialValues,
            fom: periode.tidsperiode.fom,
            tom: periode.tidsperiode.tom,
        };
    }

    return {
        ...initialValues,
    };
};

const getFrilansSNEllerOrgnr = (arbeidsformer: Arbeidsform[], orgnumre?: string[]): Arbeidsform => {
    if (arbeidsformer.includes(Arbeidsform.frilans) || arbeidsformer.includes(Arbeidsform.selvstendignringsdrivende)) {
        return arbeidsformer[0];
    }

    return orgnumre![0] as Arbeidsform;
};

const getArbeidsform = (arbeidsformer: Arbeidsform[]): Arbeidsform[] => {
    return arbeidsformer.includes(Arbeidsform.frilans) || arbeidsformer.includes(Arbeidsform.selvstendignringsdrivende)
        ? arbeidsformer
        : [Arbeidsform.arbeidstaker];
};

const getOrgnummer = (arbeidsformer: Arbeidsform[]): string[] => {
    return arbeidsformer.includes(Arbeidsform.frilans) || arbeidsformer.includes(Arbeidsform.selvstendignringsdrivende)
        ? []
        : arbeidsformer;
};

const getErArbeidstaker = (arbeidsformer: Arbeidsform[]): boolean => {
    return (
        arbeidsformer.includes(Arbeidsform.frilans) || arbeidsformer.includes(Arbeidsform.selvstendignringsdrivende)
    );
};

const getSamtidigUttaksProsentWLB = (
    nskerGradering: boolean | undefined,
    dekningsgrad: string | undefined
): string => {
    return nskerGradering && dekningsgrad !== undefined
        ? (100 - parseFloat(trimNumberValue(dekningsgrad))).toString()
        : '100';
};

const velgVedleggSomSkalBrukes = (
    aktivitetskravMorDokumentasjon: Attachment[],
    erMorForSykDokumentasjon: Attachment[]
): Attachment[] => {
    if (aktivitetskravMorDokumentasjon.length > 0) {
        return aktivitetskravMorDokumentasjon;
    }

    return erMorForSykDokumentasjon;
};

const skalVedleggPkreves = (
    morsAktivitetIPerioden: '' | MorsAktivitet | undefined,
    erMorForSyk: boolean,
    uttakRundtFdselrsak: UttakRundtFdselrsak | '' | undefined
): boolean => {
    if (
        hasValue(morsAktivitetIPerioden) ||
        erMorForSyk ||
        (uttakRundtFdselrsak !== undefined && uttakRundtFdselrsak === UttakRundtFdselrsak.morErForSyk)
    ) {
        return true;
    }

    return false;
};

const getKontoVerdi = (
    samtidigWLBUttakFrFdselFarMedmor: boolean,
    erFarEllerMedmor: boolean,
    erDeltUttak: boolean,
    startDato: Date,
    inputKonto: StnadskontoType,
    familiehendelsesdato: Date
): StnadskontoType => {
    if (samtidigWLBUttakFrFdselFarMedmor) {
        return StnadskontoType.Fedrekvote;
    }
    if (!erDeltUttak && erFarEllerMedmor && dayjs(startDato).isBefore(familiehendelsesdato, 'day')) {
        return StnadskontoType.AktivitetsfriKvote;
    }

    return inputKonto;
};

export const mapPeriodeUttakFormToPeriode = (
    values: Partial<PeriodeUttakFormData>,
    id: string,
    type: Periodetype,
    familiehendelsesdato: Date,
    erFarEllerMedmor: boolean,
    erDeltUttak: boolean,
    situasjon: Situasjon
): Periode => {
    if (type === Periodetype.Overfring) {
        const overfringTrengerDokumentasjon =
            values.overfringsrsak !== OverfringrsakType.aleneomsorg &&
            values.overfringsrsak !== OverfringrsakType.ikkeRettAnnenForelder;
        const vedlegg = overfringTrengerDokumentasjon
            ? lagSendSenereDokumentNrIngenAndreFinnes(
                  values.overfringsdokumentasjon!,
                  AttachmentType.OVERFRING_KVOTE,
                  Skjemanummer.DOK_OVERFRING_FOR_SYK
              )
            : undefined;
        const periode: Overfringsperiode = {
            id,
            type,
            forelder: values.hvemSkalTaUttak as Forelder,
            konto: values.konto as StnadskontoType,
            tidsperiode: {
                fom: values.fom!,
                tom: values.tom!,
            },
            rsak: values.overfringsrsak as OverfringrsakType,
            vedlegg,
        };

        return periode;
    }

    if (type === Periodetype.Opphold) {
        const periode: Oppholdsperiode = {
            id,
            type,
            forelder: values.hvemSkalTaUttak as Forelder,
            rsak: getOppholdsrsakFromStnadskonto(values.konto as StnadskontoType)!,
            tidsperiode: {
                fom: values.fom!,
                tom: values.tom!,
            },
        };

        return periode;
    }
    const samtidigWLBUttakFrFdselFarMedmor = erSamtidigUttakFarMedmorFrFdselWLB(
        values,
        familiehendelsesdato,
        erFarEllerMedmor,
        erDeltUttak,
        situasjon
    );
    const samtidigWLBUttakFrFrsteSeksUkerFarMedmor = erSamtidigUttakFarMedmorFrFrsteSeksUkerWLB(
        values,
        familiehendelsesdato,
        erFarEllerMedmor,
        erDeltUttak,
        situasjon
    );
    const attachmentType = hasValue(values.aktivitetskravMor)
        ? AttachmentType.MORS_AKTIVITET_DOKUMENTASJON
        : AttachmentType.UTSETTELSE_SYKDOM;

    const skjemanummer = hasValue(values.aktivitetskravMor)
        ? getMorsAktivitetSkjemanummer(values.aktivitetskravMor as MorsAktivitet)
        : Skjemanummer.DOK_MORS_UTDANNING_ARBEID_SYKDOM;

    const relevantVedlegg = velgVedleggSomSkalBrukes(
        values.aktivitetskravMorDokumentasjon!,
        values.erMorForSykDokumentasjon!
    );

    const morErForSyk =
        hasValue(values.uttakRundtFdselrsak) && values.uttakRundtFdselrsak === UttakRundtFdselrsak.morErForSyk
            ? true
            : convertYesOrNoOrUndefinedToBoolean(values.erMorForSyk);

    const erSamtidigUttak =
        values.uttakRundtFdselrsak === UttakRundtFdselrsak.samtidigUttak || samtidigWLBUttakFrFdselFarMedmor
            ? true
            : convertYesOrNoOrUndefinedToBoolean(values.samtidigUttak);

    const samtidigUttakProsentInputVerdi = hasValue(values.samtidigUttakProsent)
        ? trimNumberValue(values.samtidigUttakProsent!)
        : undefined;

    const samtidigUttakProsentVerdi =
        values.uttakRundtFdselrsak === UttakRundtFdselrsak.samtidigUttak || samtidigWLBUttakFrFdselFarMedmor
            ? getSamtidigUttaksProsentWLB(
                  convertYesOrNoOrUndefinedToBoolean(values.skalHaGradering),
                  values.stillingsprosent
              )
            : samtidigUttakProsentInputVerdi;

    const forelderVerdi = samtidigWLBUttakFrFrsteSeksUkerFarMedmor
        ? Forelder.farMedmor
        : (values.hvemSkalTaUttak as Forelder);

    const kontoVerdi = getKontoVerdi(
        samtidigWLBUttakFrFdselFarMedmor,
        erFarEllerMedmor,
        erDeltUttak,
        values.fom!,
        values.konto as StnadskontoType,
        familiehendelsesdato
    );

    const periode: Uttaksperiode = {
        id,
        forelder: forelderVerdi,
        konto: kontoVerdi,
        tidsperiode: {
            fom: values.fom!,
            tom: values.tom!,
        },
        type: Periodetype.Uttak,
        arbeidsformer: hasValue(values.arbeidsformer)
            ? getArbeidsform([values.arbeidsformer as Arbeidsform])
            : undefined,
        morsAktivitetIPerioden: getMorsAktivitet(
            values.aktivitetskravMor,
            convertYesOrNoOrUndefinedToBoolean(values.erMorForSyk)
        ),

        erArbeidstaker: getErArbeidstaker(
            hasValue(values.arbeidsformer) ? getArbeidsform([values.arbeidsformer as Arbeidsform]) : []
        ),
        erMorForSyk: morErForSyk,
        gradert: convertYesOrNoOrUndefinedToBoolean(values.skalHaGradering),
        harIkkeAktivitetskrav: values.konto === StnadskontoType.AktivitetsfriKvote ? true : undefined,
        orgnumre: getOrgnummer(hasValue(values.arbeidsformer) ? [values.arbeidsformer as Arbeidsform] : []),
        stillingsprosent: hasValue(values.stillingsprosent) ? trimNumberValue(values.stillingsprosent!) : undefined,
        nskerFlerbarnsdager: convertYesOrNoOrUndefinedToBoolean(values.nskerFlerbarnsdager),
        nskerSamtidigUttak: erSamtidigUttak,
        samtidigUttakProsent: samtidigUttakProsentVerdi,
        vedlegg: skalVedleggPkreves(
            values.aktivitetskravMor,
            convertYesOrNoOrUndefinedToBoolean(values.erMorForSyk) || false,
            values.uttakRundtFdselrsak
        )
            ? lagSendSenereDokumentNrIngenAndreFinnes(relevantVedlegg, attachmentType, skjemanummer)
            : [],
    };

    return periode;
};
