import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';
import { erPeriodeInnvilget } from 'uttaksplan/utils/periodeUtils';
import { RegelTest, RegelTestresultat } from '../utils/types/regelTypes';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { getUgyldigUttakFrsteSeksUkerForFarMedmor } from '../utils/uttakValideringUtils';

export const harFarMedmorSktUgyldigUttakEllerUtsettelseFrsteSeksUkerTest: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    const wlbReglerGjelder = andreAugust2022ReglerGjelder(grunnlag.familiehendelsesdato);
    if (grunnlag.skerErFarEllerMedmor && !wlbReglerGjelder) {
        const ugyldigePerioder = getUgyldigUttakFrsteSeksUkerForFarMedmor(
            grunnlag.perioder.filter((p) => !erPeriodeInnvilget(p)),
            grunnlag.familiehendelsesdato,
            grunnlag.antallBarn,
            grunnlag.skersituasjon.situasjon,
            grunnlag.annenForelder,
            grunnlag.skerErAleneOmOmsorg,
            grunnlag.skerHarMidlertidigOmsorg,
            grunnlag.skerErFarEllerMedmor,
            grunnlag.termindato
        );
        const passerer = ugyldigePerioder.length === 0;
        return {
            passerer,
            info: ugyldigePerioder.map((periode) => ({
                periodeId: periode.id,
            })),
        };
    }

    return { passerer: true };
};
