import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { RegelTestresultat } from '../utils/types/regelTypes';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { isUttaksperiode } from 'uttaksplan/types/Periode';
import { hasValue } from '@navikt/fp-common';
import { farMedmorBrukerForeldrepengerMedAktivitetskravRundtFdselOgMorIkkeErSyk } from 'uttaksplan/utils/uttaksskjema/aktivitetskravMorSkalBesvares';

export const inneholderPerioderUtenAktivitetskrav = (grunnlag: Sknadsinfo): RegelTestresultat => {
    const {
        perioder,
        skerErFarEllerMedmor,
        skerErAleneOmOmsorg,
        skerHarMidlertidigOmsorg,
        familiehendelsesdato,
        skersituasjon,
    } = grunnlag;

    if (!skerErFarEllerMedmor || skerErAleneOmOmsorg || skerHarMidlertidigOmsorg) {
        return {
            passerer: true,
        };
    }
    const perioderUtenAktivitetskrav = perioder.filter(
        (p) =>
            isUttaksperiode(p) &&
            !hasValue(p.morsAktivitetIPerioden) &&
            !p.erMorForSyk &&
            !p.nskerSamtidigUttak &&
            !p.nskerFlerbarnsdager &&
            !farMedmorBrukerForeldrepengerMedAktivitetskravRundtFdselOgMorIkkeErSyk(
                familiehendelsesdato,
                skerErFarEllerMedmor,
                p.konto,
                p.erMorForSyk,
                p.tidsperiode,
                skersituasjon.situasjon
            ) &&
            (p.konto === StnadskontoType.Fellesperiode || p.konto === StnadskontoType.Foreldrepenger)
    );

    return {
        passerer: perioderUtenAktivitetskrav.length === 0,
        info: perioderUtenAktivitetskrav.map((p) => ({
            intlKey: 'uttaksplan.validering.feil.inneholderPerioderUtenAktivitetskrav',
            periodeId: p.id,
        })),
    };
};
