import { TidsperiodeDate } from '@navikt/fp-common';
import uttaksConstants from 'app/constants';
import { getTidsperiode, isValidTidsperiode } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { Situasjon } from 'app/types/Situasjon';
import { andreAugust2022ReglerGjelder } from 'app/utils/dateUtils';
import { getFrsteUttaksdag2UkerFrFdsel } from 'app/utils/wlbUtils';
import dayjs from 'dayjs';

export interface Uttaksdatoer {
    frsteUttaksdag: Date;
    frFdsel: {
        frsteMuligeUttaksdag: Date;
        frsteUttaksdagForeldrepengerFrFdsel: Date;
        sisteUttaksdagFrFdsel: Date;
    };
    etterFdsel: {
        sisteUttaksdagInnenforSeksUker: Date;
        frsteUttaksdagEtterSeksUker: Date;
        sisteMuligeUttaksdag: Date;
    };
}

export const uttaksdatoer = (familiehendelsesdato: Date, erFarEllerMedmor: boolean, termindato: Date | undefined) => ({
    frsteUttaksdagForeldrepengerFrFdsel: getFrsteUttaksdagForeldrepengerFrFdsel(familiehendelsesdato),
    frsteUttaksdagPEllerEtterFdsel: Uttaksdagen(familiehendelsesdato).denneEllerNeste(),
    frsteMuligeUttaksdagFrTermin: getFrsteMuligeUttaksdag(familiehendelsesdato, erFarEllerMedmor, termindato),
    sisteMuligeUttaksdagEtterTermin: getSisteMuligeUttaksdag(familiehendelsesdato),
});

export const getUttaksdatoer = (
    familiehendelsesdato: Date,
    erFarEllerMedmor: boolean,
    termindato: Date | undefined
): Uttaksdatoer => {
    const frsteUttaksdag = Uttaksdagen(familiehendelsesdato).denneEllerNeste();

    const frsteUttaksdagForeldrepengerFrFdsel = getFrsteUttaksdagForeldrepengerFrFdsel(familiehendelsesdato);
    const frsteMuligeUttaksdag = getFrsteMuligeUttaksdag(familiehendelsesdato, erFarEllerMedmor, termindato);
    const sisteUttaksdagFrFdsel = Uttaksdagen(frsteUttaksdag).forrige();
    const sisteMuligeUttaksdag = getSisteMuligeUttaksdag(familiehendelsesdato);

    const sisteUttaksdagInnenforSeksUker = getTidsperiode(frsteUttaksdag, 30).tom;
    return {
        frsteUttaksdag,
        frFdsel: {
            frsteMuligeUttaksdag,
            sisteUttaksdagFrFdsel,
            frsteUttaksdagForeldrepengerFrFdsel,
        },
        etterFdsel: {
            sisteUttaksdagInnenforSeksUker,
            frsteUttaksdagEtterSeksUker: Uttaksdagen(sisteUttaksdagInnenforSeksUker).neste(),
            sisteMuligeUttaksdag,
        },
    };
};

export function getFrsteUttaksdagPEllerEtterFdsel(familiehendelsesdato: Date) {
    return Uttaksdagen(familiehendelsesdato).denneEllerNeste();
}

export function getFrsteUttaksdagForeldrepengerFrFdsel(familiehendelsesdato: Date): Date {
    return Uttaksdagen(getFrsteUttaksdagPEllerEtterFdsel(familiehendelsesdato)).trekkFra(
        uttaksConstants.ANTALL_UKER_FORELDREPENGER_FR_FDSEL * 5
    );
}

export function getFrsteMuligeUttaksdag(
    familiehendelsesdato: Date,
    erFarEllerMedmor: boolean,
    termindato: Date | undefined
): Date {
    if (erFarEllerMedmor) {
        if (andreAugust2022ReglerGjelder(familiehendelsesdato)) {
            return getFrsteUttaksdag2UkerFrFdsel(familiehendelsesdato, termindato);
        } else {
            return Uttaksdagen(familiehendelsesdato).denneEllerNeste();
        }
    }
    return Uttaksdagen(getFrsteUttaksdagPEllerEtterFdsel(familiehendelsesdato)).trekkFra(
        uttaksConstants.MAKS_ANTALL_UKER_FORELDREPENGER_FR_FDSEL * 5
    );
}

export function getSisteMuligeUttaksdag(familiehendelsesdato: Date): Date {
    return Uttaksdagen(
        dayjs(getFrsteUttaksdagPEllerEtterFdsel(familiehendelsesdato))
            .add(uttaksConstants.MAKS_PERMISJONSLENGDE_I_R, 'year')
            .subtract(1, 'day')
            .toDate()
    ).denneEllerNeste();
}

export const erInnenFrsteSeksUkerFdselFarMedmor = (
    tidsperiode: TidsperiodeDate,
    situasjon: Situasjon,
    skerErFarEllerMedmor: boolean,
    frsteUttaksdagEtterSeksUker: Date
): boolean => {
    if (
        situasjon !== 'fdsel' ||
        !skerErFarEllerMedmor ||
        tidsperiode === undefined ||
        isValidTidsperiode(tidsperiode) === false
    ) {
        return false;
    }
    return dayjs(tidsperiode.fom).isBefore(dayjs(frsteUttaksdagEtterSeksUker), 'day');
};
