import { bemUtils, Block, intlUtils, TidsperiodeDate } from '@navikt/fp-common';
import AnnenForelder, { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { isValidTidsperiode } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import Arbeidsforhold from 'app/types/Arbeidsforhold';
import { Forelder } from 'app/types/Forelder';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { Hovedknapp, Knapp } from 'nav-frontend-knapper';
import React, { Dispatch, FunctionComponent, SetStateAction, useEffect, useState } from 'react';
import LinkButton from 'uttaksplan/components/link-button/LinkButton';
import TidsperiodeDisplay from 'uttaksplan/components/tidsperiode-display/TidsperiodeDisplay';
import UttakEndreTidsperiodeSprsml from 'uttaksplan/components/uttak-endre-tidsperiode-sprsml/UttakEndreTidsperiodeSprsml';
import { Periode, Periodetype, Utsettelsesperiode } from 'uttaksplan/types/Periode';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { getVelgbareStnadskontotyper } from 'uttaksplan/utils/stnadskontoerUtils';
import ErMorForSykSprsml from '../sprsml/er-mor-for-syk/ErMorForSykSprsml';
import FlerbarnsdagerSprsml from '../sprsml/flerbarnsdager/FlerbarnsdagerSprsml';
import HvemSkalHaUttakSprsml from '../sprsml/hvem-skal-ha-uttak/HvemSkalHaUttakSprsml';
import HvilkenKontoSprsml from '../sprsml/hvilken-konto/HvilkenKontoSprsml';
import UttakRundtFdselrsakSprsml from '../sprsml/uttak-rundt-fdsel-rsak/UttakRundtFdselrsakSprsml';
import OverfringsrsakSprsml from '../sprsml/overfringsrsak/OverfringsrsakSprsml';
import SamtidigUttakSprsml from '../sprsml/samtidig-uttak/SamtidigUttakSprsml';
import SkalHaGraderingSprsml from '../sprsml/skal-ha-gradering/SkalHaGraderingSprsml';
import { SubmitListener } from '../submit-listener/SubmitListener';
import TidsperiodeForm from '../tidsperiode-form/TidsperiodeForm';
import { PeriodeUttakFormComponents, PeriodeUttakFormData, PeriodeUttakFormField } from './periodeUttakFormConfig';
import {
    periodeUttakFormQuestionsConfig,
    skalViseWLBInfoOmSamtidigUttakRundtFdsel,
} from './periodeUttakFormQuestionsConfig';
import {
    cleanPeriodeUttakFormData,
    getPeriodeUttakFormInitialValues,
    mapPeriodeUttakFormToPeriode,
} from './periodeUttakFormUtils';
import { FormattedMessage, IntlShape } from 'react-intl';
import { getSlettPeriodeTekst } from 'uttaksplan/utils/periodeUtils';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import { Situasjon } from 'app/types/Situasjon';
import { andreAugust2022ReglerGjelder, formaterDatoKompakt, ISOStringToDate } from 'app/utils/dateUtils';
import AktivitetskravSprsml from '../sprsml/aktivitetskrav/AktivitetskravSprsml';
import { guid } from 'nav-frontend-js-utils';
import Veilederpanel from 'nav-frontend-veilederpanel';
import VeilederNormal from 'app/assets/VeilederNormal';
import {
    getFrsteUttaksdag2UkerFrFdsel,
    getSisteUttaksdag6UkerEtterFdsel,
    starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel,
} from 'app/utils/wlbUtils';

import './periodeUttakForm.less';

interface Props {
    periode: Periode;
    erEndringssknad: boolean;
    familiehendelsesdato: Date;
    stnadskontoer: TilgjengeligStnadskonto[];
    navnPForeldre: NavnPForeldre;
    annenForelder: AnnenForelder;
    arbeidsforhold: Arbeidsforhold[];
    erFarEllerMedmor: boolean;
    erFlerbarnssknad: boolean;
    erAleneOmOmsorg: boolean;
    erDeltUttak: boolean;
    situasjon: Situasjon;
    handleUpdatePeriode: (periode: Periode, familiehendelsedato: Date) => void;
    handleAddPeriode?: (nyPeriode: Periode, familiehendelsedato: Date) => void;
    setNyPeriodeFormIsVisible?: Dispatch<SetStateAction<boolean>>;
    toggleIsOpen?: (id: string) => void;
    handleDeletePeriode?: (periodeId: string) => void;
    isNyPeriode?: boolean;
    erMorUfr: boolean;
    setPeriodeErGyldig: Dispatch<SetStateAction<boolean>>;
    termindato: Date | undefined;
    morHarRett: boolean;
    antallBarn: number;
    utsettelserIPlan: Utsettelsesperiode[];
    intl: IntlShape;
}

const periodenGjelderAnnenForelder = (erFarEllerMedmor: boolean, forelder: Forelder): boolean => {
    return (erFarEllerMedmor && forelder === Forelder.farMedmor) || (!erFarEllerMedmor && forelder === Forelder.mor)
        ? false
        : true;
};

const erUttakAvAnnenForeldersKvote = (konto: StnadskontoType | '', skerErFarEllerMedmor: boolean): boolean => {
    return (
        (konto === StnadskontoType.Mdrekvote && skerErFarEllerMedmor === true) ||
        (konto === StnadskontoType.Fedrekvote && skerErFarEllerMedmor === false)
    );
};

const getPeriodeType = (
    periodenGjelder: Forelder | '',
    erFarEllerMedmor: boolean,
    konto: StnadskontoType | '',
    familiehendelsedato: Date,
    termindato: Date | undefined,
    tidsperiode: TidsperiodeDate
): Periodetype => {
    if (
        erFarEllerMedmor &&
        erUttakAvAnnenForeldersKvote(konto, erFarEllerMedmor) &&
        starterTidsperiodeInnenforToUkerFrFdselTilSeksUkerEtterFdsel(tidsperiode, familiehendelsedato, termindato)
    ) {
        return Periodetype.Overfring;
    }
    if (periodenGjelder === '' || konto === '') {
        return Periodetype.Uttak;
    }

    if (periodenGjelderAnnenForelder(erFarEllerMedmor, periodenGjelder)) {
        return Periodetype.Opphold;
    }

    if (erUttakAvAnnenForeldersKvote(konto, erFarEllerMedmor)) {
        return Periodetype.Overfring;
    }

    return Periodetype.Uttak;
};

const PeriodeUttakForm: FunctionComponent<Props> = ({
    familiehendelsesdato,
    periode,
    handleUpdatePeriode,
    stnadskontoer,
    navnPForeldre,
    annenForelder,
    toggleIsOpen,
    arbeidsforhold,
    handleDeletePeriode,
    setNyPeriodeFormIsVisible,
    handleAddPeriode,
    isNyPeriode = false,
    erFarEllerMedmor,
    erFlerbarnssknad,
    erAleneOmOmsorg,
    erDeltUttak,
    situasjon,
    erMorUfr,
    erEndringssknad,
    setPeriodeErGyldig,
    termindato,
    morHarRett,
    antallBarn,
    utsettelserIPlan,
    intl,
}) => {
    const [tidsperiodeIsOpen, setTidsperiodeIsOpen] = useState(false);
    const bem = bemUtils('periodeUttakForm');

    useEffect(() => {
        return () => {
            setPeriodeErGyldig(true);
        };
    }, [setPeriodeErGyldig]);

    const toggleVisTidsperiode = () => {
        setTidsperiodeIsOpen(!tidsperiodeIsOpen);
    };
    const forelder = erFarEllerMedmor ? Forelder.farMedmor : Forelder.mor;
    const annenForelderHarRettIES =
        isAnnenForelderOppgitt(annenForelder) && !!annenForelder.harRettPForeldrepengerIES;

    const handleCleanup = (
        values: PeriodeUttakFormData,
        visibility: QuestionVisibility<PeriodeUttakFormField, undefined>
    ): PeriodeUttakFormData => {
        return cleanPeriodeUttakFormData(
            values,
            visibility,
            erDeltUttak,
            forelder,
            erMorUfr,
            familiehendelsesdato,
            erFarEllerMedmor,
            annenForelderHarRettIES
        );
    };
    const erDeltUttakINorge = erDeltUttak && !annenForelderHarRettIES;
    const velgbareStnadskontoer = getVelgbareStnadskontotyper(stnadskontoer);
    const navnPAnnenForelder =
        isAnnenForelderOppgitt(annenForelder) && annenForelder.fornavn !== undefined && annenForelder.fornavn !== ''
            ? annenForelder.fornavn
            : intlUtils(intl, 'annen.forelder');

    const startDatoPeriodeRundtFdselFarMedmor =
        erFarEllerMedmor && andreAugust2022ReglerGjelder(familiehendelsesdato)
            ? getFrsteUttaksdag2UkerFrFdsel(familiehendelsesdato, termindato)
            : undefined;
    const sluttDatoPeriodeRundtFdselFarMedmor =
        erFarEllerMedmor && andreAugust2022ReglerGjelder(familiehendelsesdato)
            ? getSisteUttaksdag6UkerEtterFdsel(familiehendelsesdato)
            : undefined;

    const erFarMedmorOgHarAleneomsorg = erFarEllerMedmor && erAleneOmOmsorg;

    return (
        <PeriodeUttakFormComponents.FormikWrapper
            initialValues={getPeriodeUttakFormInitialValues(
                periode,
                erDeltUttak,
                forelder,
                erMorUfr,
                familiehendelsesdato,
                erFarEllerMedmor,
                annenForelderHarRettIES
            )}
            enableReinitialize={false}
            onSubmit={(values: Partial<PeriodeUttakFormData>) => {
                if (isNyPeriode) {
                    const periodetype = getPeriodeType(
                        values.hvemSkalTaUttak!,
                        erFarEllerMedmor,
                        values.konto!,
                        familiehendelsesdato,
                        termindato,
                        { fom: values.fom, tom: values.tom } as TidsperiodeDate
                    );
                    setNyPeriodeFormIsVisible!(false);
                    handleAddPeriode!(
                        mapPeriodeUttakFormToPeriode(
                            values,
                            guid(),
                            periodetype,
                            familiehendelsesdato,
                            erFarEllerMedmor,
                            erDeltUttak,
                            situasjon
                        ),
                        familiehendelsesdato
                    );
                } else {
                    return handleUpdatePeriode(
                        mapPeriodeUttakFormToPeriode(
                            values,
                            periode.id,
                            getPeriodeType(
                                values.hvemSkalTaUttak!,
                                erFarEllerMedmor,
                                values.konto!,
                                familiehendelsesdato,
                                termindato,
                                { fom: values.fom, tom: values.tom } as TidsperiodeDate
                            ),
                            familiehendelsesdato,
                            erFarEllerMedmor,
                            erDeltUttak,
                            situasjon
                        ),
                        familiehendelsesdato
                    );
                }
            }}
            renderForm={({ setFieldValue, values, isValid }) => {
                const periodetype = getPeriodeType(
                    values.hvemSkalTaUttak!,
                    erFarEllerMedmor,
                    values.konto!,
                    familiehendelsesdato,
                    termindato,
                    { fom: values.fom, tom: values.tom } as TidsperiodeDate
                );
                setPeriodeErGyldig(isValid);

                const visibility = periodeUttakFormQuestionsConfig.getVisbility({
                    values,
                    regelProps: {
                        annenForelder,
                        erAleneOmOmsorg,
                        erDeltUttakINorge,
                        erFarEllerMedmor,
                        erFlerbarnssknad,
                        familiehendelsesdato,
                        periodetype,
                        situasjon,
                        termindato,
                        morHarRett,
                        stnadskontoer,
                        antallBarn,
                    },
                });

                return (
                    <>
                        <Block visible={!isValidTidsperiode({ fom: values.fom!, tom: values.tom! })} padBottom="l">
                            <TidsperiodeForm
                                tidsperiode={{ fom: values.fom!, tom: values.tom! }}
                                familiehendelsesdato={familiehendelsesdato}
                                periode={periode}
                                onBekreft={(values) => {
                                    setFieldValue(PeriodeUttakFormField.fom, ISOStringToDate(values.fom));
                                    setFieldValue(PeriodeUttakFormField.tom, ISOStringToDate(values.tom));
                                }}
                                ugyldigeTidsperioder={undefined}
                                utsettelserIPlan={utsettelserIPlan}
                                termindato={termindato}
                                erFarEllerMedmor={erFarEllerMedmor}
                                morHarRett={morHarRett}
                                situasjon={situasjon}
                                erFarMedmorOgHarAleneomsorg={erFarMedmorOgHarAleneomsorg}
                            />
                        </Block>
                        <PeriodeUttakFormComponents.Form includeButtons={false}>
                            {!isNyPeriode && <SubmitListener cleanup={() => handleCleanup(values, visibility)} />}

                            <Block visible={isValidTidsperiode({ fom: values.fom!, tom: values.tom! })} padBottom="l">
                                <TidsperiodeDisplay
                                    tidsperiode={{ fom: values.fom!, tom: values.tom! }}
                                    toggleVisTidsperiode={toggleVisTidsperiode}
                                />
                                <UttakEndreTidsperiodeSprsml
                                    periode={periode}
                                    familiehendelsesdato={familiehendelsesdato}
                                    ugyldigeTidsperioder={undefined}
                                    utsettelserIPlan={utsettelserIPlan}
                                    onBekreft={(values) => {
                                        toggleVisTidsperiode();
                                        setFieldValue(PeriodeUttakFormField.fom, ISOStringToDate(values.fom));
                                        setFieldValue(PeriodeUttakFormField.tom, ISOStringToDate(values.tom));
                                    }}
                                    changeTidsperiode={(values) => {
                                        setFieldValue(PeriodeUttakFormField.fom, values.fom);
                                        setFieldValue(PeriodeUttakFormField.tom, values.tom);
                                    }}
                                    tidsperiode={{ fom: values.fom!, tom: values.tom! }}
                                    onAvbryt={() => toggleVisTidsperiode()}
                                    visible={tidsperiodeIsOpen}
                                    termindato={termindato}
                                    erFarEllerMedmor={erFarEllerMedmor}
                                    morHarRett={morHarRett}
                                    situasjon={situasjon}
                                    erFarMedmorOgHarAleneomsorg={erFarMedmorOgHarAleneomsorg}
                                />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(PeriodeUttakFormField.hvemSkalTaUttak)}>
                                <HvemSkalHaUttakSprsml
                                    fieldName={PeriodeUttakFormField.hvemSkalTaUttak}
                                    erFarEllerMedmor={false}
                                    navnPForeldre={navnPForeldre}
                                />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(PeriodeUttakFormField.konto)}>
                                <HvilkenKontoSprsml
                                    fieldName={PeriodeUttakFormField.konto}
                                    velgbareStnadskontoer={velgbareStnadskontoer}
                                    erOppholdsperiode={false}
                                    navnPForeldre={navnPForeldre}
                                    erFarEllerMedmor={erFarEllerMedmor}
                                    situasjon={situasjon}
                                    erAleneOmOmsorg={erAleneOmOmsorg}
                                />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(PeriodeUttakFormField.overfringsrsak)}>
                                <OverfringsrsakSprsml
                                    vedlegg={values.overfringsdokumentasjon}
                                    navnAnnenForelder={navnPAnnenForelder!}
                                    erEndringssknad={erEndringssknad}
                                    valgtOverfringsrsak={values.overfringsrsak}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(PeriodeUttakFormField.uttakRundtFdselrsak)}
                            >
                                <UttakRundtFdselrsakSprsml
                                    fieldName={PeriodeUttakFormField.uttakRundtFdselrsak}
                                    uttakRundtFdselrsak={values.uttakRundtFdselrsak}
                                    navnMor={navnPForeldre.mor}
                                    vedlegg={values.erMorForSykDokumentasjon}
                                />
                            </Block>
                            {startDatoPeriodeRundtFdselFarMedmor !== undefined &&
                                sluttDatoPeriodeRundtFdselFarMedmor !== undefined && (
                                    <Block
                                        padBottom="l"
                                        visible={skalViseWLBInfoOmSamtidigUttakRundtFdsel(
                                            values,
                                            familiehendelsesdato,
                                            erFarEllerMedmor,
                                            erDeltUttak,
                                            situasjon
                                        )}
                                    >
                                        <Veilederpanel
                                            fargetema="normal"
                                            svg={<VeilederNormal transparentBackground={true} />}
                                        >
                                            <FormattedMessage
                                                id="uttaksplan.samtidigUttakVeileder"
                                                values={{
                                                    fomDato: formaterDatoKompakt(startDatoPeriodeRundtFdselFarMedmor),
                                                    tomDato: formaterDatoKompakt(sluttDatoPeriodeRundtFdselFarMedmor),
                                                }}
                                            />
                                        </Veilederpanel>
                                    </Block>
                                )}
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(PeriodeUttakFormField.nskerFlerbarnsdager)}
                            >
                                <FlerbarnsdagerSprsml fieldName={PeriodeUttakFormField.nskerFlerbarnsdager} />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(PeriodeUttakFormField.erMorForSyk)}>
                                <ErMorForSykSprsml
                                    fieldName={PeriodeUttakFormField.erMorForSyk}
                                    erMorForSyk={values.erMorForSyk}
                                    navnMor={navnPForeldre.mor}
                                    vedlegg={values.erMorForSykDokumentasjon}
                                />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(PeriodeUttakFormField.samtidigUttak)}>
                                <SamtidigUttakSprsml
                                    erFlerbarnssknad={erFlerbarnssknad}
                                    navnPForeldre={navnPForeldre}
                                    navnPAnnenForelder={navnPAnnenForelder}
                                    samtidigUttakProsentVisible={visibility.isVisible(
                                        PeriodeUttakFormField.samtidigUttakProsent
                                    )}
                                    familiehendelsesdato={familiehendelsesdato}
                                    situasjon={situasjon}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(PeriodeUttakFormField.aktivitetskravMor)}
                            >
                                <AktivitetskravSprsml
                                    fieldName={PeriodeUttakFormField.aktivitetskravMor}
                                    navnPForeldre={navnPForeldre}
                                    aktivitetskravMorValue={values.aktivitetskravMor}
                                    aktivitetskravVedlegg={values.aktivitetskravMorDokumentasjon}
                                    FormComponents={PeriodeUttakFormComponents}
                                    vedleggFieldName={PeriodeUttakFormField.aktivitetskravMorDokumentasjon}
                                />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(PeriodeUttakFormField.skalHaGradering)}>
                                <SkalHaGraderingSprsml
                                    graderingsprosentVisible={visibility.isVisible(
                                        PeriodeUttakFormField.stillingsprosent
                                    )}
                                    arbeidsforhold={arbeidsforhold}
                                    tidsperiode={{ fom: values.fom!, tom: values.tom! }}
                                />
                            </Block>
                            <Block
                                visible={
                                    !isNyPeriode && handleDeletePeriode !== undefined && toggleIsOpen !== undefined
                                }
                            >
                                <div className={bem.element('knapperad-endre')}>
                                    <Knapp htmlType="button" onClick={() => toggleIsOpen!(periode.id)}>
                                        <FormattedMessage id="uttaksplan.lukk" />
                                    </Knapp>
                                    <div className={bem.element('slettPeriodeWrapper')}>
                                        <LinkButton
                                            onClick={() => handleDeletePeriode!(periode.id)}
                                            className={bem.element('slettPeriode')}
                                        >
                                            <FormattedMessage id={getSlettPeriodeTekst(periode.type)} />
                                        </LinkButton>
                                    </div>
                                </div>
                            </Block>
                            <Block
                                visible={
                                    isNyPeriode &&
                                    setNyPeriodeFormIsVisible !== undefined &&
                                    handleAddPeriode !== undefined
                                }
                            >
                                <div className={bem.element('knapperad-legg-til')}>
                                    <Knapp htmlType="button" onClick={() => setNyPeriodeFormIsVisible!(false)}>
                                        <FormattedMessage id="uttaksplan.avbryt" />
                                    </Knapp>
                                    {visibility.areAllQuestionsAnswered() ? (
                                        <Hovedknapp>
                                            <FormattedMessage id="uttaksplan.leggTil" />
                                        </Hovedknapp>
                                    ) : null}
                                </div>
                            </Block>
                        </PeriodeUttakFormComponents.Form>
                    </>
                );
            }}
        />
    );
};

export default PeriodeUttakForm;
