import { Block, intlUtils } from '@navikt/fp-common';
import VeilederNormal from 'app/assets/VeilederNormal';
import FormikFileUploader from 'app/components/formik-file-uploader/FormikFileUploader';
import { Attachment } from 'app/types/Attachment';
import { AttachmentType } from 'app/types/AttachmentType';
import { Skjemanummer } from 'app/types/Skjemanummer';
import { RadioProps } from 'nav-frontend-skjema';
import { Normaltekst } from 'nav-frontend-typografi';
import Veilederpanel from 'nav-frontend-veilederpanel';
import React, { FunctionComponent } from 'react';
import { FormattedMessage, IntlShape, useIntl } from 'react-intl';
import { UtsettelsersakType } from 'uttaksplan/types/UtsettelsersakType';
import {
    PeriodeUtsettelseFormComponents,
    PeriodeUtsettelseFormField,
} from '../../periode-utsettelse-form/periodeUtsettelseFormConfig';

interface Props {
    periodenErKunHelligdager: boolean;
    skalViseGamleUtsettelsersaker: boolean;
    erFarEllerMedmor: boolean;
    tidsperiodenErInnenforFrsteSeksUker: boolean;
    utsettelsersak: UtsettelsersakType | '';
    vedlegg: Attachment[];
    erMorUfr: boolean;
    skerErFarEllerMedmorOgKunDeHarRett: boolean;
}

const getUtsettelsersakOptions = (
    intl: IntlShape,
    periodenErKunHelligdager: boolean,
    skalViseGamleUtsettelsersaker: boolean,
    erFarEllerMedmor: boolean,
    tidsperiodenErInnenforFrsteSeksUker: boolean,
    erMorUfr: boolean,
    skerErFarEllerMedmorOgKunDeHarRett: boolean
) => {
    const allRadios: RadioProps[] = [
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.jegskalhaferie'),
            value: UtsettelsersakType.Ferie,
            disabled: periodenErKunHelligdager === true,
            name: 'utsettelsersak',
        },
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.jegskaljobbeheltid'),
            value: UtsettelsersakType.Arbeid,
            name: 'utsettelsersak',
        },
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.pgasykdom'),
            value: UtsettelsersakType.Sykdom,
            name: 'utsettelsersak',
        },
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.institusjonBarn'),
            value: UtsettelsersakType.InstitusjonBarnet,
            name: 'utsettelsersak',
        },
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.institusjonSker'),
            value: UtsettelsersakType.InstitusjonSker,
            name: 'utsettelsersak',
        },
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.hv_velse'),
            value: UtsettelsersakType.Hvvelse,
            name: 'utsettelsersak',
        },
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.navtiltak'),
            value: UtsettelsersakType.NavTiltak,
            name: 'utsettelsersak',
        },
        {
            label: intlUtils(intl, 'uttaksplan.utsettelsersak.fri'),
            value: UtsettelsersakType.Fri,
            name: 'utsettelsersak',
        },
    ];

    const defaultRadios = allRadios.filter((option) => {
        if (skalViseGamleUtsettelsersaker) {
            if (option.value === UtsettelsersakType.Fri) {
                return false;
            }

            return true;
        }

        if (!skalViseGamleUtsettelsersaker && !erFarEllerMedmor) {
            if (tidsperiodenErInnenforFrsteSeksUker) {
                return (
                    option.value === UtsettelsersakType.Sykdom ||
                    option.value === UtsettelsersakType.InstitusjonBarnet ||
                    option.value === UtsettelsersakType.InstitusjonSker
                );
            }

            return false;
        }

        if (!skalViseGamleUtsettelsersaker && erFarEllerMedmor) {
            if (!erMorUfr) {
                if (option.value === UtsettelsersakType.Fri && skerErFarEllerMedmorOgKunDeHarRett) {
                    return true;
                }

                return false;
            }

            if (tidsperiodenErInnenforFrsteSeksUker) {
                return (
                    option.value === UtsettelsersakType.Sykdom ||
                    option.value === UtsettelsersakType.InstitusjonBarnet ||
                    option.value === UtsettelsersakType.InstitusjonSker
                );
            }

            return option.value === UtsettelsersakType.Fri;
        }

        return option.value === UtsettelsersakType.Sykdom || option.value === UtsettelsersakType.Fri;
    });

    return defaultRadios;
};

const getUtsettelsersakSkjemanummer = (rsak: UtsettelsersakType | '') => {
    switch (rsak) {
        case UtsettelsersakType.Sykdom:
            return Skjemanummer.DOK_OVERFRING_FOR_SYK;
        case UtsettelsersakType.InstitusjonSker:
        case UtsettelsersakType.InstitusjonBarnet:
            return Skjemanummer.DOK_INNLEGGELSE;
        case UtsettelsersakType.Hvvelse:
            return Skjemanummer.HV_VELSE;
        case UtsettelsersakType.NavTiltak:
            return Skjemanummer.NAV_TILTAK;
        default:
            return Skjemanummer.ANNET;
    }
};

const showAttachmentUploader = (rsak: UtsettelsersakType | ''): boolean => {
    switch (rsak) {
        case UtsettelsersakType.Sykdom:
        case UtsettelsersakType.InstitusjonSker:
        case UtsettelsersakType.InstitusjonBarnet:
        case UtsettelsersakType.Hvvelse:
        case UtsettelsersakType.NavTiltak:
            return true;
        default:
            return false;
    }
};

const getAttachmentUploaderLabel = (rsak: UtsettelsersakType | ''): string => {
    switch (rsak) {
        case UtsettelsersakType.Sykdom:
        case UtsettelsersakType.InstitusjonSker:
        case UtsettelsersakType.InstitusjonBarnet:
            return 'Last opp dokumentasjon for sykdom';
        case UtsettelsersakType.Hvvelse:
            return 'Last opp dokumentasjon for HV-velse';
        case UtsettelsersakType.NavTiltak:
            return 'Last opp dokumentasjon for NAV tiltak';
        default:
            return '';
    }
};

const getAttachmentUploaderType = (rsak: UtsettelsersakType | ''): AttachmentType => {
    switch (rsak) {
        case UtsettelsersakType.Sykdom:
        case UtsettelsersakType.InstitusjonSker:
        case UtsettelsersakType.InstitusjonBarnet:
            return AttachmentType.UTSETTELSE_SYKDOM;
        case UtsettelsersakType.Hvvelse:
            return AttachmentType.HV_VELSE;
        case UtsettelsersakType.NavTiltak:
            return AttachmentType.NAV_TILTAK;
        default:
            return AttachmentType.UTSETTELSE_SYKDOM; // Should never happen
    }
};

const getVeilederTekst = (rsak: UtsettelsersakType | ''): React.ReactElement => {
    switch (rsak) {
        case UtsettelsersakType.Sykdom:
        case UtsettelsersakType.InstitusjonSker:
        case UtsettelsersakType.InstitusjonBarnet:
            return <FormattedMessage id="uttaksplan.veileder.sykdom" />;
        case UtsettelsersakType.Hvvelse:
            return <FormattedMessage id="uttaksplan.veileder.hvvelse" />;
        case UtsettelsersakType.NavTiltak:
            return <FormattedMessage id="uttaksplan.veileder.navTiltak" />;
        default:
            return <FormattedMessage id="uttaksplan.veileder.sykdom" />; // Should never happen
    }
};

const UtsettelsersakSprsml: FunctionComponent<Props> = ({
    periodenErKunHelligdager,
    skalViseGamleUtsettelsersaker,
    erFarEllerMedmor,
    tidsperiodenErInnenforFrsteSeksUker,
    utsettelsersak,
    vedlegg,
    erMorUfr,
    skerErFarEllerMedmorOgKunDeHarRett,
}) => {
    const intl = useIntl();
    const rsakOptions = getUtsettelsersakOptions(
        intl,
        periodenErKunHelligdager,
        skalViseGamleUtsettelsersaker,
        erFarEllerMedmor,
        tidsperiodenErInnenforFrsteSeksUker,
        erMorUfr,
        skerErFarEllerMedmorOgKunDeHarRett
    );

    if (rsakOptions.length === 0) {
        return (
            <Veilederpanel fargetema="normal" type="normal" svg={<VeilederNormal transparentBackground={true} />}>
                <Block padBottom="l">
                    <Normaltekst>
                        <b>
                            <FormattedMessage id="uttaksplan.veileder.trengerIkkeUtsettelse.del1" />
                        </b>
                    </Normaltekst>
                </Block>
                <Block padBottom="l">
                    <Normaltekst>
                        <FormattedMessage id="uttaksplan.veileder.trengerIkkeUtsettelse.del2" />
                    </Normaltekst>
                </Block>
                <Block padBottom="l">
                    <Normaltekst>
                        <FormattedMessage id="uttaksplan.veileder.trengerIkkeUtsettelse.del3" />
                    </Normaltekst>
                </Block>
            </Veilederpanel>
        );
    }

    return (
        <>
            <Block padBottom="l">
                <PeriodeUtsettelseFormComponents.RadioPanelGroup
                    name={PeriodeUtsettelseFormField.rsak}
                    radios={rsakOptions}
                    useTwoColumns={true}
                />
            </Block>
            <Block padBottom="l" visible={showAttachmentUploader(utsettelsersak)}>
                <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                    {getVeilederTekst(utsettelsersak)}
                </Veilederpanel>
            </Block>
            <Block padBottom="l" visible={showAttachmentUploader(utsettelsersak)}>
                <FormikFileUploader
                    label={getAttachmentUploaderLabel(utsettelsersak)}
                    name={PeriodeUtsettelseFormField.vedlegg}
                    attachments={vedlegg || []}
                    attachmentType={getAttachmentUploaderType(utsettelsersak)}
                    skjemanummer={getUtsettelsersakSkjemanummer(utsettelsersak)}
                />
            </Block>
        </>
    );
};

export default UtsettelsersakSprsml;
