import { bemUtils, Block, intlUtils } from '@navikt/fp-common';
import SknadRoutes from 'app/routes/routes';
import { Hovedknapp, Knapp } from 'nav-frontend-knapper';
import ModalWrapper from 'nav-frontend-modal';
import { Normaltekst, Undertittel } from 'nav-frontend-typografi';
import React, { Dispatch, FunctionComponent, SetStateAction } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { useNavigate } from 'react-router-dom';

import './vilDuGTilbakeModal.less';

interface Props {
    isOpen: boolean;
    setIsOpen: Dispatch<SetStateAction<boolean>>;
}

const VilDuGTilbakeModal: FunctionComponent<Props> = ({ isOpen, setIsOpen }) => {
    const intl = useIntl();
    const bem = bemUtils('vilDuGTilbakeModal');
    const navigate = useNavigate();

    return (
        <ModalWrapper
            className={bem.block}
            contentLabel={'Tittel'}
            closeButton={false}
            isOpen={isOpen}
            onRequestClose={() => setIsOpen(false)}
        >
            <Block padBottom="l">
                <Undertittel tag="h1">{intlUtils(intl, 'uttaksplan.vilDuGTilbakeModal.tittel')}</Undertittel>
            </Block>
            <Block padBottom="l">
                <Normaltekst>{intlUtils(intl, 'uttaksplan.vilDuGTilbakeModal.intro')}</Normaltekst>
            </Block>
            <Block padBottom="l">
                <Normaltekst>{intlUtils(intl, 'uttaksplan.vilDuGTilbakeModal.sprsml')}</Normaltekst>
            </Block>
            <div className={bem.element('knapperad')}>
                <Hovedknapp
                    onClick={() => {
                        setIsOpen(false);
                        navigate(SknadRoutes.UTTAKSPLAN_INFO);
                    }}
                >
                    <FormattedMessage id="uttaksplan.vilDuGTilbakeModal.okLabel" />
                </Hovedknapp>
                <Knapp onClick={() => setIsOpen(false)}>
                    <FormattedMessage id="uttaksplan.vilDuGTilbakeModal.avbrytLabel" />
                </Knapp>
            </div>
        </ModalWrapper>
    );
};

export default VilDuGTilbakeModal;
