import { RegelTestresultat } from '../utils/types/regelTypes';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';
import { getBrukteDager } from 'uttaksplan/utils/brukteDagerUtils';
import { laTilPeriodeEtterFrsteStnadsdagPflgendeBarn } from './periodevalideringstester/forSenUttakVedPflgendeBarn';

export const overstigerMinsterettVedToTette = (grunnlag: Sknadsinfo): RegelTestresultat => {
    const { familiehendelsesdato, perioder, stnadskontoer, skerErFarEllerMedmor, minsterettUkerToTette } = grunnlag;
    const perioderEtterFrsteStnadsperiodeNyttBarn = perioder.filter((periode) =>
        laTilPeriodeEtterFrsteStnadsdagPflgendeBarn(periode, grunnlag.frsteUttaksdagNesteBarnsSak)
    );
    if (
        minsterettUkerToTette === undefined ||
        minsterettUkerToTette === 0 ||
        perioderEtterFrsteStnadsperiodeNyttBarn.length === 0
    ) {
        return {
            passerer: true,
        };
    }
    const perioderFrFrsteStnadsperiodeNyttBarn = perioder.filter(
        (periode) => !laTilPeriodeEtterFrsteStnadsdagPflgendeBarn(periode, grunnlag.frsteUttaksdagNesteBarnsSak)
    );
    const minsterettMaxAntallUker = minsterettUkerToTette;
    const minsterettMaxAntallDager = minsterettMaxAntallUker * 5;

    const brukteDagerPerForelderFrFrsteStnadsdagNyttBarn = getBrukteDager(
        stnadskontoer,
        perioderFrFrsteStnadsperiodeNyttBarn,
        familiehendelsesdato
    );
    const uttaksdagerFremTilNyttBarnStnadsdag = skerErFarEllerMedmor
        ? brukteDagerPerForelderFrFrsteStnadsdagNyttBarn.farMedmor.dagerEgneKvoter +
          brukteDagerPerForelderFrFrsteStnadsdagNyttBarn.farMedmor.dagerFellesperiode
        : brukteDagerPerForelderFrFrsteStnadsdagNyttBarn.mor.dagerEgneKvoter +
          brukteDagerPerForelderFrFrsteStnadsdagNyttBarn.mor.dagerFellesperiode;

    const dagerIgjenBrukePMinsteretten = minsterettMaxAntallDager - uttaksdagerFremTilNyttBarnStnadsdag;

    const brukteDagerPerForelderEtterFrsteStnadsdagNyttBarn = getBrukteDager(
        stnadskontoer,
        perioderEtterFrsteStnadsperiodeNyttBarn,
        familiehendelsesdato
    );
    const uttaksdagerEtterNyttBarnStnadsdag = skerErFarEllerMedmor
        ? brukteDagerPerForelderEtterFrsteStnadsdagNyttBarn.farMedmor.dagerEgneKvoter +
          brukteDagerPerForelderEtterFrsteStnadsdagNyttBarn.farMedmor.dagerFellesperiode
        : brukteDagerPerForelderEtterFrsteStnadsdagNyttBarn.mor.dagerEgneKvoter +
          brukteDagerPerForelderEtterFrsteStnadsdagNyttBarn.mor.dagerFellesperiode;

    const dagerIgjenAvMinsterett = dagerIgjenBrukePMinsteretten - uttaksdagerEtterNyttBarnStnadsdag;

    return {
        passerer: dagerIgjenAvMinsterett >= 0,
        info: {
            intlKey: 'uttaksplan.validering.info.overstigerMinsterettVedToTette',
            values: {
                uker: minsterettMaxAntallUker,
                forMyeUttak: Math.abs(dagerIgjenAvMinsterett),
            },
        },
    };
};
