import { Sknadsinfo } from '../../utils/types/Sknadsinfo';
import { RegelTest, RegelTestresultat } from 'uttaksplan/validering/utils/types/regelTypes';
import {
    getSisteUttaksdag6UkerEtterFdsel,
    gjelderWLBReglerFarMedmorRundtFdsel,
    isUttaksperiodeFarMedmorPgaFdsel,
    slutterTidsperiodeInnen6UkerEtterFdsel,
} from 'app/utils/wlbUtils';
import { formaterDatoKompakt } from 'app/utils/dateUtils';

export const slutterUttaksperiodeRundtFdselInnen6UkerEtterFdsel: RegelTest = (
    grunnlag: Sknadsinfo
): RegelTestresultat => {
    if (
        !gjelderWLBReglerFarMedmorRundtFdsel(
            grunnlag.familiehendelsesdato,
            grunnlag.skerErFarEllerMedmor,
            grunnlag.morHarRett,
            grunnlag.skersituasjon.situasjon
        )
    ) {
        return {
            passerer: true,
        };
    }

    const perioderFarMedmorSomIkkeSlutterFr6UkerEtterFdsel = grunnlag.perioder
        .filter((p) => isUttaksperiodeFarMedmorPgaFdsel(p, grunnlag.familiehendelsesdato, grunnlag.termindato))
        .filter((p) => !slutterTidsperiodeInnen6UkerEtterFdsel(p.tidsperiode, grunnlag.familiehendelsesdato));

    const sisteUttaksdagSeksUkerEtterFdsel = formaterDatoKompakt(
        getSisteUttaksdag6UkerEtterFdsel(grunnlag.familiehendelsesdato)
    );
    return {
        passerer: perioderFarMedmorSomIkkeSlutterFr6UkerEtterFdsel.length === 0,
        info: perioderFarMedmorSomIkkeSlutterFr6UkerEtterFdsel.map((periode) => ({
            intlKey: 'uttaksplan.validering.feil.slutterUttaksperiodeRundtFdselEtter6UkerEtterFdsel',
            values: { dato: sisteUttaksdagSeksUkerEtterFdsel },
            periodeId: periode.id,
        })),
    };
};
