import { Block, intlUtils, Step } from '@navikt/fp-common';
import SknadRoutes from 'app/routes/routes';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useAvbrytSknad from 'app/utils/hooks/useAvbrytSknad';
import { Hovedknapp } from 'nav-frontend-knapper';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import AlertStripe from 'nav-frontend-alertstriper';
import stepConfig, { getPreviousStepHref } from '../stepsConfig';
import Uttaksplan from 'uttaksplan/Uttaksplan';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import useSknad from 'app/utils/hooks/useSknad';
import {
    getFarMedmorErAleneOmOmsorg,
    getKjnnFromFnr,
    getMorErAleneOmOmsorg,
    getMorHarRettPForeldrepengerINorgeEllerES,
    getNavnPForeldre,
} from 'app/utils/personUtils';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { getForeldreparSituasjon } from 'app/utils/foreldreparSituasjonUtils';
import { Forelder } from 'app/types/Forelder';
import { isUttakAnnenPart, isUttakAvForeldrepengerFrFdsel, isUttaksperiode, Periode } from 'uttaksplan/types/Periode';
import { getFamiliehendelsedato, getTermindato } from 'app/utils/barnUtils';
import actionCreator from 'app/context/action/actionCreator';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';
import Api from 'app/api/api';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import getStnadskontoParams, {
    getAntallBarnSomSkalBrukesFraSaksgrunnlagBeggeParter,
    getTermindatoSomSkalBrukesFraSaksgrunnlagBeggeParter,
} from 'app/api/getStnadskontoParams';
import NavFrontendSpinner from 'nav-frontend-spinner';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { getErMorUfr } from 'app/utils/annenForelderUtils';
import useDebounce from 'app/utils/hooks/useDebounce';
import { getPerioderSomSkalSendesInn, storeAppState } from 'app/utils/submitUtils';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { SenEndringrsak } from 'uttaksplan/types/SenEndringrsak';
import useFortsettSknadSenere from 'app/utils/hooks/useFortsettSknadSenere';
import { getEndringstidspunkt, getMorsSisteDag, ISOStringToDate } from 'app/utils/dateUtils';
import { cleanupInvisibleCharsFromTilleggsopplysninger } from 'app/utils/tilleggsopplysningerUtils';
import VilDuGTilbakeModal from './components/vil-du-g-tilbake-modal/VilDuGTilbakeModal';
import { getAktiveArbeidsforhold } from 'app/utils/arbeidsforholdUtils';
import { UttaksplanFormComponents } from 'app/steps/uttaksplan/UttaksplanFormConfig';

import { getPerioderMedUttakRundtFdsel } from 'app/utils/wlbUtils';
import uttaksplanQuestionsConfig from './uttaksplanQuestionConfig';
import { getUttaksplanFormInitialValues } from './UttaksplanFormUtils';

import {
    getVisAutomatiskJusteringForm,
    getKanJustereAutomatiskVedFdsel,
} from 'uttaksplan/components/automatisk-justering-form/automatiskJusteringUtils';
import { FormikValues } from 'formik';
import {
    getStartdatoFrstePeriodeAnnenPart,
    mapAnnenPartsEksisterendeSakFromDTO,
} from 'app/utils/eksisterendeSakUtils';
import { getHarAktivitetskravIPeriodeUtenUttak } from 'app/utils/uttaksplan/uttaksplanUtils';
import { RequestStatus } from 'app/types/RequestState';
import { Periodene } from '../uttaksplan-info/utils/Periodene';
import { finnOgSettInnHull, settInnAnnenPartsUttak } from 'uttaksplan/builder/uttaksplanbuilderUtils';
import { isUfdtBarn } from 'app/context/types/Barn';
import { dateToISOString } from '@navikt/sif-common-formik/lib';
import dayjs from 'dayjs';
import { getAntallUkerMinsterett } from '../uttaksplan-info/utils/stnadskontoer';
import { sendErrorMessageToSentry } from 'app/api/apiUtils';
import useSaveLoadedRoute from 'app/utils/hooks/useSaveLoadedRoute';

const UttaksplanStep = () => {
    const intl = useIntl();
    const skerinfo = useSkerinfo();
    const sknad = useSknad();
    const [gTilbakeIsOpen, setGTilbakeIsOpen] = useState(false);
    const [uttaksplanErGyldig, setUttaksplanErGyldig] = useState(true);
    const [submitIsClicked, setSubmitIsClicked] = useState(false);
    const { dispatch, state } = useForeldrepengesknadContext();
    const [endringstidspunkt, setEndringstidspunkt] = useState(state.endringstidspunkt);
    const [perioderSomSkalSendesInn, setPerioderSomSkalSendesInn] = useState(state.perioderSomSkalSendesInn);
    const nextRoute = sknad.erEndringssknad ? SknadRoutes.OPPSUMMERING : SknadRoutes.UTENLANDSOPPHOLD;
    const { uttaksplanInfo, eksisterendeSak, harUttaksplanBlittSlettet, annenPartsUttakErLagtTilIPlan } = state;
    const { person, arbeidsforhold } = skerinfo;
    const { annenForelder, sker, barn, skersituasjon, dekningsgrad, erEndringssknad, tilleggsopplysninger } = sknad;
    const { erAleneOmOmsorg } = sker;
    const { situasjon } = skersituasjon;
    const { rolle } = skersituasjon;
    const { barnFraNesteSak } = state;
    const debouncedState = useDebounce(state, 3000);
    const annenForelderKjnn = getKjnnFromFnr(annenForelder);
    const erDeltUttak = isAnnenForelderOppgitt(annenForelder)
        ? !!annenForelder.harRettPForeldrepengerINorge || !!annenForelder.harRettPForeldrepengerIES
        : false;
    const erFarEllerMedmor = isFarEllerMedmor(sknad.skersituasjon.rolle);
    const morErAleneOmOmsorg = getMorErAleneOmOmsorg(!erFarEllerMedmor, erAleneOmOmsorg, annenForelder);
    const farMedmorErAleneOmOmsorg = getFarMedmorErAleneOmOmsorg(erFarEllerMedmor, erAleneOmOmsorg, annenForelder);
    const skerErAleneOmOmsorg = morErAleneOmOmsorg || farMedmorErAleneOmOmsorg;
    const forelderVedAleneomsorg = erDeltUttak ? undefined : erFarEllerMedmor ? Forelder.farMedmor : Forelder.mor;
    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const familiehendelsesdatoDate = ISOStringToDate(familiehendelsesdato);
    const erMorUfr = getErMorUfr(annenForelder, erFarEllerMedmor);
    const navnPForeldre = getNavnPForeldre(person, annenForelder, erFarEllerMedmor, intl);
    const antallBarn = barn.antallBarn;
    const erFlerbarnssknad = antallBarn > 1;
    const morHarRett = getMorHarRettPForeldrepengerINorgeEllerES(rolle, erFarEllerMedmor, annenForelder);
    const opprinneligPlan = eksisterendeSak?.uttaksplan;
    const harKomplettUttaksplan = eksisterendeSak ? eksisterendeSak.uttaksplan !== undefined : false;
    const harMidlertidigOmsorg = false; //TODO skerHarMidlertidigOmsorg
    const morsSisteDag = getMorsSisteDag(uttaksplanInfo);
    const termindato = getTermindato(barn);
    const annenForelderFnr =
        isAnnenForelderOppgitt(annenForelder) && !!annenForelder.utenlandskFnr === false
            ? annenForelder.fnr
            : undefined;
    const erAdopsjon = situasjon === 'adopsjon';
    const annenForelderFnrNesteSak = barnFraNesteSak !== undefined ? barnFraNesteSak.annenForelderFnr : undefined;
    const frsteBarnFraNesteSakFnr =
        barnFraNesteSak !== undefined && barnFraNesteSak.fnr !== undefined && barnFraNesteSak.fnr.length > 0
            ? barnFraNesteSak.fnr[0]
            : undefined;
    const familieHendelseDatoNesteSak =
        barnFraNesteSak !== undefined ? barnFraNesteSak.familiehendelsesdato : undefined;
    const frsteUttaksdagNesteBarnsSak =
        barnFraNesteSak !== undefined ? barnFraNesteSak.startdatoFrsteStnadsperiode : undefined;

    const bareFarMedmorHarRett = !getMorHarRettPForeldrepengerINorgeEllerES(
        skersituasjon.rolle,
        erFarEllerMedmor,
        annenForelder
    );

    const barnFnr = !isUfdtBarn(barn) && barn.fnr !== undefined && barn.fnr.length > 0 ? barn.fnr[0] : undefined;
    const eksisterendeSakAnnenPartRequestIsSuspended =
        !skerErAleneOmOmsorg &&
        annenForelderFnr !== undefined &&
        annenForelderFnr !== '' &&
        (barnFnr !== undefined || familiehendelsesdato !== undefined)
            ? false
            : true;

    useSaveLoadedRoute(SknadRoutes.UTTAKSPLAN);

    const { eksisterendeSakAnnenPartData, eksisterendeSakAnnenPartError, eksisterendeSakAnnenPartRequestStatus } =
        Api.useGetAnnenPartsVedtak(
            annenForelderFnr,
            barnFnr,
            familiehendelsesdato,
            eksisterendeSakAnnenPartRequestIsSuspended
        );

    const eksisterendeVedtakAnnenPart = useMemo(
        () =>
            mapAnnenPartsEksisterendeSakFromDTO(
                eksisterendeSakAnnenPartData,
                barn,
                erFarEllerMedmor,
                familiehendelsesdato,
                frsteUttaksdagNesteBarnsSak
            ),
        [eksisterendeSakAnnenPartData, barn, erFarEllerMedmor, familiehendelsesdato, frsteUttaksdagNesteBarnsSak]
    );

    const saksgrunnlagsTermindato = getTermindatoSomSkalBrukesFraSaksgrunnlagBeggeParter(
        eksisterendeSak?.grunnlag.termindato,
        eksisterendeVedtakAnnenPart?.grunnlag.termindato
    );
    const saksgrunnlagsAntallBarn = getAntallBarnSomSkalBrukesFraSaksgrunnlagBeggeParter(
        erFarEllerMedmor,
        barn.antallBarn,
        eksisterendeVedtakAnnenPart?.grunnlag.antallBarn
    );
    useEffect(() => {
        if (erFarEllerMedmor && sknad.barn.antallBarn !== saksgrunnlagsAntallBarn) {
            const sknadMedOppdatertAntallBarn = {
                ...sknad,
                barn: { ...sknad.barn, antallBarn: saksgrunnlagsAntallBarn },
            };
            dispatch(actionCreator.setSknad(sknadMedOppdatertAntallBarn));
        }
    }, [erFarEllerMedmor, saksgrunnlagsAntallBarn, dispatch, sknad]);

    const nesteBarnsSakAnnenPartRequestIsSuspended =
        annenForelderFnrNesteSak !== undefined &&
        annenForelderFnrNesteSak !== '' &&
        (frsteBarnFraNesteSakFnr !== undefined || familieHendelseDatoNesteSak !== undefined) &&
        (eksisterendeSakAnnenPartRequestIsSuspended || eksisterendeSakAnnenPartRequestStatus === RequestStatus.FINISHED)
            ? false
            : true;

    const {
        eksisterendeSakAnnenPartData: nesteSakAnnenPartData,
        eksisterendeSakAnnenPartError: nesteSakAnnenPartError,
        eksisterendeSakAnnenPartRequestStatus: nesteSakAnnenPartRequestStatus,
    } = Api.useGetAnnenPartsVedtak(
        annenForelderFnrNesteSak,
        frsteBarnFraNesteSakFnr,
        dateToISOString(familieHendelseDatoNesteSak),
        nesteBarnsSakAnnenPartRequestIsSuspended
    );

    const frsteUttaksdagAnnenPart = getStartdatoFrstePeriodeAnnenPart(nesteSakAnnenPartData);

    useEffect(() => {
        if (
            frsteUttaksdagAnnenPart !== undefined &&
            state.barnFraNesteSak !== undefined &&
            (dayjs(frsteUttaksdagAnnenPart).isBefore(state.barnFraNesteSak.startdatoFrsteStnadsperiode, 'd') ||
                state.barnFraNesteSak.startdatoFrsteStnadsperiode === undefined)
        ) {
            const oppdatertBarnNesteSak = {
                ...state.barnFraNesteSak,
                startdatoFrsteStnadsperiode: frsteUttaksdagAnnenPart,
            };
            dispatch(actionCreator.setBarnFraNesteSak(oppdatertBarnNesteSak));
        }
    }, [frsteUttaksdagNesteBarnsSak, frsteUttaksdagAnnenPart, barnFraNesteSak, dispatch, state.barnFraNesteSak]);

    const harAktivitetskravIPeriodeUtenUttak = getHarAktivitetskravIPeriodeUtenUttak({
        erDeltUttak,
        morHarRett,
        skerErAleneOmOmsorg,
    });

    //Legg til annen parts perioder i planen til bruker
    useEffect(() => {
        if (
            eksisterendeSak !== undefined &&
            opprinneligPlan !== undefined &&
            eksisterendeVedtakAnnenPart !== undefined &&
            !annenPartsUttakErLagtTilIPlan
        ) {
            //Sett samtidigUttak p skerens perioder hvis de overlapper med annen parts samtidig uttak:
            opprinneligPlan.forEach((p) => {
                if (isUttaksperiode(p)) {
                    const overlappendePerioderAnnenPart = Periodene(
                        eksisterendeVedtakAnnenPart.uttaksplan
                    ).finnOverlappendePerioder(p);

                    if (
                        overlappendePerioderAnnenPart.length !== 0 &&
                        overlappendePerioderAnnenPart.find(
                            (periode) => isUttakAnnenPart(periode) && periode.nskerSamtidigUttak === true
                        )
                    ) {
                        if (!p.nskerSamtidigUttak) {
                            p.nskerSamtidigUttak = true;
                            p.samtidigUttakProsent = '100';
                        }
                    }
                }
            });

            const uttaksplanMedAnnenPart = finnOgSettInnHull(
                settInnAnnenPartsUttak(
                    opprinneligPlan,
                    eksisterendeVedtakAnnenPart.uttaksplan,
                    familiehendelsesdatoDate!,
                    frsteUttaksdagNesteBarnsSak,
                    true
                ),
                harAktivitetskravIPeriodeUtenUttak,
                familiehendelsesdatoDate!,
                erAdopsjon,
                bareFarMedmorHarRett,
                erFarEllerMedmor,
                frsteUttaksdagNesteBarnsSak
            );
            const eksisterendeSakMedAnnenPartsPlan = {
                ...eksisterendeSak,
                uttaksplan: uttaksplanMedAnnenPart,
            };
            dispatch(actionCreator.setUttaksplan(uttaksplanMedAnnenPart));
            dispatch(actionCreator.setEksisterendeSak(eksisterendeSakMedAnnenPartsPlan));
            dispatch(actionCreator.setAnnenPartsUttakErLagtTilIPlan(true));
        }
    }, [
        eksisterendeVedtakAnnenPart,
        opprinneligPlan,
        familiehendelsesdatoDate,
        harAktivitetskravIPeriodeUtenUttak,
        erAdopsjon,
        bareFarMedmorHarRett,
        erFarEllerMedmor,
        dispatch,
        frsteUttaksdagNesteBarnsSak,
        eksisterendeSak,
        annenPartsUttakErLagtTilIPlan,
    ]);

    const onValidSubmitHandler = () => {
        setSubmitIsClicked(true);
        const cleanedTilleggsopplysninger = cleanupInvisibleCharsFromTilleggsopplysninger(tilleggsopplysninger);
        return [
            actionCreator.setTilleggsopplysninger(cleanedTilleggsopplysninger),
            actionCreator.setEndringstidspunkt(endringstidspunkt),
            actionCreator.setPerioderSomSkalSendesInn(perioderSomSkalSendesInn),
        ];
    };

    const handleBegrunnelseChange = (rsak: SenEndringrsak, begrunnelse: string) => {
        const ekstraInformasjon = rsak !== SenEndringrsak.Ingen ? rsak : undefined;
        const opplysninger = {
            ...tilleggsopplysninger,
            begrunnelseForSenEndring: {
                ...tilleggsopplysninger.begrunnelseForSenEndring,
                tekst: begrunnelse,
                ekstraInformasjon: ekstraInformasjon,
            },
        };
        dispatch(actionCreator.setTilleggsopplysninger(opplysninger));
    };
    useEffect(() => {
        const periodeAngittAvAnnenPart = opprinneligPlan?.find((p) => isUttaksperiode(p) && p.angittAvAnnenPart);

        if (periodeAngittAvAnnenPart && endringstidspunkt === undefined) {
            const tidspunktForEndring = periodeAngittAvAnnenPart.tidsperiode.fom;
            dispatch(actionCreator.setEndringstidspunkt(tidspunktForEndring));

            const perioderForSendeInn = getPerioderSomSkalSendesInn(
                sknad.uttaksplan,
                erEndringssknad,
                erFarEllerMedmor,
                opprinneligPlan,
                tidspunktForEndring
            );
            setPerioderSomSkalSendesInn(perioderForSendeInn);
            dispatch(actionCreator.setPerioderSomSkalSendesInn(perioderForSendeInn));
        }
    }, [opprinneligPlan, dispatch, endringstidspunkt, erFarEllerMedmor, sknad.uttaksplan, erEndringssknad]);

    const { handleSubmit, isSubmitting } = useOnValidSubmit(
        onValidSubmitHandler,
        nextRoute,
        (state: ForeldrepengesknadContextState) => storeAppState(state)
    );

    const perioderMedUttakRundtFdsel = getPerioderMedUttakRundtFdsel(
        sknad.uttaksplan,
        familiehendelsesdatoDate!,
        termindato
    );

    const visAutomatiskJusteringForm = getVisAutomatiskJusteringForm(
        erFarEllerMedmor,
        familiehendelsesdatoDate!,
        situasjon,
        perioderMedUttakRundtFdsel,
        barn,
        termindato,
        bareFarMedmorHarRett
    );

    const kanJustereAutomatiskVedFdsel = getKanJustereAutomatiskVedFdsel(
        perioderMedUttakRundtFdsel,
        termindato,
        erFarEllerMedmor,
        barn
    );

    const setnskerJustertUttakVedFdselTilUndefinedHvisUgyldig = () => {
        if ((visAutomatiskJusteringForm || erEndringssknad) && !kanJustereAutomatiskVedFdsel) {
            dispatch(actionCreator.setnskerJustertUttakVedFdsel(undefined));
        }
    };

    const nskerJustertUttakVedFdselErBesvart = (nskerAutomatiskJusteringSvar: boolean | undefined) => {
        return (
            visAutomatiskJusteringForm && kanJustereAutomatiskVedFdsel && nskerAutomatiskJusteringSvar !== undefined
        );
    };

    const ref = useRef<FormikValues>(null);
    const clickHandler = (values: any) => {
        setSubmitIsClicked(true);
        if (uttaksplanErGyldig && !erTomEndringssknad) {
            if (ref.current) {
                ref.current.handleSubmit();
            }

            setnskerJustertUttakVedFdselTilUndefinedHvisUgyldig();

            if (nskerJustertUttakVedFdselErBesvart(values.nskerAutomatiskJustering)) {
                handleSubmit(values);
            }
        }
    };

    const onAvbrytSknad = useAvbrytSknad();
    const onFortsettSknadSenere = useFortsettSknadSenere();

    useEffect(() => {
        Api.storeAppState(debouncedState, person.fnr);
    }, [person.fnr, debouncedState]);

    const foreldreSituasjon = getForeldreparSituasjon(
        person.kjnn,
        annenForelderKjnn,
        erDeltUttak,
        morErAleneOmOmsorg,
        farMedmorErAleneOmOmsorg,
        rolle
    );
    const kontoRequestIsSuspended =
        (eksisterendeSakAnnenPartRequestIsSuspended
            ? false
            : eksisterendeSakAnnenPartRequestStatus !== RequestStatus.FINISHED) ||
        (nesteBarnsSakAnnenPartRequestIsSuspended ? false : nesteSakAnnenPartRequestStatus !== RequestStatus.FINISHED);

    const { tilgjengeligeStnadskontoerData: stnadskontoer100, tilgjengeligeStnadskontoerError } =
        Api.useGetUttakskontoer(
            getStnadskontoParams(
                Dekningsgrad.HUNDRE_PROSENT,
                barn,
                annenForelder,
                skersituasjon,
                farMedmorErAleneOmOmsorg,
                morErAleneOmOmsorg,
                dateToISOString(familieHendelseDatoNesteSak),
                saksgrunnlagsAntallBarn,
                saksgrunnlagsTermindato
            ),
            kontoRequestIsSuspended
        );
    const { tilgjengeligeStnadskontoerData: stnadskontoer80 } = Api.useGetUttakskontoer(
        getStnadskontoParams(
            Dekningsgrad.TTI_PROSENT,
            barn,
            annenForelder,
            skersituasjon,
            farMedmorErAleneOmOmsorg,
            morErAleneOmOmsorg,
            dateToISOString(familieHendelseDatoNesteSak),
            saksgrunnlagsAntallBarn,
            saksgrunnlagsTermindato
        ),
        kontoRequestIsSuspended
    );

    const handleOnPlanChange = (nyPlan: Periode[]) => {
        setSubmitIsClicked(false);
        dispatch(actionCreator.setUttaksplan(nyPlan));
        const tidspunktForEndring = getEndringstidspunkt(opprinneligPlan, nyPlan, erEndringssknad);
        setEndringstidspunkt(tidspunktForEndring);

        const perioderForSendeInn = getPerioderSomSkalSendesInn(
            nyPlan,
            erEndringssknad,
            erFarEllerMedmor,
            opprinneligPlan,
            tidspunktForEndring
        );
        setPerioderSomSkalSendesInn(perioderForSendeInn);
        dispatch(actionCreator.setPerioderSomSkalSendesInn(perioderForSendeInn));
    };

    useEffect(() => {
        if (tilgjengeligeStnadskontoerError) {
            sendErrorMessageToSentry(tilgjengeligeStnadskontoerError);
            throw new Error(
                `Vi klarte ikke  hente opp stnadskontoer. Prv igjen om noen minutter og hvis problemet vedvarer kontakt brukersttte.`
            );
        }
        if (eksisterendeSakAnnenPartError) {
            sendErrorMessageToSentry(eksisterendeSakAnnenPartError);
            throw new Error(
                `Vi klarte ikke  hente informasjon om saken til annen forelder. Prv igjen om noen minutter og hvis problemet vedvarer kontakt brukersttte.`
            );
        }

        if (nesteSakAnnenPartError) {
            sendErrorMessageToSentry(nesteSakAnnenPartError);
            throw new Error(
                `Vi klarte ikke  hente informasjon om saken til annen forelder for neste barn. Prv igjen om noen minutter og hvis problemet vedvarer kontakt brukersttte.`
            );
        }
    }, [tilgjengeligeStnadskontoerError, eksisterendeSakAnnenPartError, nesteSakAnnenPartError]);

    if (
        !stnadskontoer100 ||
        !stnadskontoer80 ||
        (eksisterendeSakAnnenPartRequestStatus !== RequestStatus.FINISHED &&
            !eksisterendeSakAnnenPartRequestIsSuspended) ||
        (nesteSakAnnenPartRequestStatus !== RequestStatus.FINISHED && !nesteBarnsSakAnnenPartRequestIsSuspended)
    ) {
        return (
            <div style={{ textAlign: 'center', padding: '12rem 0' }}>
                <NavFrontendSpinner type="XXL" />
            </div>
        );
    }

    const stnadskontoer = getValgtStnadskontoFor80Og100Prosent(stnadskontoer80, stnadskontoer100);
    const minsterettUkerToTette = getAntallUkerMinsterett(stnadskontoer100.minsteretter.toTette);

    const valgteStnadskontoer =
        dekningsgrad === Dekningsgrad.HUNDRE_PROSENT ? stnadskontoer[100] : stnadskontoer[80];

    const erTomEndringssknad =
        erEndringssknad && (perioderSomSkalSendesInn === undefined || perioderSomSkalSendesInn.length === 0);

    const handleSlettUttaksplan = () => {
        const slettetPlanUtenomFpFrFdsel = sknad.uttaksplan.filter((periode) =>
            isUttakAvForeldrepengerFrFdsel(periode)
        );
        dispatch(actionCreator.slettUttaksplan(slettetPlanUtenomFpFrFdsel));
        dispatch(actionCreator.setUttaksplanSlettet(true));
    };

    const handleResetUttaksplan = () => {
        if (state.eksisterendeSak) {
            dispatch(actionCreator.setUttaksplan(state.eksisterendeSak.uttaksplan));
            dispatch(actionCreator.setPerioderSomSkalSendesInn([]));
            setPerioderSomSkalSendesInn([]);
        }
    };

    return (
        <UttaksplanFormComponents.FormikWrapper
            initialValues={getUttaksplanFormInitialValues(state.sknad.nskerJustertUttakVedFdsel)}
            onSubmit={handleSubmit}
            innerRef={ref}
            renderForm={({ values: formValues }) => {
                const visibility = uttaksplanQuestionsConfig.getVisbility({
                    ...formValues,
                    termindato,
                    perioderMedUttakRundtFdsel,
                });

                return (
                    <Step
                        bannerTitle={intlUtils(intl, 'sknad.pageheading')}
                        backLinkHref={erEndringssknad ? undefined : getPreviousStepHref('uttaksplan')}
                        backLinkOnClick={
                            erEndringssknad
                                ? undefined
                                : (_href, event) => {
                                      event.preventDefault();
                                      setGTilbakeIsOpen(true);
                                  }
                        }
                        activeStepId="uttaksplan"
                        pageTitle={intlUtils(intl, 'sknad.uttaksplan')}
                        stepTitle={intlUtils(intl, 'sknad.uttaksplan')}
                        onCancel={onAvbrytSknad}
                        onContinueLater={onFortsettSknadSenere}
                        steps={stepConfig(intl)}
                        kompakt={true}
                    >
                        <Uttaksplan
                            foreldreSituasjon={foreldreSituasjon}
                            forelderVedAleneomsorg={forelderVedAleneomsorg}
                            erDeltUttak={erDeltUttak}
                            uttaksplan={sknad.uttaksplan}
                            familiehendelsesdato={familiehendelsesdato}
                            handleOnPlanChange={handleOnPlanChange}
                            stnadskontoer={valgteStnadskontoer}
                            navnPForeldre={navnPForeldre}
                            annenForelder={annenForelder}
                            arbeidsforhold={getAktiveArbeidsforhold(
                                arbeidsforhold,
                                ISOStringToDate(familiehendelsesdato)
                            )}
                            erEndringssknad={erEndringssknad}
                            erFarEllerMedmor={erFarEllerMedmor}
                            erFlerbarnssknad={erFlerbarnssknad}
                            erAleneOmOmsorg={skerErAleneOmOmsorg}
                            harMidlertidigOmsorg={harMidlertidigOmsorg}
                            situasjon={situasjon}
                            erMorUfr={erMorUfr}
                            morHarRett={morHarRett}
                            skersituasjon={skersituasjon}
                            dekningsgrad={dekningsgrad}
                            antallBarn={antallBarn}
                            tilleggsopplysninger={tilleggsopplysninger}
                            setUttaksplanErGyldig={setUttaksplanErGyldig}
                            handleBegrunnelseChange={handleBegrunnelseChange}
                            eksisterendeSak={eksisterendeSak}
                            perioderSomSkalSendesInn={perioderSomSkalSendesInn}
                            morsSisteDag={morsSisteDag}
                            harKomplettUttaksplan={harKomplettUttaksplan}
                            opprinneligPlan={harUttaksplanBlittSlettet ? undefined : opprinneligPlan}
                            handleSlettUttaksplan={handleSlettUttaksplan}
                            handleResetUttaksplan={handleResetUttaksplan}
                            termindato={termindato}
                            barn={barn}
                            visibility={visibility}
                            visAutomatiskJusteringForm={visAutomatiskJusteringForm}
                            perioderMedUttakRundtFdsel={perioderMedUttakRundtFdsel}
                            barnFraNesteSak={barnFraNesteSak}
                            familiehendelsesdatoNesteSak={familieHendelseDatoNesteSak}
                            frsteUttaksdagNesteBarnsSak={frsteUttaksdagNesteBarnsSak}
                            minsterettUkerToTette={minsterettUkerToTette}
                        />
                        <VilDuGTilbakeModal isOpen={gTilbakeIsOpen} setIsOpen={setGTilbakeIsOpen} />
                        {!uttaksplanErGyldig && submitIsClicked && (
                            <Block textAlignCenter={true} padBottom="l">
                                <AlertStripe type="feil">
                                    <FormattedMessage id="uttaksplan.validering.kanIkkeGVidere" />
                                </AlertStripe>
                            </Block>
                        )}
                        {erTomEndringssknad && submitIsClicked && (
                            <Block textAlignCenter={true} padBottom="l">
                                <AlertStripe type="feil">
                                    <FormattedMessage id="uttaksplan.validering.kanIkkeGVidereEndringssknad" />
                                </AlertStripe>
                            </Block>
                        )}
                        <Block textAlignCenter={true} padBottom="l">
                            <Hovedknapp onClick={clickHandler} disabled={isSubmitting} spinner={isSubmitting}>
                                {intlUtils(intl, 'sknad.gVidere')}
                            </Hovedknapp>
                        </Block>
                    </Step>
                );
            }}
        />
    );
};
export default UttaksplanStep;
