import { hasValue } from '@navikt/fp-common';
import { QuestionConfig, Questions } from '@navikt/sif-common-question-config/lib';
import { isValidTidsperiode } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import { PeriodeFrFdselFormData, PeriodeFrFdselFormField } from './periodeFrFdselFormConfig';

const PeriodeFrFdselFormConfig: QuestionConfig<PeriodeFrFdselFormData, PeriodeFrFdselFormField> = {
    [PeriodeFrFdselFormField.fom]: {
        isAnswered: ({ fom }) => hasValue(fom),
        isIncluded: ({ skalIkkeHaUttakFrTermin }) => !skalIkkeHaUttakFrTermin,
    },
    [PeriodeFrFdselFormField.tom]: {
        isAnswered: ({ tom }) => hasValue(tom),
        isIncluded: ({ skalIkkeHaUttakFrTermin }) => !skalIkkeHaUttakFrTermin,
    },
    [PeriodeFrFdselFormField.skalIkkeHaUttakFrTermin]: {
        isAnswered: () => true,
        isIncluded: () => true,
        visibilityFilter: ({ fom, tom, skalIkkeHaUttakFrTermin }) =>
            skalIkkeHaUttakFrTermin ? true : isValidTidsperiode({ fom, tom }),
    },
};

export const periodeFrFdselFormQuestionsConfig = Questions<PeriodeFrFdselFormData, PeriodeFrFdselFormField>(
    PeriodeFrFdselFormConfig
);
