import React, { FunctionComponent } from 'react';
import { injectIntl, IntlShape } from 'react-intl';
import { bemUtils } from '@navikt/fp-common';
import { Normaltekst } from 'nav-frontend-typografi';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import { getVarighetString } from 'app/utils/dateUtils';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { StnadskontoUttak } from 'uttaksplan/types/StnadskontoUttak';
import { getStnadskontoNavn } from 'uttaksplan/utils/stnadskontoerUtils';
import StnadskontoIkon from 'uttaksplan/components/stnadskonto-ikon/StnadskontoIkon';
import './kontostatus.less';
import { Situasjon } from 'app/types/Situasjon';

export interface Props {
    uttak: StnadskontoUttak;
    navnPForeldre: NavnPForeldre;
    erEndringssknad: boolean;
    intl: IntlShape;
    erFarEllerMedmor: boolean;
    situasjon: Situasjon;
    erAleneOmOmsorg: boolean;
}

const bem = bemUtils('kontostatus');

const Kontostatus: FunctionComponent<Props> = ({
    uttak,
    navnPForeldre,
    erEndringssknad,
    intl,
    erFarEllerMedmor,
    situasjon,
    erAleneOmOmsorg,
}) => {
    if (erEndringssknad && uttak.konto === StnadskontoType.ForeldrepengerFrFdsel) {
        uttak.dager = 0;
    }

    const varighetString = getVarighetString(uttak.dager, intl);
    const kontoErOvertrukket = uttak.dager < 0;

    return (
        <Normaltekst className={bem.block} tag="div">
            <div className={bem.element('ikon')} aria-hidden={true} role="presentation">
                <StnadskontoIkon
                    konto={uttak.konto}
                    navnPForeldre={navnPForeldre}
                    erFarEllerMedmor={erFarEllerMedmor}
                    situasjon={situasjon}
                    erAleneOmOmsorg={erAleneOmOmsorg}
                />
            </div>
            <div className={bem.element('content')}>
                <div className={kontoErOvertrukket ? bem.element('kontoOvertrukket') : bem.element('konto')}>
                    {getStnadskontoNavn(intl, uttak.konto, navnPForeldre, erFarEllerMedmor, erAleneOmOmsorg)}
                </div>
                <strong
                    className={kontoErOvertrukket ? bem.element('dagerOvertrukket') : bem.element('dager')}
                    data-name={uttak.konto}
                >
                    {kontoErOvertrukket ? `- ${varighetString}` : varighetString}
                </strong>
            </div>
        </Normaltekst>
    );
};

export default injectIntl(Kontostatus);
