import { Sknad } from 'app/context/types/Sknad';
import { uttaksplanInneholderPerioderUtenKonto } from 'app/utils/uttaksplan/uttaksplanUtils';
import SknadRoutes from './routes';

const isAvailable = (route: SknadRoutes, sknad: Sknad): boolean => {
    switch (route) {
        case SknadRoutes.SKERSITUASJON:
            return sknad.harGodkjentVilkr === true;
        case SknadRoutes.OPPSUMMERING:
            return uttaksplanInneholderPerioderUtenKonto(sknad.uttaksplan) === false && sknad.uttaksplan.length > 0;
        default:
            return true;
    }
};

export default isAvailable;
