import { hasValue } from '@navikt/fp-common';
import { isUtsettelsesperiode } from 'uttaksplan/types/Periode';
import { RegelTestresultat } from '../utils/types/regelTypes';
import { Sknadsinfo } from '../utils/types/Sknadsinfo';

export const inneholderUtsettelserUtenrsak = (grunnlag: Sknadsinfo): RegelTestresultat => {
    const utsettelserUtenrsak = grunnlag.perioder.filter((p) => isUtsettelsesperiode(p) && !hasValue(p.rsak));

    const passerer = utsettelserUtenrsak.length === 0;

    return {
        passerer,
        info: utsettelserUtenrsak.map((periode) => ({
            intlKey: 'uttaksplan.validering.feil.inneholderUtsettelserUtenrsak',
            periodeId: periode.id,
        })),
    };
};
